import getWeb3 from "../utils/getWeb3"

const ASET_ASSET = 'ASET_ASSET'
const ASET_WALLET = 'ASET_WALLET'
const ASET_WALLETVISIABLE = 'ASET_WALLETVISIABLE'
const registerWeb3 = 'registerWeb3'

const actions = {
  [ASET_ASSET](context, asset) {
    context.commit('SET_ASSET', asset);
  },

  [ASET_WALLET](context, wallet) {
    context.commit('SET_WALLET', wallet);
  },

  [ASET_WALLETVISIABLE](context, visiable) {
    context.commit('SET_WALLETVISIABLE', visiable);
  },

  [registerWeb3](context, chainName) {
    getWeb3.connect(chainName).then(result => {
      
      context.commit('registerWeb3Instance', result)
    }).catch(e => {
      console.log('error', e)
    })
  },



}
export default actions;



