              </div>
            </div>
          </div>
        </div>
        <div class="bnx-rank">
          <div class="dragon-rank">
            <div
              class="rank-item"
              v-for="(item, index) in cyberRankInfo"
              :key="index"
            >
              <div class="index">
                <span v-if="index > 2">{{ index + 1 }}</span>
                <img
                  v-if="index < 3"
                  :src="
                    'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/rank/rank-' +
                    (index + 1) +
                    '.png'
                  "
                  alt
                />
              </div>

              <div class="address">
                <span v-if="item">{{ item.address | filterAccount }}</span>
                <span v-else></span>
              </div>
              <div class="result-num">
                <div class="win-num" v-if="item">
                  <img src="../../asset/ui/win-icon.png" alt class="win-icon" />
                  <p>{{ item.point }}</p>
                </div>
                <div class="win-num" v-if="item">
                  <img src="../../asset/ui/dk.png" alt />
                  <p>{{ item.dkReward }}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="enter-button" @click="hallViewVisiable = true">Go Fight</div>
      <div class="reward-button" @click="showRewardDialog()">
        <img src="../../asset/ui/reward-icon.gif" alt />
        <p>Prize</p>
      </div>
      <img src="../../asset/ui/grass-bott.png" alt class="bottom-grass" />
    </div>
    <div class="reward-dialog" v-if="rewardDialogVisible">
      <div class="claim-reward-part">
        <div class="claim-items">
          <div class="claim-item">
            <img src="../../asset/ui/dg.png" alt />
            <p v-if="warPrize">
              {{ Number(warPrize["dcAmount"] / 10 ** 18).toFixed(0) }}
            </p>
          </div>
          <div class="claim-item">
            <img src="../../asset/ui/dk.png" alt />
            <p v-if="warPrize">
              {{ Number(warPrize["dkAmount"] / 10 ** 18).toFixed(3) }}
            </p>
          </div>
        </div>
        <div class="claim-btns" @click="claimRewards">
          <span v-if="!loadings['claimRewards']">Claim</span>
          <span v-else>
            <i class="el-icon-loading"></i>
          </span>
        </div>
      </div>
      <div
        class="reward-dialog-modal"
        @click="rewardDialogVisible = false"
      ></div>
    </div>
    <div class="box">
      <scrollText
        :data="scrollText"
        :class-option="moveOption"
        @ScrollEnd="getNewWarInfo"
        v-if="scrollText.length >= 5"
        class="scroll-plugin"
      >
        <ul class="inner-scroll-bar">
          <li v-for="(item, index) in scrollText" :key="index">{{ item }}</li>
        </ul>
      </scrollText>
    </div>
    <hallView v-if="hallViewVisiable"></hallView>
  </div>
</template>

<script>
import hallView from "./Hall.vue";
import store from "@/store/index";
import ContractUtil from "../../utils/contractUtils";
import scrollText from "vue-seamless-scroll";
const path = store.state.contractPath;
const abiJsons = require("../../contracts/deployments." + path + ".json");
const tokensJsons = require("../../contracts/tokens." + path + ".json");
const erc20AbiJosn = require("../../contracts/erc20.json");
import Util from "../../utils/util";

export default {
  name: "WarView",
  components: {
    hallView,
    scrollText,
  },
  data() {
    return {
      loadings: {},
      activeTribe: "dragon",
      activeDragon: null,
      unApproveToken: {},
      dragonData: [],
      activeAttr: 0,
      tokens: ["DG", "DK"],
      tokenContracts: {},
      dragonsRemainNumbers: {},
      hallViewVisiable: false,
      rewardDialogVisible: false,
      warCountDown: 0,
      timer: null,
      interval: null,
      dragonHonor: 0,
      cyberHoner: 0,
      dragonRankInfo: null,
      cyberRankInfo: null,
      warPrize: null,
      warId: 0,
      scrollText: [],
    };
  },
  watch: {
    web3(val1, val2) {
      if (this.web3.web3Instance) {
        this.init();
      }
    },
  },

  computed: {
    web3() {
      return this.$store.state.web3;
    },
    moveOption() {
      return {
        direction: 2,
        limitMoveNum: 5,
        step: 1,
      };
    },
  },
  mounted() {
    this.init();
    this.$store.dispatch("ASET_WALLETVISIABLE", false);
  },

  beforeDestory() {
    this.$store.dispatch("ASET_WALLETVISIABLE", false);
  },

  methods: {
    init() {
      if (!this.web3 || !this.web3.web3Instance) {
        return;
      }

      let web3 = this.web3.web3Instance();

      this.dragonWarAddress = abiJsons["DragonWar"]["address"];
      this.dragonWarContract = new web3.eth.Contract(
        abiJsons["DragonWar"]["abi"],
        this.dragonWarAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.warPrizeAddress = abiJsons["WarPrize"]["address"];
      this.warPrizeContract = new web3.eth.Contract(
        abiJsons["WarPrize"]["abi"],
        this.warPrizeAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.getInfo();
    },

    getInfo() {
      this.getWarTimeRemain();
      // this.getDragonUserInfo();
      // this.getCyberUserInfo();
      this.getHonorsNumber();
      this.getWarPrizeInfo();
      this.getDragonWarInfo();
      clearInterval(this.interval);
      // this.interval = setInterval(() => {
      //   // this.getDragonUserInfo();
      //   // this.getCyberUserInfo();
      //   this.getHonorsNumber();
      // }, 15000);
    },
    getDragonUserInfo() {
      ContractUtil.call(
        this.dragonWarContract,
        "getDragonHonorUserLen",
        []
      ).then((res) => {
        ContractUtil.call(
          this.dragonWarContract,
          "getHonorUsers",
          [1, 0, 0]
        ).then((res) => {
          this.dragonRankInfo = this.parseRankData(
            res,
            this.dragonHonor > this.cyberHoner ? this.dragonHonor : 0
          );

          this.dragonRankInfo.sort((a, b) => {
            return b.point - a.point;
          });

          this.dragonRankInfo.length = 100;
        });
      });
    },
    getCyberUserInfo() {
      ContractUtil.call(
        this.dragonWarContract,
        "getCyberHonorUserLen",
        []
      ).then((res) => {
        ContractUtil.call(
          this.dragonWarContract,
          "getHonorUsers",
          [2, 0, 0]
        ).then((res) => {
          this.cyberRankInfo = this.parseRankData(
            res,
            this.cyberHoner > this.dragonHonor ? this.cyberHoner : 0
          );

          this.cyberRankInfo.sort((a, b) => {
            return b.point - a.point;
          });

          this.cyberRankInfo.length = 100;
        });
      });
    },
    parseRankData(data, honor) {
      if (honor > 0) {
        honor = 0;
        for (var i in data[0]) {
          honor += Math.pow(data[0][i], 0.5);
        }
      }
      let arr = [];
      data[0].forEach((item, index) => {
        let userRank = {};
        userRank = {
          dkReward:
            honor == 0
              ? 0
              : Number((Math.pow(item, 0.5) / honor) * 1000).toFixed(2),
          point: item,
          address: data[1][index],
        };
        arr.push(userRank);
      });
      return arr;
    },
    getHonorsNumber() {
      ContractUtil.call(this.dragonWarContract, "dragonHonor", []).then(
        (res) => {
          this.dragonHonor = parseInt(res);
          ContractUtil.call(this.dragonWarContract, "cyberHonor", []).then(
            (res) => {
              this.cyberHoner = parseInt(res);

              this.getCyberUserInfo();
              this.getDragonUserInfo();
            }
          );
        }
      );
    },
    getWarPrizeInfo() {
      ContractUtil.call(this.warPrizeContract, "userRewards", [
        this.web3.coinbase,
      ]).then((res) => {
        this.warPrize = {
          dcAmount: res["dcAmount"],
          dkAmount: res["dkAmount"],
        };
      });
    },
    getDragonWarInfo() {
      ContractUtil.call(this.dragonWarContract, "getWarId", []).then(
        async (res) => {
          this.warId = res;
          if (res < 5) return;
          for (let i = res - 5; i <= res; i++) {
            let info = await this.getDetailInfo(i);
            this.scrollText.push(this.parseWinText(info));
          }
        }
      );
    },
    getNewWarInfo() {
      ContractUtil.call(this.dragonWarContract, "getWarId", []).then(
        async (res) => {
          if (this.warId == res) return;
          for (let i = this.warId + 1; i <= res; i++) {
            let info = await this.getDetailInfo(i);
            this.scrollText.push(this.parseWinText(info));
          }
          this.warId = res;
        }
      );
    },
    getDetailInfo(id) {
      return ContractUtil.call(this.dragonWarContract, "warInfos", [id]);
    },
    parseWinText(info) {
      let challenger = this.filterAccount(info.home),
        defender = this.filterAccount(info.guest),
        result = info.status,
        reward = Number((info.dcReward * 1) / 10 ** 18).toFixed(2),
        homeCamp = info.homeId == info.dragonId ? "Knight " : "Cyber ",
        guestCamp = info.homeId == info.dragonId ? "Cyber " : "Knight ",
        text = "";
      if (result == 1) {
        text = `${guestCamp + challenger} beaten ${
          homeCamp + defender
        },and won ${reward}DG`;
      } else {
        text = `${guestCamp + challenger} challenged ${
          homeCamp + defender
        } failed, come on next time`;
      }
      return text;
    },
    showRewardDialog() {
      this.rewardDialogVisible = true;
      this.getWarPrizeInfo();
    },
    claimRewards() {
      let that = this;

      let loading = "claimRewards";

      if (this.loadings[loading]) {
        return;
      }

      that.$set(that.loadings, loading, true);

      that.warPrizeContract.methods
        .claim()
        .estimateGas({ from: this.web3.coinbase }, function (err, gas) {
          if (err) {
            that.$set(that.loadings, loading, false);
            that.$message({
              message: that.$t("common.failed") + Util.errMsg(err),
              type: "warning",
              center: true,
            });
            return;
          }

          that.warPrizeContract.methods
            .claim()
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {})
            .on("receipt", function (receipt) {
              if (that.txs[receipt.transactionHash]) {
                return;
              }

              that.txs[receipt.transactionHash] = receipt.transactionHash;
              that.$set(that.loadings, loading, false);
              that.getInfo();
              that.$message({
                message: that.$t("common.success"),
                type: "success",
                center: true,
              });
            })
            .on("confirmation", function (confirmationNumber, receipt) {})
            .on("error", function (err) {
              that.$set(that.loadings, loading, false);
            });
        });
    },
    timeRemainDecrease() {
      this.timer = setInterval(() => {
        if (this.warCountDown <= 0) {
          clearInterval(this.timer);
        } else {
          this.warCountDown -= 1;
        }
      }, 1000);
    },
    getWarTimeRemain() {
      ContractUtil.call(this.dragonWarContract, "getRemainTime", []).then(
        (res) => {
          this.warCountDown = res;
          this.timeRemainDecrease();
        }
      );
    },
    filterAccount(value) {
      if (!value) {
        return value;
      }
      return value.slice(0, 4) + "***" + value.slice(-3);
    },
  },
  filters: {
    filterTimeCount(t) {
      return Util.countdownHour(t);
    },
    filterAccount(value) {
      if (!value) {
        return value;
      }
      return value.slice(0, 4) + "***" + value.slice(-3);
    },
  },
  beforeDestory() {
    clearInterval(this.interval);
    clearInterval(this.timer);
  },
};
</script>

