<template>
  <div class="battle-container" ref="canvasContainer">
    <div class="avatar-container">
      <div class="avatar-left">
        <img
          v-if="avatarImgs['dragon']"
          :src="avatarImgs['dragon']"
          alt
          class="avatar"
        />
        <div class="name">{{ dragon["name"] }}</div>

        <div class="attr-box">
          <div>
            <img src="../../asset/ui/combat.png" class="icon" alt />
            <p>{{ dragon["attack"] }}</p>
          </div>
          <div>
            <img src="../../asset/ui/defense.png" class="icon" alt />
            <p>{{ dragon["defense"] }}</p>
          </div>
          <div>
            <img src="../../asset/ui/magic-resistance.png" class="icon" alt />
            <p>{{ dragon["magicResistance"] }}</p>
          </div>
        </div>
      </div>
      <div class="round-num">
        <img src="../../asset/ui/vs.png" class="vs" alt />
        <div class="round-box">
          Round <span>{{ battleIndex + 1 }}</span>
        </div>
      </div>
      <div class="avatar-right" v-if="monster">
        <img
          v-if="avatarImgs['monster']"
          :src="avatarImgs['monster']"
          alt
          class="avatar"
        />
        <div class="name">{{ monster["name"] }}</div>

        <div class="attr-box">
          <div>
            <img src="../../asset/ui/combat.png" class="icon" alt />
            <p>{{ monster["attack"] }}</p>
          </div>
          <div>
            <img src="../../asset/ui/defense.png" class="icon" alt />
            <p>{{ monster["defense"] }}</p>
          </div>
          <div>
            <img src="../../asset/ui/magic-resistance.png" class="icon" alt />
            <p>{{ monster["magicResistance"] }}</p>
          </div>
        </div>
      </div>
    </div>

    <!-- <div class="over-button" @click="gameOver">JUMP OVER</div> -->
    <div class="info-container">
      <div class="attributes-list dragon-attrs" v-if="dragon">
        <div class="attr-item">
          <div>
            <img src="../../asset/svg/strength.svg" alt="" />
          </div>
          <div class="value">{{ dragon["strength"] }}</div>
        </div>

        <div class="attr-item">
          <div>
            <img src="../../asset/svg/agility.svg" alt="" />
          </div>
          <div class="value">{{ dragon["agility"] }}</div>
        </div>

        <div class="attr-item">
          <div>
            <img src="../../asset/svg/constitution.svg" alt="" />
          </div>
          <div class="value">{{ dragon["constitution"] }}</div>
        </div>

        <div class="attr-item">
          <div>
            <img src="../../asset/svg/intelligence.svg" alt="" />
          </div>
          <div class="value">{{ dragon["intelligence"] }}</div>
        </div>

        <div class="attr-item">
          <div>
            <img src="../../asset/svg/willpower.svg" alt="" />
          </div>

          <div class="value">{{ dragon["willpower"] }}</div>
        </div>

        <div class="attr-item">
          <div>
            <img src="../../asset/svg/spirit.svg" alt="" />
          </div>

          <div class="value">{{ dragon["spirit"] }}</div>
        </div>
      </div>

      <div class="attributes-list monster-attrs" v-if="monster">
        <div class="attr-item">
          <div>
            <img src="../../asset/svg/strength.svg" alt="" />
          </div>
          <div class="value">{{ monster["strength"] }}</div>
        </div>

        <div class="attr-item">
          <div>
            <img src="../../asset/svg/agility.svg" alt="" />
          </div>
          <div class="value">{{ monster["agility"] }}</div>
        </div>

        <div class="attr-item">
          <div>
            <img src="../../asset/svg/constitution.svg" alt="" />
          </div>
          <div class="value">{{ monster["constitution"] }}</div>
        </div>

        <div class="attr-item">
          <div>
            <img src="../../asset/svg/intelligence.svg" alt="" />
          </div>
          <div class="value">{{ monster["intelligence"] }}</div>
        </div>

        <div class="attr-item">
          <div>
            <img src="../../asset/svg/willpower.svg" alt="" />
          </div>

          <div class="value">{{ monster["willpower"] }}</div>
        </div>

        <div class="attr-item">
          <div>
            <img src="../../asset/svg/spirit.svg" alt="" />
          </div>

          <div class="value">{{ monster["spirit"] }}</div>
        </div>
      </div>
    </div>

    <div class="result-container center" v-if="resultDialogVisiable">
      <div class="result-box center">
        <div class="result-title">Your loot</div>

        <div class="result-list center">
          <div class="result-item" v-if="battleRewards['dgAmount'] > 0">
            <img class="center" src="../../asset/ui/dg.png" alt />
            <p>{{ battleRewards["dgAmount"] }}</p>
          </div>

          <div class="result-item" v-if="battleRewards['dkAmount'] > 0">
            <img class="center" src="../../asset/ui/dk.png" alt />
            <p>{{ battleRewards["dkAmount"] }}</p>
          </div>

          <div class="result-item" v-if="battleRewards['props'] > 0">
            <img
              class="center"
              src="https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/key/key-1.png"
              alt
            />
            <p>{{ battleRewards["props"] }}</p>
          </div>
        </div>

        <div class="confirm-btn" @click="backToDungeon">
          {{ $t("common.confirm") }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MonsterSprite from "./MonsterSprite";
import Util from "../../utils/util";

export default {
  name: "BattleView",

  props: {
    battleLevel: Number,
    dragon: Object,
    battleInfo: Array,
    battleRewards: Object,
  },
  data() {
    return {
      stage: null,
      width: document.documentElement.clientWidth * 2,
      height: document.documentElement.clientHeight * 2,
      scale: 0.5,
      asset: null,
      canvas: null,
      monsterAtlas: [],
      monsterSprites: [],
      monsterContainers: [],
      fightTween: null,
      rhythmTime: 100,

      monster: null,
      monsterNum: 0,
      battleData: [],
      battleIndex: 0,
      resultDialogVisiable: false,
      pkSprite: null,
      // monstersData: [
      //   {
      //     type: 5,
      //     life: 500,
      //     maxLife: 500,
      //     isBoss: false,
      //   },
      //   {
      //     type: 3,
      //     life: 300,
      //     maxLife: 300,
      //     isBoss: false,
      //   }
      // ],

      // battleInfo: [
      //   {
      //     remainHp: [
      //       588, 466, 548, 426, 508, 416, 488, 406, 488, 406, 485, 206, 480, 0,
      //     ],
      //   },
      //   {
      //     remainHp: [480, 266, 470, 236, 465, 166, 450, 146, 440, 106, 420, 0],
      //   },
      //   // [],
      //   // [],
      // ],

      attackSprite: null,

      attackType: "dragon",

      dragonContainer: null,
      propertyDialogVisiable: false,
      avatarImgs: {},
      txs: {},
      loadings: {},

      monsterAttack: [],
      dragonAttack: [],
      monsterLifeData: [],
      dragonLifeData: [],
      dragonStartLife: 0,
      dragonStartLifeData: [],
    };
  },

  watch: {
    web3(val1, val2) {
      if (this.web3.web3Instance) {
        this.init();
      }
    },
  },

  computed: {
    web3() {
      return this.$store.state.web3;
    },
  },

  mounted() {
    if (this.width > 480) {
      this.width = (document.documentElement.clientHeight / 1.78) * 2;
    }
    this.asset = this.$store.state.asset;
    this.initStage(this.$refs.canvasContainer);
  },

  methods: {
    init() {
      let that = this;
      let web3 = this.web3.web3Instance();

      this.dragonAdventureAddress = abiJsons["DragonAdventure"]["address"];
      this.dragonAdventureContract = new web3.eth.Contract(
        abiJsons["DragonAdventure"]["abi"],
        this.dragonAdventureAddress,
        {
          from: this.web3.coinbase,
        }
      );

      // this.getInfo();
    },

    initStage(container) {
      this.stage = new Hilo.Stage({
        renderType: "canvas",
        width: this.width,
        height: this.height,
        scaleX: this.scale,
        scaleY: this.scale,
        container: container,
      });
      this.stage.canvas.style.left = 0;
      this.stage.canvas.style.top = 0;
      this.stage.enableDOMEvent([
        Hilo.event.POINTER_START,
        Hilo.event.POINTER_MOVE,
        Hilo.event.POINTER_END,
      ]);
      this.ticker = new Hilo.Ticker(60);
      this.ticker.addTick(this.stage);
      this.ticker.addTick(Hilo.Tween);
      this.ticker.start(true);
      this.renderBattlesData();
    },

    renderBattlesData() {
      this.dragon = Util.getDragonDetailAttr(this.dragon);
      let dragonStartLife = 0;
      this.battleInfo.map((battleInfo, index) => {
        if (index) {
          dragonStartLife =
            this.battleInfo[index - 1]["remainHp"][
              this.battleInfo[index - 1]["remainHp"].length - 2
            ];
        } else {
          dragonStartLife = this.dragon["life"];
        }
        this.dragonStartLifeData.push(dragonStartLife);
      });
      this.renderBattleInfo(true);
    },

    renderInfoBar() {
      this.$set(
        this.avatarImgs,
        "dragon",
        "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/dragons/dragon" +
          this.dragon.type +
          ".png"
      );
      this.$set(
        this.avatarImgs,
        "monster",
        "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/monster/avatar-" +
          this.monster["type"] +
          ".png"
      );
    },

    renderBattleInfo(isFirst) {
      let battleInfo = this.battleInfo[this.battleIndex];
      this.monster = Util.getMonsterInfo(battleInfo["monster"]);
      
      let _remainHp = battleInfo["remainHp"];
      let _dragonsLife = [];
      let _monsterLife = [];
      let dragonAttackNums = [];
      let monsterAttackNums = [];
      _monsterLife.push(this.monster.life);
      for (let i = 1; i <= _remainHp.length; i++) {
        if (i % 2 === 0) {
          _monsterLife.push(_remainHp[i - 1]);
        } else {
          _dragonsLife.push(_remainHp[i - 1]);
        }
      }

      for (let i in _monsterLife) {
        if (i > 0) {
          dragonAttackNums.push(
            Math.abs(_monsterLife[i] - _monsterLife[i - 1])
          );
        }
      }
      if (_monsterLife[_monsterLife.length - 1] === 0) {
        dragonAttackNums[dragonAttackNums.length - 1] =
          dragonAttackNums[dragonAttackNums.length - 1] +
          Math.floor(
            dragonAttackNums[dragonAttackNums.length - 1] *
              Math.random().toFixed(1)
          );
      }

      for (let i in _dragonsLife) {
        if (i > 0) {
          monsterAttackNums.push(
            Math.abs(_dragonsLife[i] - _dragonsLife[i - 1])
          );
        }
      }

      if (_dragonsLife[_dragonsLife.length - 1] === 0) {
        monsterAttackNums[monsterAttackNums.length - 1] =
          monsterAttackNums[monsterAttackNums.length - 1] +
          Math.floor(
            monsterAttackNums[monsterAttackNums.length - 1] *
              Math.random().toFixed(1)
          );
      }

      this.dragonAttack = dragonAttackNums;

      this.dragonLifeData = _dragonsLife.splice(1, _dragonsLife.length - 1);

      this.monsterAttack = monsterAttackNums;
      this.monsterLifeData = _monsterLife.splice(1, _monsterLife.length - 1);
      this.renderInfoBar();
      this.runStartPk(isFirst);
    },

    runStartPk(isFirst) {
      let that = this;
      if (isFirst) {
        let atlas = this.asset.getStartBattleAtlas();
        let pkSprite = new Hilo.Sprite({
          id: "pk",
          atlas: atlas,
        });
        pkSprite.addFrame(atlas.getSprite("pk"));
        let _scale = this.width / pkSprite.width;
        pkSprite.width = this.width;
        pkSprite.height = pkSprite.height * _scale;
        pkSprite.pivotX = pkSprite.width / 2;
        pkSprite.pivotY = pkSprite.height / 2;
        pkSprite.x = this.width / 2;
        pkSprite.y = this.height * 0.36;

        pkSprite.interval = 6;
        this.stage.addChild(pkSprite);
        this.pkSprite = pkSprite;
        // setTimeout(() => {
        //   pkSprite.removeFromParent();
        //   that.startFight();
        // }, 2200);
        that.renderMonster();
      } else {
        // setTimeout(() => {
        //   that.startFight();
        // }, 200);
        that.renderMonster();
      }
    },

    renderBattleResult(result) {
      if (result) {
        if (this.battleIndex < this.battleInfo.length - 1) {
          this.runNextPk();
        } else {
          this.runVictory();
        }
      } else {
        this.runDefeat();
      }
    },

    runNextPk() {
      this.battleIndex++;
      this.dragonContainer.removeFromParent();
      this.dragonContainer = null;
      this.renderBattleInfo();
    },

    runVictory() {
      let that = this;
      this.dragonContainer.removeFromParent();
      this.monsterContainer.removeFromParent();
      let atlas = this.asset.getVictoryAtlas();
      let victorySprite = new Hilo.Sprite({
        id: "victory",
        atlas: atlas,
      });
      victorySprite.addFrame(atlas.getSprite("victory"));
      victorySprite.interval = 5;
      let _scale = this.width / victorySprite.width;
      victorySprite.width = _scale * victorySprite.width;
      victorySprite.height = _scale * victorySprite.height;
      victorySprite.pivotX = victorySprite.width / 2;
      victorySprite.pivotY = victorySprite.height / 2;
      victorySprite.x = this.width * 0.55;
      victorySprite.y = this.height * 0.4;
      this.stage.addChild(victorySprite);

      setTimeout(() => {
        victorySprite.removeFromParent();
        that.showBattleResult();
      }, 1000);
    },

    runDefeat() {
      let that = this;
      this.dragonContainer.removeFromParent();
      this.monsterContainer.removeFromParent();
      let defeatSprite = new Hilo.Bitmap({
        id: "defeat",
        image: this.asset.defeat,
      });
      defeatSprite.pivotX = defeatSprite.width / 2;
      defeatSprite.pivotY = defeatSprite.height / 2;
      defeatSprite.x = this.width / 2;
      defeatSprite.y = this.height / 2;
      defeatSprite.scaleX = 0;
      defeatSprite.scaleY = 0;
      defeatSprite.alpha = 0;
      // defeatSprite.opacity
      this.stage.addChild(defeatSprite);
      Hilo.Tween.to(
        defeatSprite,
        { alpha: 1 },
        {
          duration: 1000,
          repeat: 0,
          loop: false,
          reverse: false,
          ease: Hilo.Ease.Quad.EaseIn,
        }
      );
      Hilo.Tween.to(
        defeatSprite,
        { scaleX: 1.5, scaleY: 1.5 },
        {
          duration: 3000,
          repeat: 0,
          loop: false,
          reverse: false,
          ease: Hilo.Ease.Quad.EaseIn,
          onComplete: function () {
            setTimeout(() => {
              defeatSprite.removeFromParent();
              that.showBattleResult();
            }, 1500);
          },
        }
      );
    },

    renderDragon() {
      let dragonAtlas = this.asset.getDragonAtlas(this.dragon.type);
      this.dragonContainer = new Hilo.Container({
        id: "dragonContainer",
      });

      this.stage.addChild(this.dragonContainer);

      let dragonSprite = new Hilo.Sprite({
        id: "dragon",
        atlas: dragonAtlas,
      });
      dragonSprite.addFrame(dragonAtlas.getSprite("dragon"));
      dragonSprite.interval = 4;
      dragonSprite.goto("back");
      dragonSprite.width = dragonSprite.width * 1.5;
      dragonSprite.height = dragonSprite.height * 1.5;
      this.dragonContainer.addChild(dragonSprite);
      this.dragonContainer.width = dragonSprite.width;
      this.dragonContainer.height = dragonSprite.height;
      this.dragonContainer.pivotX = this.dragonContainer.width / 2;
      this.dragonContainer.pivotY = this.dragonContainer.height / 2;
      this.dragonContainer.x = this.width / 2;
      this.dragonContainer.y = this.height * 0.7;
      let dragonLife = this.dragonStartLifeData[this.battleIndex];

      this.renderPropertyBar(this.dragonContainer, "dragon", dragonLife);
      let _time = 100;
      if (this.pkSprite) {
        _time = 1100;
      }

      setTimeout(() => {
        if (this.pkSprite) {
          this.pkSprite.removeFromParent();
          this.pkSprite = null;
        }

        this.startFight();
      }, _time);
    },

    renderMonster() {
      let that = this;
      let type = this.monster.type;
      if (type === 3) {
        this.renderBossComing(() => {
          that.renderMonsterContainer(type);
        });
      } else {
        that.renderMonsterContainer(type);
      }
    },

    renderBossComing(cb) {
      let atlas = this.asset.getBossComingAtlas();
      let bossSprite = new Hilo.Sprite({
        id: "boss",
        atlas: atlas,
      });
      bossSprite.addFrame(atlas.getSprite("sprite"));
      let _scale = this.width / bossSprite.width;
      bossSprite.width = this.width;
      bossSprite.height = bossSprite.height * _scale;
      bossSprite.pivotX = bossSprite.width / 2;
      bossSprite.pivotY = bossSprite.height / 2;
      bossSprite.x = this.width / 2;
      bossSprite.y = this.height * 0.36;

      bossSprite.interval = 6;
      this.stage.addChild(bossSprite);

      setTimeout(() => {
        bossSprite.removeFromParent();
        cb();
      }, 1000);
    },

    renderMonsterContainer(type) {
      let _Container = new Hilo.Container({
        id: "monsterContainer",
      });
      this.stage.addChild(_Container);
      this.monsterContainer = _Container;

      this.renderMonsterSprite(type, _Container);
    },

    renderMonsterSprite(type, container) {
      // let atlas = this.asset.getDragonAtlas(Number(type));

      let atlas = this.asset.getMonsterAtlas(Number(type));
      let _monsterSprite = new MonsterSprite({
        id: "monster",
        atlas: atlas,
      });

      // _monsterSprite.goto("front");

      //  _monsterSprite.addFrame(atlas.getSprite("dragon"));

      container.addChild(_monsterSprite);
      // let _spaceLength =
      //   (this.width * 0.75) / (Object.keys(this.monster).length - 1);
      // if (index === this.monster.length - 1) {
      //   _monsterSprite.interval = 12;
      //   _monsterSprite.width = _monsterSprite.width * 1.3;
      //   _monsterSprite.height = _monsterSprite.height * 1.3;
      //   container.height = _monsterSprite.height;
      //   container.width = _monsterSprite.width;
      //   container.pivotX = container.width / 2;
      //   container.pivotY = container.height / 2;
      //   container.x = this.width / 2;
      //   container.y = this.height * 0.275;
      // } else {
      let _scale = 0.8;
      if (type === 3) {
        _scale = 1.8;
      }
      _monsterSprite.interval = 10;
      _monsterSprite.width = _monsterSprite.width * _scale;
      _monsterSprite.height = _monsterSprite.height * _scale;
      container.height = _monsterSprite.height;
      container.width = _monsterSprite.width;
      container.pivotX = container.width / 2;
      container.pivotY = container.height / 2;
      container.x = this.width * 0.5;
      container.y = this.height * 0.3;
      this.renderPropertyBar(container, "monster", this.monster["life"]);
      this.renderDragon();
    },

    renderPropertyBar(container, type, life) {
      let lifeProgressBar = new Hilo.Bitmap({
        id: "lifeProgressBar",
        image: this.asset.percentageBar,
      });
      lifeProgressBar.width = container.width * 0.4;
      lifeProgressBar.height = lifeProgressBar.width * 0.13;
      lifeProgressBar.pivotX = lifeProgressBar.width * 0.5;
      lifeProgressBar.x = container.width / 2;
      container.addChild(lifeProgressBar);
      let lifeProgressInner = new Hilo.Bitmap({
        id: "lifeProgressInner",
        image: this.asset.percentageInner,
      });
      let _percent = 0;
      if (type === "monster") {
        _percent = life / this.monster["life"];
      } else {
        _percent = life / this.dragon["life"];
      }
      lifeProgressInner.width = lifeProgressBar.width * _percent * 0.96;
      lifeProgressInner.height = lifeProgressBar.height * 0.75;
      lifeProgressInner.pivotX = 0;
      lifeProgressInner.pivotY = lifeProgressInner.height / 2;
      lifeProgressInner.x =
        lifeProgressBar.x -
        lifeProgressBar.width / 2 +
        lifeProgressBar.width * 0.02;
      lifeProgressInner.y = lifeProgressBar.y + lifeProgressBar.height / 2;
      container.addChild(lifeProgressInner);

      let lifeNumText = new Hilo.Container({
        id: "lifeNumText",
      });

      for (let i = 0; i < String(life).length; i++) {
        let num = Number(String(life).charAt(i));
        let numSprite = new Hilo.Bitmap({
          id: i + "_" + num,
          image: this.asset.lifeNum,
          rect: [37.545 * num, 0, 37.545, 43],
        });
        numSprite.width = numSprite.width * 0.6;
        numSprite.height = numSprite.height * 0.6;
        lifeNumText.addChild(numSprite);
        numSprite.x = lifeNumText.width * 0.3 + i * numSprite.width * 0.5;
        lifeNumText.height = numSprite.height;
        lifeNumText.width = lifeNumText.width + numSprite.width;
      }
      lifeNumText.pivotX = 0;
      lifeNumText.pivotY = lifeNumText.height / 2;
      lifeNumText.x = lifeProgressBar.x + lifeProgressBar.width * 0.6;
      lifeNumText.y = lifeProgressBar.y + lifeNumText.height * 0.25;
      container.addChild(lifeNumText);
    },

    toFight(sprite, targrt, attackNum) {
      let that = this;
      let _beforePosition = {
        x: sprite.x,
        y: sprite.y,
      };
      sprite.x = targrt.x;
      if (that.attackType === "monster") {
        sprite.y = targrt.y - sprite.height * 0.8;
      } else {
        sprite.y = targrt.y + sprite.height * 0.7;
      }

      that.renderAttack(targrt, attackNum + "", (targetDeath) => {
        sprite.x = _beforePosition.x;
        sprite.y = _beforePosition.y;
        setTimeout(() => {
          if (that.attackType === "monster") {
            if (targetDeath) {
              this.runDefeat();
              return;
            }
            that.attackType = "dragon";
            that.dragonFightMonster();
          } else {
            if (targetDeath) {
              that.monsterContainer.removeFromParent();
              that.renderBattleResult(true);
              return;
            } else {
              that.attackType = "monster";
              that.monsterFightDragon();
            }
          }
        }, 1000);
      });
    },

    startFight() {
      // this.monsterFightDragon();
      this.dragonFightMonster();
    },

    monsterFightDragon() {
      let _attackMonste = this.monsterContainer;
      let _attack = this.monsterAttack.shift();
      this.toFight(_attackMonste, this.dragonContainer, _attack);
    },

    dragonFightMonster() {
      let _attackDragon = this.dragonContainer;
      let _target = this.monsterContainer;
      let _attack = this.dragonAttack.shift();

      this.toFight(_attackDragon, _target, _attack);
    },

    renderAttack(target, attackNum, cb) {
      let that = this;
      let _attackSprite = null;
      let _time = 1;
      let atlas = this.asset.getAttackAtlas(2);
      if (this.attackSprite) {
        this.attackSprite.removeFromParent();
        this.attackSprite = null;
      }
      if (attackNum > 0) {
        _attackSprite = this.renderLifeValue(attackNum, target);
        this.attackSprite = new Hilo.Sprite({
          id: "attack",
          atlas: atlas,
        });
        this.attackSprite.addFrame(atlas.getSprite("attack"));
        this.attackSprite.width = this.attackSprite.width * 0.5;
        this.attackSprite.height = this.attackSprite.height * 0.5;
        this.attackSprite.interval = 7;
        this.attackSprite.pivotX = this.attackSprite.width / 2;
        this.attackSprite.pivotY = this.attackSprite.height / 2;
        this.attackSprite.x = target.width / 2;
        this.attackSprite.y = target.height / 2;
        target.addChild(this.attackSprite);
      } else {
        _time = 0.5;
        _attackSprite = new Hilo.Bitmap({
          id: "miss",
          image: this.asset.miss,
        });
        _attackSprite.width = _attackSprite.width * 0.4;
        _attackSprite.height = _attackSprite.height * 0.4;
        _attackSprite.x = target.x + 50;
        _attackSprite.y = target.y - 50;
        this.stage.addChild(_attackSprite);
      }

      let _result = false;
      that.refreshLifeValue((result) => {
        _result = result;
        let _y = 0;
        if (this.attackType === "monster") {
          _y = 80;
        } else {
          _y = -80;
        }
        Hilo.Tween.to(
          target,
          { y: target.y + _y },
          {
            duration: this.rhythmTime * _time,
            repeat: 0,
            loop: false,
            reverse: false,
            ease: Hilo.Ease.Quad.EaseIn,
            onComplete: function () {},
          }
        );

        Hilo.Tween.to(
          _attackSprite,
          { y: _attackSprite.y - 100, alpha: 0 },
          {
            duration: this.rhythmTime * 5 * _time,
            repeat: 0,
            loop: false,
            ease: Hilo.Ease.Quad.EaseIn,
            onComplete: function () {
              _attackSprite.removeFromParent();
              if (that.attackSprite) {
                that.attackSprite.removeFromParent();
              }
              target.y = target.y - _y;
              cb(_result);
            },
          }
        );
      });
    },

    refreshLifeValue(cb) {
      if (this.attackType === "monster") {
        let lifeNum = this.dragonLifeData.shift();
        let lifeProgressInner =
          this.dragonContainer.getChildById("lifeProgressInner");
        let lifeProgressBar =
          this.dragonContainer.getChildById("lifeProgressBar");

        this.dragonContainer.getChildById("lifeNumText").removeFromParent();
        let lifeNumText = new Hilo.Container({
          id: "lifeNumText",
        });

        for (let i = 0; i < String(lifeNum).length; i++) {
          let num = Number(String(lifeNum).charAt(i));
          let numSprite = new Hilo.Bitmap({
            id: i + "_" + num,
            image: this.asset.lifeNum,
            rect: [37.545 * num, 0, 37.545, 43],
          });
          numSprite.width = numSprite.width * 0.6;
          numSprite.height = numSprite.height * 0.6;
          lifeNumText.addChild(numSprite);
          numSprite.x = lifeNumText.width * 0.3 + i * numSprite.width * 0.5;
          lifeNumText.height = numSprite.height;
          lifeNumText.width = lifeNumText.width + numSprite.width;
        }
        lifeNumText.pivotX = 0;
        lifeNumText.pivotY = lifeNumText.height / 2;
        lifeNumText.x = lifeProgressBar.x + lifeProgressBar.width * 0.6;
        lifeNumText.y = lifeProgressBar.y + lifeNumText.height * 0.25;
        this.dragonContainer.addChild(lifeNumText);
        if (lifeNum > 0) {
          lifeProgressInner.width =
            lifeProgressBar.width * (lifeNum / this.dragon["life"]) * 0.96;
          cb(false);
        } else {
          lifeProgressInner.width = 0;
          cb(true);
        }
      } else {
        let lifeNum = this.monsterLifeData.shift();
        let _monsterContainer = this.monsterContainer;
        let lifeProgressInner =
          _monsterContainer.getChildById("lifeProgressInner");
        let lifeProgressBar = _monsterContainer.getChildById("lifeProgressBar");
        this.monsterContainer.getChildById("lifeNumText").removeFromParent();
        let lifeNumText = new Hilo.Container({
          id: "lifeNumText",
        });

        for (let i = 0; i < String(lifeNum).length; i++) {
          let num = Number(String(lifeNum).charAt(i));
          let numSprite = new Hilo.Bitmap({
            id: i + "_" + num,
            image: this.asset.lifeNum,
            rect: [37.545 * num, 0, 37.545, 43],
          });
          numSprite.width = numSprite.width * 0.6;
          numSprite.height = numSprite.height * 0.6;
          lifeNumText.addChild(numSprite);
          numSprite.x = lifeNumText.width * 0.3 + i * numSprite.width * 0.5;
          lifeNumText.height = numSprite.height;
          lifeNumText.width = lifeNumText.width + numSprite.width;
        }
        lifeNumText.pivotX = 0;
        lifeNumText.pivotY = lifeNumText.height / 2;
        lifeNumText.x = lifeProgressBar.x + lifeProgressBar.width * 0.6;
        lifeNumText.y = lifeProgressBar.y + lifeNumText.height * 0.25;
        this.monsterContainer.addChild(lifeNumText);
        if (lifeNum > 0) {
          lifeProgressInner.width =
            lifeProgressBar.width * (lifeNum / this.monster["life"]) * 0.96;
          cb(false);
        } else {
          lifeProgressInner.width = 0;
          cb(true);
        }
      }
    },

    renderLifeValue(attackNum, target) {
      let _attackSprite = new Hilo.Container({
        id: "countLife",
      });
      let _count = new Hilo.Bitmap({
        id: "count",
        image: this.asset.num,
        rect: [37.545 * 10, 0, 37.545, 43],
      });

      _attackSprite.addChild(_count);
      _attackSprite.width = _count.width;
      for (let i = 0; i < attackNum.length; i++) {
        let num = Number(attackNum.charAt(i));
        let numSprite = new Hilo.Bitmap({
          id: i + "_" + num,
          image: this.asset.num,
          rect: [37.545 * num, 0, 37.545, 43],
        });
        _attackSprite.addChild(numSprite);
        numSprite.x = _attackSprite.width * 0.7 + i * 30;
      }

      _attackSprite.x = target.x + 50;
      _attackSprite.y = target.y - 50;
      this.stage.addChild(_attackSprite);
      return _attackSprite;
    },

    showBattleResult() {
      this.resultDialogVisiable = true;
    },

    backToDungeon() {
      this.resultDialogVisiable = false;
      this.$parent.backDungeonHome();
    },
  },
};
</script>

<style scoped>
.battle-container {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-image: url("../../asset/ui/fight-bg.png");
  background-size: 100% 100%;
  z-index: 10;
  background-size: 100% 116%;
}
.avatar-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.avatar-container > div {
  width: 40%;
  height: 80px;
  background-size: contain;
  background-repeat: no-repeat;
  position: relative;
}

.avatar-container .round-num {
  width: 20%;
}

.avatar-container .round-num .round-box {
  width: 100%;
  font-size: 33px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-weight: 500;
  text-shadow: -0.0355em -0.0355em 0 #000, 0.0355em -0.0355em 0 #000,
    -0.0355em 0.0355em 0 #000, 0.0355em 0.0355em 0 #000;
  color: #fcfcfc;
  color: #ff9e5e;
}

.avatar-container .round-num .round-box span {
  font-size: 55px;
  color: #ff9e5e;
}

.avatar-container .avatar-left {
  background-image: url("../../asset/ui/avatar_left.png");
}
.avatar-container .avatar-left .avatar {
  position: absolute;
  width: 30%;
  top: 50%;
  left: 5%;
  transform: translate(0, -50%);
}

.avatar-container .attr-box {
  justify-content: space-between;
  width: 45%;
  position: absolute;
  top: 105%;
}

.avatar-container .attr-box > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 0 10% 0 5%;
  background-image: url("../../asset/ui/inner-tab.png");
  background-size: 100% 100%;
  margin-bottom: 6px;
}

.avatar-container .attr-box > div img {
  height: 35px;
}

.avatar-container .attr-box > div p {
  line-height: 30px;
  font-size: 16px;
  font-weight: 500;
  text-shadow: -0.0555em -0.0555em 0 #000, 0.0555em -0.0555em 0 #000,
    -0.0555em 0.0555em 0 #000, 0.0555em 0.0555em 0 #000;
  color: #fbfaf9;
}
.avatar-container .avatar-right .avatar {
  position: absolute;
  width: 40%;
  top: 50%;
  left: 60%;
  transform: translate(0, -50%);
}
.avatar-container .avatar-left .attr-box {
  left: 0%;
}
.avatar-container .avatar-right .attr-box {
  right: 0%;
}
.avatar-container .avatar-right {
  background-image: url("../../asset/ui/avatar_right.png");
  background-position: right;
}

.avatar-container .vs {
  transform: scale(0.7);
}

.avatar-container .avatar-left .name {
  position: absolute;
  left: 40%;
  top: 12%;
  font-size: 22px;
  font-weight: 500;
  text-shadow: -0.0455em -0.0455em 0 #000, 0.04555em -0.0455em 0 #000,
    -0.0455em 0.0455em 0 #000, 0.0455em 0.0455em 0 #000;
  color: #fff;
}

.avatar-container .avatar-right .name {
  position: absolute;
  right: 40%;
  top: 12%;
  font-size: 22px;
  font-weight: 500;
  text-shadow: -0.0455em -0.0455em 0 #000, 0.04555em -0.0455em 0 #000,
    -0.0455em 0.0455em 0 #000, 0.0455em 0.0455em 0 #000;
  color: #fff;
}

.over-button {
  width: 80px;
  height: 80px;
  background-image: url("../../asset/ui/round.png");
  box-sizing: border-box;
  padding: 4% 0 0;
  background-size: 100% 100%;
  position: absolute;
  right: 0;
  bottom: 10%;
  transform: translate(0,0);
  cursor: pointer;
  font-weight: 500;
  color: #fff;
  text-shadow: -0.0335em -0.0335em 0 #fcf8ca, 0.0335em -0.0335em #000,
    -0.0335em 0.0335em 0 #000, 0.0335em 0.0335em 0 #000;
  z-index: 1;
}

.over-button:hover {
  text-shadow: -0.0335em -0.0335em 0 #000, 0.0335em -0.0335em #000,
    -0.0335em 0.0335em 0 #000, 0.0335em 0.0335em 0 #000;
  cursor: pointer;
}

.info-container {
  width: 108%;
  height: 20vh;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  background-image: url("../../asset/ui/gmwbz_bg_mi.png");
  background-size: 100% 112%;
  background-position: 55% 0%;
  box-sizing: border-box;
  padding: 6% 10% 4%;
  display: flex;
  justify-content: space-around;
}

.info-container .attributes-list {
  width: 48%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}

.info-container .attributes-list img {
  width: 26px;
  height: 26px;
}

.info-container .attributes-list > div {
  width: 48.6%;
  display: flex;
  justify-content: space-between;
  height: 30px;
  align-items: center;
  align-content: center;
  color: rgba(255, 255, 255, 0.8);

  background-size: 100% 100%;
  box-sizing: border-box;
  padding: 0 5%;
  align-items: center;
  font-size: 20px;
}

.info-container .dragon-attrs > div {
  background-image: url("../../asset/ui/bg_blue.png");
  box-sizing: border-box;
  padding: 0 8%;
  opacity: 0.9;
}

.info-container .monster-attrs > div {
  background-image: url("../../asset/ui/bg_red.png");
  opacity: 0.9;
  padding: 0 8%;
}

.result-container {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.45);
  z-index: 9;
}

.result-container .result-box {
  width: 100%;
  height: 50%;
  background-image: url("../../asset/ui/battleresult.png");
  background-size: 100% 100%;
  box-sizing: border-box;
  padding: 20% 0 30%;
}

.result-container .result-box .result-title {
  position: absolute;
  left: 50%;
  top: 10%;
  transform: translate(-50%, 0);
  background-size: 100% 100%;
  font-size: 45px;
  font-weight: 500;
  background-image: linear-gradient(to top, #ffcc5e, rgb(250, 125, 75));
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

.result-container .result-box .result-list {
  display: flex;
  top: 35%;
}

.result-container .result-box .result-list .result-item {
  width: 100px;
  height: 100px;
  margin: 20px;
  background-image: url("../../asset/ui/reward.png");
  background-size: 100% 100%;
  position: relative;
}

.result-container .result-box .result-list .result-item img {
  width: 70%;
}

.result-container .result-box .result-list .result-item p {
  font-weight: 400;
  color: #fdc54e;
  font-size: 26px;
  text-shadow: -0.04555em -0.04555em 0 #000, 0.04555em -0.04555em #000,
    -0.04555em 0.04555em 0 #000, 0.04555em 0.04555em 0 #000;
  position: absolute;
  bottom: 5%;
  right: 5%;
}

.result-container .result-box .confirm-btn {
  position: absolute;
  bottom: 43%;
  left: 50%;
  transform: translate(-50%, 50%);
  background-image: url("../../asset/ui/common_btn_confirm.png");
  background-size: 100% 100%;
  padding: 10px 20px;
  cursor: pointer;
  color: #fff;
  font-size: 27px;
  font-weight: 400;
  text-shadow: -0.04555em -0.04555em 0 #000, 0.04555em -0.04555em #000,
    -0.04555em 0.04555em 0 #000, 0.04555em 0.04555em 0 #000;
}
</style>


