          <div class="attr-item">
            <div>
              <svg
                t="1634637704008"
                class="icon"
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="1696"
                width="200"
                height="200"
              >
                <path
                  d="M512 192c-176.469333 0-320 143.530667-320 320s143.530667 320 320 320 320-143.530667 320-320-143.530667-320-320-320zM512 149.333333a21.333333 21.333333 0 0 0 21.333333-21.333333V42.666667a21.333333 21.333333 0 0 0-42.666666 0v85.333333a21.333333 21.333333 0 0 0 21.333333 21.333333zM512 874.666667a21.333333 21.333333 0 0 0-21.333333 21.333333v85.333333a21.333333 21.333333 0 0 0 42.666666 0v-85.333333a21.333333 21.333333 0 0 0-21.333333-21.333333zM981.333333 490.666667h-85.333333a21.333333 21.333333 0 0 0 0 42.666666h85.333333a21.333333 21.333333 0 0 0 0-42.666666zM149.333333 512a21.333333 21.333333 0 0 0-21.333333-21.333333H42.666667a21.333333 21.333333 0 0 0 0 42.666666h85.333333a21.333333 21.333333 0 0 0 21.333333-21.333333zM301.525333 190.122667a21.376 21.376 0 0 0 29.141334 7.808 21.333333 21.333333 0 0 0 7.808-29.141334l-42.666667-73.898666a21.333333 21.333333 0 1 0-36.949333 21.333333l42.666666 73.898667zM722.474667 833.877333a21.333333 21.333333 0 1 0-36.949334 21.333334l42.666667 73.898666a21.333333 21.333333 0 1 0 36.992-21.333333l-42.709333-73.898667zM844.586667 341.333333a21.077333 21.077333 0 0 0 10.666666-2.858666l73.898667-42.666667a21.333333 21.333333 0 0 0-21.333333-36.949333l-73.898667 42.666666a21.333333 21.333333 0 0 0 10.666667 39.808zM168.789333 685.525333l-73.898666 42.666667a21.333333 21.333333 0 0 0 21.333333 36.949333l73.898667-42.666666a21.333333 21.333333 0 0 0-21.333334-36.949334zM94.890667 295.808l73.898666 42.666667a21.333333 21.333333 0 0 0 21.333334-36.949334l-73.898667-42.666666a21.248 21.248 0 0 0-29.141333 7.808 21.248 21.248 0 0 0 7.808 29.141333zM929.109333 728.192l-73.898666-42.666667a21.333333 21.333333 0 0 0-21.333334 36.949334l73.898667 42.666666a21.333333 21.333333 0 0 0 21.333333-36.949333zM693.333333 197.930667a21.504 21.504 0 0 0 29.184-7.808l42.666667-73.898667a21.333333 21.333333 0 0 0-36.949333-21.333333l-42.666667 73.898666a21.333333 21.333333 0 0 0 7.765333 29.141334zM330.666667 826.069333a21.33333