              >
                <path
                  d="M942.246 318.937l-52.517 27.805-9.267 58.697-105.05 84.965-64.878-4.635-145.198-194.638-3.099-37.075 15.451-24.719V179.91l-58.692-77.229-58.693 77.229v49.427l15.451 24.719-3.097 37.075-145.2 194.638-64.878 4.635-105.049-84.965-9.268-58.697-52.516-27.805-24.719 3.086v52.524l160.652 395.457 231.71 108.127h111.213l231.711-108.127 160.65-395.457v-52.524l-24.717-3.086zM613.413 633.521l-1.136 0.003c-19.509 4.509-51.31 27.141-59.777 36.853-8.128 9.312-30.185 52.305-32.754 57.335l0.004 0.486c-0.022-0.111-0.054-0.229-0.078-0.342l-0.174 0.342-0.006-1.131c-4.561-19.499-27.283-51.244-37.015-59.685-9.918-8.608-57.908-32.849-57.908-32.849l94.422-0.761H424.57c19.406-3.764 52.425-26.934 61.114-36.782 8.177-9.268 30.456-52.126 33.057-57.151l-0.001-0.487c0.021 0.111 0.053 0.229 0.076 0.343l0.177-0.343v1.138c4.454 19.521 27.008 51.38 36.694 59.876 9.283 8.151 52.206 30.317 57.239 32.905l0.487-0.003c-0.112 0.022-0.229 0.054-0.343 0.077l0.343 0.176z"
                  p-id="5063"
