    <div class="result-container center" v-if="resultDialogVisiable">
      <div class="result-box center">
        <div class="result-title">Your loot</div>

        <div class="result-list center">
          <div class="result-item">
            <img class="center" src="../../asset/ui/dg.png" alt />
            <p>{{ reward }}</p>
          </div>
        </div>

        <div
          class="confirm-btn"
          @click="$parent.getInfo(), ($parent.battleViewVisiable = false)"
        >
          {{ $t("common.confirm") }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Util from "../../utils/util";

export default {
  name: "BattleView",
  props: {
    battleInfo: Object,
    battleRewards: Object,
  },
  data() {
    return {
      stage: null,
      width: document.documentElement.clientWidth * 2,
      height: document.documentElement.clientHeight * 2,
      scale: 0.5,
      asset: null,
      canvas: null,
      cyberDragonAtlas: [],
      cyberDragonSprites: [],
      cyberDragonContainers: [],
      fightTween: null,
      rhythmTime: 100,

      cyberDragonNum: 0,
      battleData: [],
      resultDialogVisiable: false,
      cyberDragonsData: [],
      attackSprite: null,

      attackType: "",

      dragonContainer: null,
      propertyDialogVisiable: false,
      avatarImgs: {},
      cyberDragonAttack: [],
      dragonAttack: [],
      cyberDragonLifeData: [],
      dragonLifeData: [],
      dragonStartLife: 0,
      dragonStartLifeData: [],

      dragon: null,
      cyberDragon: null,
      remainHp: [],
      reward: null,

      // battleInfo: [
      //   [
      //     "539",
      //     "418",
      //     "539",
      //     "369",
      //     "539",
      //     "369",
      //     "493",
      //     "324",
      //     "449",
      //     "252",
      //     "449",
      //     "214",
      //     "449",
      //     "156",
      //     "403",
      //     "74",
      //     "403",
      //     "0",
      //   ],
      //   ["32000000000000000000", "1070000000000000000", "5"],
      // ],
    };
  },

  mounted() {
    if (this.width > 480) {
      this.width = (document.documentElement.clientHeight / 1.78) * 2;
    }
    this.asset = this.$store.state.asset;
    this.dragon = this.battleInfo["dragon"];
    this.cyberDragon = this.battleInfo["cyber"];
    this.remainHp = this.battleInfo["remainHp"];
    this.reward = this.battleInfo["reward"];
    if (this.battleInfo["challenger"] === "dragon") {
      this.attackType = "dragon";
    } else {
      this.attackType = "cyberDragon";
    }
    this.initStage(this.$refs.canvasContainer);
  },

  methods: {
    initStage(container) {
      this.stage = new Hilo.Stage({
        renderType: "canvas",
        width: this.width,
        height: this.height,
        scaleX: this.scale,
        scaleY: this.scale,
        container: container,
      });
      this.stage.canvas.style.left = 0;
      this.stage.canvas.style.top = 0;
      this.stage.enableDOMEvent([
        Hilo.event.POINTER_START,
        Hilo.event.POINTER_MOVE,
        Hilo.event.POINTER_END,
      ]);
      this.ticker = new Hilo.Ticker(60);
      this.ticker.addTick(this.stage);
      this.ticker.addTick(Hilo.Tween);
      this.ticker.start(true);
      this.renderBattleInfo();
    },

    runStartPk() {
      let that = this;
      let atlas = this.asset.getStartBattleAtlas();
      let pkSprite = new Hilo.Sprite({
        id: "pk",
        atlas: atlas,
      });
      pkSprite.addFrame(atlas.getSprite("pk"));
      let _scale = this.width / pkSprite.width;
      pkSprite.width = this.width;
      pkSprite.height = pkSprite.height * _scale;
      pkSprite.pivotX = pkSprite.width / 2;
      pkSprite.pivotY = pkSprite.height / 2;
      pkSprite.x = this.width / 2;
      pkSprite.y = this.height * 0.36;

      pkSprite.interval = 6;
      this.stage.addChild(pkSprite);

      setTimeout(() => {
        pkSprite.removeFromParent();
        that.startFight(); 
      }, 2200);
      that.rendercyberDragon();
      that.renderDragon();
    },

    // renderBattlesData() {
    //   let dragonStartLife = this.dragon["life"];
    //     this.dragonStartLifeData.push(dragonStartLife);
    //   this.renderBattleInfo(true);
    // },

    renderInfoBar() {
      this.$set(
        this.avatarImgs,
        "dragon",
        "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/dragons/dragon" +
          this.dragon.type +
          ".png"
      );
      this.$set(
        this.avatarImgs,
        "cyberDragon",
        "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/bnx/hero-" +
          this.cyberDragon.type +
          ".png"
      );
    },

    renderBattleInfo() {
      let _remainHp = this.remainHp;
      let _dragonsLife = [];
      let _cyberDragonLife = [];
      let dragonAttackNums = [];
      let cyberDragonAttackNums = [];
      if (this.attackType === "cyberDragon") {
        _dragonsLife.push(this.dragon.life);
        for (let i = 1; i <= _remainHp.length; i++) {
          if (i % 2 === 0) {
            _dragonsLife.push(_remainHp[i - 1]);
          } else {
            _cyberDragonLife.push(_remainHp[i - 1]);
          }
        }
      } else {
        _cyberDragonLife.push(this.cyberDragon.life);
        for (let i = 1; i <= _remainHp.length; i++) {
          if (i % 2 === 0) {
            _cyberDragonLife.push(_remainHp[i - 1]);
          } else {
            _dragonsLife.push(_remainHp[i - 1]);
          }
        }
        ///
      }

      for (let i in _cyberDragonLife) {
        if (i > 0) {
          dragonAttackNums.push(
            Math.abs(_cyberDragonLife[i] - _cyberDragonLife[i - 1])
          );
        }
      }
      if (_cyberDragonLife[_cyberDragonLife.length - 1] === 0) {
        dragonAttackNums[dragonAttackNums.length - 1] =
          dragonAttackNums[dragonAttackNums.length - 1] +
          Math.floor(
            dragonAttackNums[dragonAttackNums.length - 1] *
              Math.random().toFixed(1)
          );
      }

      for (let i in _dragonsLife) {
        if (i > 0) {
          cyberDragonAttackNums.push(
            Math.abs(_dragonsLife[i] - _dragonsLife[i - 1])
          );
        }
      }

      if (_dragonsLife[_dragonsLife.length - 1] === 0) {
        cyberDragonAttackNums[cyberDragonAttackNums.length - 1] =
          cyberDragonAttackNums[cyberDragonAttackNums.length - 1] +
          Math.floor(
            cyberDragonAttackNums[cyberDragonAttackNums.length - 1] *
              Math.random().toFixed(1)
          );
      }

      this.dragonAttack = dragonAttackNums;

      this.dragonLifeData = _dragonsLife.splice(1, _dragonsLife.length - 1);

      this.cyberDragonAttack = cyberDragonAttackNums;
      this.cyberDragonLifeData = _cyberDragonLife.splice(
        1,
        _cyberDragonLife.length - 1
      );
      this.renderInfoBar();
      this.runStartPk();
    },

    renderBattleResult(death) {
      if (this.battleInfo["status"]) {
        this.runVictory();
      } else {
        this.runDefeat();
      }
    },

    runNextPk() {
      this.battleIndex++;
      this.dragonContainer.removeFromParent();
      this.dragonContainer = null;
      this.renderBattleInfo();
    },

    runVictory() {
      let that = this;
      this.dragonContainer.removeFromParent();
      this.cyberDragonContainer.removeFromParent();
      let atlas = this.asset.getVictoryAtlas();
      let victorySprite = new Hilo.Sprite({
        id: "victory",
        atlas: atlas,
      });
      victorySprite.addFrame(atlas.getSprite("victory"));
      victorySprite.interval = 5;
      let _scale = this.width / victorySprite.width;
      victorySprite.width = _scale * victorySprite.width;
      victorySprite.height = _scale * victorySprite.height;
      victorySprite.pivotX = victorySprite.width / 2;
      victorySprite.pivotY = victorySprite.height / 2;
      victorySprite.x = this.width * 0.55;
      victorySprite.y = this.height * 0.4;
      this.stage.addChild(victorySprite);

      setTimeout(() => {
        victorySprite.removeFromParent();
        that.showBattleResult();
      }, 2000);
    },

    runDefeat() {
      let that = this;
      this.dragonContainer.removeFromParent();
      this.cyberDragonContainer.removeFromParent();
      let defeatSprite = new Hilo.Bitmap({
        id: "defeat",
        image: this.asset.defeat,
      });
      defeatSprite.pivotX = defeatSprite.width / 2;
      defeatSprite.pivotY = defeatSprite.height / 2;
      defeatSprite.x = this.width / 2;
      defeatSprite.y = this.height / 2;
      defeatSprite.scaleX = 0;
      defeatSprite.scaleY = 0;
      defeatSprite.alpha = 0;
      // defeatSprite.opacity
      this.stage.addChild(defeatSprite);
      Hilo.Tween.to(
        defeatSprite,
        { alpha: 1 },
        {
          duration: 1000,
          repeat: 0,
          loop: false,
          reverse: false,
          ease: Hilo.Ease.Quad.EaseIn,
        }
      );
      Hilo.Tween.to(
        defeatSprite,
        { scaleX: 1.5, scaleY: 1.5 },
        {
          duration: 3000,
          repeat: 0,
          loop: false,
          reverse: false,
          ease: Hilo.Ease.Quad.EaseIn,
          onComplete: function () {
            setTimeout(() => {
              defeatSprite.removeFromParent();
              that.$parent.getInfo();
              that.$parent.battleViewVisiable = false;
            }, 1500);
          },
        }
      );
    },

    renderDragon() {
      let dragonAtlas = this.asset.getDragonAtlas(this.dragon.type);
      this.dragonContainer = new Hilo.Container({
        id: "dragonContainer",
      });

      this.stage.addChild(this.dragonContainer);

      let dragonSprite = new Hilo.Sprite({
        id: "dragon",
        atlas: dragonAtlas,
      });
      dragonSprite.addFrame(dragonAtlas.getSprite("dragon"));
      dragonSprite.interval = 4;
      dragonSprite.goto("sideright");
      dragonSprite.width = dragonSprite.width * 2;
      dragonSprite.height = dragonSprite.height * 2;
      this.dragonContainer.addChild(dragonSprite);
      this.dragonContainer.width = dragonSprite.width;
      this.dragonContainer.height = dragonSprite.height;
      this.dragonContainer.pivotX = this.dragonContainer.width / 2;
      this.dragonContainer.pivotY = this.dragonContainer.height / 2;
      this.dragonContainer.x =
        this.width * 0.08 + this.dragonContainer.width / 2;
      this.dragonContainer.y = this.height * 0.4;
      let dragonLife = this.dragon.life;
      this.renderPropertyBar(this.dragonContainer, "dragon", dragonLife);
    },

    rendercyberDragon() {
      let type = this.cyberDragon.type;
      let _Container = new Hilo.Container({
        id: "cyberDragonContainer",
      });
      this.stage.addChild(_Container);
      this.cyberDragonContainer = _Container;

      this.rendercyberDragonSprite(type, _Container);
    },

    rendercyberDragonSprite(type, container) {
      // let atlas = this.asset.getDragonAtlas(Number(type));
      let _cyberDragonSprite = new Hilo.Bitmap({
        id: "cyberDragon",
        image: this.asset["cyber_" + this.cyberDragon["profession"]],
      });

      // _cyberDragonSprite.goto("front");

      //  _cyberDragonSprite.addFrame(atlas.getSprite("dragon"));

      container.addChild(_cyberDragonSprite);
      // let _spaceLength =
      //   (this.width * 0.75) / (Object.keys(this.cyberDragon).length - 1);
      // if (index === this.cyberDragon.length - 1) {
      //   _cyberDragonSprite.interval = 12;
      //   _cyberDragonSprite.width = _cyberDragonSprite.width * 1.3;
      //   _cyberDragonSprite.height = _cyberDragonSprite.height * 1.3;
      //   container.height = _cyberDragonSprite.height;
      //   container.width = _cyberDragonSprite.width;
      //   container.pivotX = container.width / 2;
      //   container.pivotY = container.height / 2;
      //   container.x = this.width / 2;
      //   container.y = this.height * 0.275;
      // } else {
      let _scale = 0.8;

      _cyberDragonSprite.width = _cyberDragonSprite.width * _scale * 0.7;
      _cyberDragonSprite.height = _cyberDragonSprite.height * _scale * 0.7;
      container.height = _cyberDragonSprite.height;
      container.width = _cyberDragonSprite.width;
      container.pivotX = container.width / 2;
      container.pivotY = container.height / 2;
      container.x = this.width - this.width * 0.08 - container.width / 2;
      container.y = this.height * 0.4;
      this.renderPropertyBar(
        container,
        "cyberDragon",
        this.cyberDragon["life"]
      );
    },

    renderPropertyBar(container, type, life) {
      let lifeProgressBar = new Hilo.Bitmap({
        id: "lifeProgressBar",
        image: this.asset.percentageBar,
      });
      lifeProgressBar.width = container.width * 0.4;
      lifeProgressBar.height = lifeProgressBar.width * 0.13;
      lifeProgressBar.pivotX = lifeProgressBar.width * 0.5;
      lifeProgressBar.x = container.width / 2;
      container.addChild(lifeProgressBar);
      let lifeProgressInner = new Hilo.Bitmap({
        id: "lifeProgressInner",
        image: this.asset.percentageInner,
      });
      let _percent = 0;
      if (type === "cyberDragon") {
        _percent = life / this.cyberDragon["life"];
      } else {
        _percent = life / this.dragon["life"];
      }
      lifeProgressInner.width = lifeProgressBar.width * _percent * 0.96;
      lifeProgressInner.height = lifeProgressBar.height * 0.75;
      lifeProgressInner.pivotX = 0;
      lifeProgressInner.pivotY = lifeProgressInner.height / 2;
      lifeProgressInner.x =
        lifeProgressBar.x -
        lifeProgressBar.width / 2 +
        lifeProgressBar.width * 0.02;
      lifeProgressInner.y = lifeProgressBar.y + lifeProgressBar.height / 2;
      container.addChild(lifeProgressInner);

      let lifeNumText = new Hilo.Container({
        id: "lifeNumText",
      });

      for (let i = 0; i < String(life).length; i++) {
        let num = Number(String(life).charAt(i));
        let numSprite = new Hilo.Bitmap({
          id: i + "_" + num,
          image: this.asset.lifeNum,
          rect: [37.545 * num, 0, 37.545, 43],
        });
        numSprite.width = numSprite.width * 0.6;
        numSprite.height = numSprite.height * 0.6;
        lifeNumText.addChild(numSprite);
        numSprite.x = lifeNumText.width * 0.3 + i * numSprite.width * 0.5;
        lifeNumText.height = numSprite.height;
        lifeNumText.width = lifeNumText.width + numSprite.width;
      }
      lifeNumText.pivotX = 0;
      lifeNumText.pivotY = lifeNumText.height / 2;
      lifeNumText.x = lifeProgressBar.x + lifeProgressBar.width * 0.6;
      lifeNumText.y = lifeProgressBar.y + lifeNumText.height * 0.25;
      container.addChild(lifeNumText);
    },

    toFight(sprite, targrt, attackNum) {
      let that = this;
      let _beforePosition = {
        x: sprite.x,
        y: sprite.y,
      };
      sprite.x = targrt.x;
      if (that.attackType === "dragon") {
        sprite.x = targrt.x - sprite.width * 0.8;
      } else {
        sprite.x = targrt.x + sprite.width * 0.7;
      }

      that.renderAttack(targrt, attackNum + "", (targetDeath) => {
        sprite.x = _beforePosition.x;
        sprite.y = _beforePosition.y;
        setTimeout(() => {
          if (that.attackType === "cyberDragon") {
            if (targetDeath) {
              that.renderBattleResult("cyber");
              return;
            }
            that.attackType = "dragon";
            that.dragonFightcyberDragon();
          } else {
            if (targetDeath) {
              that.cyberDragonContainer.removeFromParent();
              that.renderBattleResult("dragon");
              return;
            } else {
              that.attackType = "cyberDragon";
              that.cyberDragonFightDragon();
            }
          }
        }, 1000);
      });
    },

    startFight() {
      if (this.battleInfo["challenger"] === "dragon") {
        this.dragonFightcyberDragon();
      } else {
        this.cyberDragonFightDragon();
      }
    },

    cyberDragonFightDragon() {
      let _attackMonste = this.cyberDragonContainer;
      let _attack = this.cyberDragonAttack.shift();
      this.toFight(_attackMonste, this.dragonContainer, _attack);
    },

    dragonFightcyberDragon() {
      let _attackDragon = this.dragonContainer;
      let _target = this.cyberDragonContainer;
      let _attack = this.dragonAttack.shift();

      this.toFight(_attackDragon, _target, _attack);
    },

    renderAttack(target, attackNum, cb) {
      let that = this;
      let _attackSprite = null;
      let _time = 1;
      let atlas = this.asset.getAttackAtlas(2);
      if (this.attackSprite) {
        this.attackSprite.removeFromParent();
        this.attackSprite = null;
      }
      if (attackNum > 0) {
        _attackSprite = this.renderLifeValue(attackNum, target);
        this.attackSprite = new Hilo.Sprite({
          id: "attack",
          atlas: atlas,
        });
        this.attackSprite.addFrame(atlas.getSprite("attack"));
        this.attackSprite.width = this.attackSprite.width * 0.5;
        this.attackSprite.height = this.attackSprite.height * 0.5;
        this.attackSprite.interval = 7;
        this.attackSprite.pivotX = this.attackSprite.width / 2;
        this.attackSprite.pivotY = this.attackSprite.height / 2;
        this.attackSprite.x = target.width / 2;
        this.attackSprite.y = target.height / 2;
        target.addChild(this.attackSprite);
      } else {
        _time = 0.5;
        _attackSprite = new Hilo.Bitmap({
          id: "miss",
          image: this.asset.miss,
        });
        _attackSprite.width = _attackSprite.width * 0.4;
        _attackSprite.height = _attackSprite.height * 0.4;
        _attackSprite.x = target.x + 50;
        _attackSprite.y = target.y - 50;
        this.stage.addChild(_attackSprite);
      }

      let _result = false;
      that.refreshLifeValue((result) => {
        _result = result;
        let _x = 0;
        if (this.attackType === "dragon") {
          _x = 80;
        } else {
          _x = -80;
        }
        Hilo.Tween.to(
          target,
          { x: target.x + _x },
          {
            duration: this.rhythmTime * _time,
            repeat: 0,
            loop: false,
            reverse: false,
            ease: Hilo.Ease.Quad.EaseIn,
            onComplete: function () {},
          }
        );

        Hilo.Tween.to(
          _attackSprite,
          { y: _attackSprite.y - 100, alpha: 0 },
          {
            duration: this.rhythmTime * 5 * _time,
            repeat: 0,
            loop: false,
            ease: Hilo.Ease.Quad.EaseIn,
            onComplete: function () {
              _attackSprite.removeFromParent();
              if (that.attackSprite) {
                that.attackSprite.removeFromParent();
              }
              target.x = target.x - _x;
              cb(_result);
            },
          }
        );
      });
    },

    refreshLifeValue(cb) {
      if (this.attackType === "cyberDragon") {
        let lifeNum = this.dragonLifeData.shift();
        let lifeProgressInner =
          this.dragonContainer.getChildById("lifeProgressInner");
        let lifeProgressBar =
          this.dragonContainer.getChildById("lifeProgressBar");

        this.dragonContainer.getChildById("lifeNumText").removeFromParent();
        let lifeNumText = new Hilo.Container({
          id: "lifeNumText",
        });

        for (let i = 0; i < String(lifeNum).length; i++) {
          let num = Number(String(lifeNum).charAt(i));
          let numSprite = new Hilo.Bitmap({
            id: i + "_" + num,
            image: this.asset.lifeNum,
            rect: [37.545 * num, 0, 37.545, 43],
          });
          numSprite.width = numSprite.width * 0.6;
          numSprite.height = numSprite.height * 0.6;
          lifeNumText.addChild(numSprite);
          numSprite.x = lifeNumText.width * 0.3 + i * numSprite.width * 0.5;
          lifeNumText.height = numSprite.height;
          lifeNumText.width = lifeNumText.width + numSprite.width;
        }
        lifeNumText.pivotX = 0;
        lifeNumText.pivotY = lifeNumText.height / 2;
        lifeNumText.x = lifeProgressBar.x + lifeProgressBar.width * 0.6;
        lifeNumText.y = lifeProgressBar.y + lifeNumText.height * 0.25;
        this.dragonContainer.addChild(lifeNumText);
        if (lifeNum > 0) {
          lifeProgressInner.width =
            lifeProgressBar.width * (lifeNum / this.dragon["life"]) * 0.96;
          cb(false);
        } else {
          lifeProgressInner.width = 0;
          cb(true);
        }
      } else {
        let lifeNum = this.cyberDragonLifeData.shift();
        let _cyberDragonContainer = this.cyberDragonContainer;
        let lifeProgressInner =
          _cyberDragonContainer.getChildById("lifeProgressInner");
        let lifeProgressBar =
          _cyberDragonContainer.getChildById("lifeProgressBar");
        this.cyberDragonContainer
          .getChildById("lifeNumText")
          .removeFromParent();
        let lifeNumText = new Hilo.Container({
          id: "lifeNumText",
        });

        for (let i = 0; i < String(lifeNum).length; i++) {
          let num = Number(String(lifeNum).charAt(i));
          let numSprite = new Hilo.Bitmap({
            id: i + "_" + num,
            image: this.asset.lifeNum,
            rect: [37.545 * num, 0, 37.545, 43],
          });
          numSprite.width = numSprite.width * 0.6;
          numSprite.height = numSprite.height * 0.6;
          lifeNumText.addChild(numSprite);
          numSprite.x = lifeNumText.width * 0.3 + i * numSprite.width * 0.5;
          lifeNumText.height = numSprite.height;
          lifeNumText.width = lifeNumText.width + numSprite.width;
        }
        lifeNumText.pivotX = 0;
        lifeNumText.pivotY = lifeNumText.height / 2;
        lifeNumText.x = lifeProgressBar.x + lifeProgressBar.width * 0.6;
        lifeNumText.y = lifeProgressBar.y + lifeNumText.height * 0.25;
        this.cyberDragonContainer.addChild(lifeNumText);
        if (lifeNum > 0) {
          lifeProgressInner.width =
            lifeProgressBar.width * (lifeNum / this.cyberDragon["life"]) * 0.96;
          cb(false);
        } else {
          lifeProgressInner.width = 0;
          cb(true);
        }
      }
    },

    renderLifeValue(attackNum, target) {
      let _attackSprite = new Hilo.Container({
        id: "countLife",
      });
      let _count = new Hilo.Bitmap({
        id: "count",
        image: this.asset.num,
        rect: [37.545 * 10, 0, 37.545, 43],
      });

      _attackSprite.addChild(_count);
      _attackSprite.width = _count.width;
      for (let i = 0; i < attackNum.length; i++) {
        let num = Number(attackNum.charAt(i));
        let numSprite = new Hilo.Bitmap({
          id: i + "_" + num,
          image: this.asset.num,
          rect: [37.545 * num, 0, 37.545, 43],
        });
        _attackSprite.addChild(numSprite);
        numSprite.x = _attackSprite.width * 0.7 + i * 30;
      }

      _attackSprite.x = target.x + 50;
      _attackSprite.y = target.y - 50;
      this.stage.addChild(_attackSprite);
      return _attackSprite;
    },

    showBattleResult() {
      this.resultDialogVisiable = true;
    },

    backToDungeon() {
      this.resultDialogVisiable = false;
      this.$parent.backDungeonHome();
    },
  },
};
</script>

<style scoped>
.battle-container {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-image: url("../../asset/ui/twinsfight-bg.png");
  background-size: 120% 120%;
  background-position: 50% 50%;
  z-index: 10;
}

.battle-title {
  width: 360px;
  height: 140px;
  margin: 0 auto;
  background-image: url("../../asset/ui/war-title.png");
  background-size: 100% 100%;
  line-height: 160px;
  font-size: 30px;
  font-size: 40px;
  text-shadow: -0.0255em -0.0255em 0 #000, 0.0255em -0.0255em 0 #000,
    -0.0255em 0.0255em 0 #000, 0.0255em 0.0255em 0 #000;
  color: #fff;
}

.avatar-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.avatar-container > div {
  width: 40%;
  height: 80px;
  background-size: contain;
  background-repeat: no-repeat;
  position: relative;
}

.avatar-container .avatar-left {
  background-image: url("../../asset/ui/avatar_left.png");
}

.avatar-container .avatar-left .avatar {
  position: absolute;
  width: 30%;
  top: 50%;
  left: 5%;
  transform: translate(0, -50%);
}

.avatar-container .attr-box {
  justify-content: space-between;
  width: 45%;
  position: absolute;
  top: 105%;
}

.avatar-container .attr-box > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 0 10% 0 5%;
  background-image: url("../../asset/ui/inner-tab.png");
  background-size: 100% 100%;
  margin-bottom: 6px;
}

.avatar-container .attr-box > div img {
  height: 35px;
}

.avatar-container .attr-box > div p {
  line-height: 30px;
  font-size: 16px;
  font-weight: 500;
  text-shadow: -0.0555em -0.0555em 0 #000, 0.0555em -0.0555em 0 #000,
    -0.0555em 0.0555em 0 #000, 0.0555em 0.0555em 0 #000;
  color: #fbfaf9;
}

.avatar-container .avatar-right .avatar {
  position: absolute;
  width: 40%;
  top: 50%;
  left: 60%;
  transform: translate(0, -50%);
}

.avatar-container .avatar-left .attr-box {
  left: 0%;
}

.avatar-container .avatar-right .attr-box {
  right: 0%;
}

.avatar-container .avatar-right {
  background-image: url("../../asset/ui/avatar_right.png");
  background-position: right;
}

.avatar-container img {
  transform: scale(0.7);
}

.avatar-container .avatar-left .name {
  position: absolute;
  left: 40%;
  top: 12%;
  font-size: 22px;
  font-weight: 500;
  text-shadow: -0.0455em -0.0455em 0 #000, 0.04555em -0.0455em 0 #000,
    -0.0455em 0.0455em 0 #000, 0.0455em 0.0455em 0 #000;
  color: #fff;
}

.avatar-container .avatar-right .name {
  position: absolute;
  right: 40%;
  top: 12%;
  font-size: 22px;
  font-weight: 500;
  text-shadow: -0.0455em -0.0455em 0 #000, 0.04555em -0.0455em 0 #000,
    -0.0455em 0.0455em 0 #000, 0.0455em 0.0455em 0 #000;
  color: #fff;
}

.over-button {
  width: 80px;
  height: 80px;
  background-image: url("../../asset/ui/round.png");
  box-sizing: border-box;
  padding: 4% 0 0;
  background-size: 100% 100%;
  position: absolute;
  right: 0;
  bottom: 10%;
  transform: translate(0, 0);
  cursor: pointer;
  font-weight: 500;
  color: #fff;
  text-shadow: -0.0335em -0.0335em 0 #fcf8ca, 0.0335em -0.0335em #000,
    -0.0335em 0.0335em 0 #000, 0.0335em 0.0335em 0 #000;
  z-index: 1;
}

.over-button:hover {
  text-shadow: -0.0335em -0.0335em 0 #000, 0.0335em -0.0335em #000,
    -0.0335em 0.0335em 0 #000, 0.0335em 0.0335em 0 #000;
  cursor: pointer;
}

.info-container {
  width: 108%;
  height: 26vh;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  background-image: url("../../asset/ui/cwsx_img.png");
  background-size: 100% 112%;
  background-position: 55% 0%;
  box-sizing: border-box;
  padding: 6% 10% 4%;
  display: flex;
  justify-content: space-around;
}

.info-container .bnx-info,
.info-container .dragon-info {
  width: 47%;
  height: 96%;
  background-color: #63636c;
  border-radius: 10px;
  margin-top: 2%;
  box-sizing: border-box;
  padding: 2%;
}

.info-container .hero-title {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.info-container .hero-title img {
  height: 40px;
}

.info-container .hero-title p {
  font-size: 28px;
  color: cornsilk;
  line-height: 40px;
}

.info-container .hero-attribute-value {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
}

.info-container .hero-attribute-value .value-item {
  width: 32%;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  background-image: url("../../asset/ui/tip-barbg.png");
  background-size: 100% 100%;
  padding: 3px 10px 6px 4px;
  font-size: 22px;
  color: #ffffff;
  text-shadow: -0.03em -0.03em 0 #000, 0.03em -0.03em 0 #000,
    -0.03em 0.03em 0 #000, 0.03em 0.03em 0 #000;
}

.info-container .hero-attribute-value .value-item img {
  height: 26px;
}

.info-container .attributes-list {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}

.info-container .attributes-list svg {
  width: 26px;
  height: 26px;
}

.info-container .attributes-list > div {
  width: 47%;
  display: flex;
  justify-content: space-between;
  height: 26px;
  align-items: center;
  align-content: center;
  color: rgba(255, 255, 255, 0.8);

  background-size: 100% 100%;
  box-sizing: border-box;
  padding: 0 5%;
  align-items: center;
  font-size: 20px;
  margin-bottom: 10px;
}

.info-container .dragon-attrs > div {
  box-sizing: border-box;
  padding: 0 8%;
  background-image: url("../../asset/ui/bg_red.png");
  opacity: 0.9;
}

.info-container .cyberDragon-attrs > div {
  background-image: url("../../asset/ui/bg_blue.png");
  opacity: 0.9;
  padding: 0 8%;
}

.result-container {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.45);
  z-index: 9;
}

.result-container .result-box {
  width: 100%;
  height: 50%;
  background-image: url("../../asset/ui/battleresult.png");
  background-size: 100% 100%;
  box-sizing: border-box;
  padding: 20% 0 30%;
}

.result-container .result-box .result-title {
  position: absolute;
  left: 50%;
  top: 10%;
  transform: translate(-50%, 0);
  background-size: 100% 100%;
  font-size: 45px;
  font-weight: 500;
  background-image: linear-gradient(to top, #ffcc5e, rgb(250, 125, 75));
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

.result-container .result-box .result-list {
  display: flex;
  top: 35%;
}

.result-container .result-box .result-list .result-item {
  width: 100px;
  height: 100px;
  margin: 20px;
  background-image: url("../../asset/ui/reward.png");
  background-size: 100% 100%;
  position: relative;
}

.result-container .result-box .result-list .result-item img {
  width: 70%;
}

.result-container .result-box .result-list .result-item p {
  font-weight: 400;
  color: #fdc54e;
  font-size: 26px;
  text-shadow: -0.04555em -0.04555em 0 #000, 0.04555em -0.04555em #000,
    -0.04555em 0.04555em 0 #000, 0.04555em 0.04555em 0 #000;
  position: absolute;
  bottom: 5%;
  right: 5%;
}

.result-container .result-box .confirm-btn {
  position: absolute;
  bottom: 43%;
  left: 50%;
  transform: translate(-50%, 50%);
  background-image: url("../../asset/ui/common_btn_confirm.png");
  background-size: 100% 100%;
  padding: 10px 20px;
  cursor: pointer;
  color: #fff;
  font-size: 27px;
  font-weight: 400;
  text-shadow: -0.04555em -0.04555em 0 #000, 0.04555em -0.04555em #000,
    -0.04555em 0.04555em 0 #000, 0.04555em 0.04555em 0 #000;
}
</style>


