<template>
  <div
    id="training-container"
    :class="{ isTopZindex: GameViewVisiable || Game1ViewVisiable }"
  >
    <div class="map-container" ref="wrapper">
      <div class="map-view">
        <!-- :style="{ height: mapHeight + '%', width: mapWidth + '%' }" -->
        <div
          class="train-box"
          v-for="(item, key) in trainData"
          :key="key"
          :class="{
            'normal-train': key === 'normal',
            'wood-train': key === 'wood',
            'wind-train': key === 'wind',
            'fire-train': key === 'fire',
            'water-train': key === 'water',
          }"
        >
          <div>
            <div class="train-name">{{ item.name }}</div>

            <div class="train-version">
              <span
                class="template-btn3"
                @click="(trainType = key), (Game1ViewVisiable = true)"
                >V1</span
              >
              <span
                class="template-btn4"
                @click="(trainType = key), (GameViewVisiable = true)"
                >V2</span
              >
            </div>
          </div>

          <div class="dialog-box" v-if="false">
            <div class="element-box">
              <img
                v-for="element in item.element"
                :key="element"
                :src="'../../asset/ui/element/element-' + element + '.png'"
                alt
              />
            </div>
            <div class="level-limit">
              <span>Lv.{{ item.level }}</span>
            </div>
            <div class="element-limit" v-if="item.limit">
              <div v-for="(value, title) in item.limit" :key="title">
                <div>{{ title }}</div>
                <div>{{ value }}</div>
              </div>
            </div>

            <div class="coin-box">
              <div>
                <img
                  src="../../asset/ui/dg.png"
                  alt
                  class="coin"
                  v-if="item.token.indexOf('dg') !== -1"
                />
                <img
                  src="../../asset/ui/dk.png"
                  alt
                  class="coin"
                  v-if="item.token.indexOf('dk') !== -1"
                />
              </div>
            </div>

            <div class="enter-btn" v-if="item.open">
              {{ $t("training.enter") }}
            </div>

            <div class="unOpen" v-if="!item.open">Coming Soon</div>
          </div>
        </div>
      </div>
    </div>
    <GameView v-if="GameViewVisiable" :trainType="trainType"></GameView>
    <Game1View v-if="Game1ViewVisiable" :trainType="trainType"></Game1View>
  </div>
</template>

<script>
import GameView from "./Game.vue";
import Game1View from "./Game1.vue";

const Config = require("../../data/config.json");
export default {
  name: "Training",
  components: {
    GameView,
    Game1View,
  },
  data() {
    return {
      GameViewVisiable: false,
      Game1ViewVisiable: false,

      positionX: 0,
      positionY: 0,
      isDown: false,
      mapWidth: 100,
      mapHeight: 100,
      trainData: Config["train"],
      trainType: null,
      trainInfo: null,
    };
  },
  mounted() {
    this.$store.dispatch("ASET_WALLETVISIABLE", false);
  },
  methods: {
    closeGameView() {
      this.GameViewVisiable = false;
      this.Game1ViewVisiable = false;
    },
  },
};
</script>

<style scoped>
.isTopZindex {
  z-index: 999 !important;
}

#training-container {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: #000;
  background-size: 100% 100%;
  z-index: 2;
}

#training-container .train-list {
  width: 100%;
  height: 70%;
  /* background-color: rgba(173, 66, 66, 0.383); */
  flex-wrap: wrap;
  position: relative;
  margin-top: 10%;
  display: flex;
}

.map-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;

  -webkit-overflow-scrolling: touch;
  position: relative;
  background-image: url("../../asset/ui/world-bg.png");
  background-size: 100% 100%;
  /* background-color: #fff; */
}

.worldMapBg {
  /* height:50%; */
  position: absolute;
  height: 100%;
  left: 0;
}

#training-container .train-list > div {
  width: 48%;
  height: 180px;
  margin: 0 auto;
  margin-bottom: 10px;
  background-image: url("../../asset/ui/train_bg_img.png");
  background-size: 100% 100%;
}

#training-container .train-list img {
  width: 250px;
}

#training-container .train-list .train1 {
  top: 100%;
  left: 50%;
  transform: translate(-50%, -80%) scale(1.2);
}

#training-container .train-list .train2 {
  top: 70%;
  left: 100%;
  transform: translate(-60%, -50%) scale(0.9);
}

#training-container .train-list .train3 {
  top: 45%;
  left: 100%;
  transform: translate(-80%, -50%) scale(0.7);
}

#training-container .train-list .train4 {
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.5);
}

#training-container .train-list .train5 {
  top: 45%;
  left: 0;
  transform: translate(-20%, -50%) scale(0.7);
}

#training-container .train-list .train6 {
  top: 70%;
  left: 0;
  transform: translate(-40%, -50%) scale(0.9);
}

.map-view {
  width: 100%;
  height: 96%;
  background-image: url("../../asset/ui/world-bg-content.png");
  background-size: 100% 100%;
  position: absolute;

  background-repeat: no-repeat;
  /* background-color: aqua; */
}

.map-view > div {
  position: absolute;
}

.map-view .train-name {
  font-size: 22px;
  background-image: url("../../asset/ui/common-title-bg.png");
  background-size: 100% 100%;
  font-weight: 500;
  padding: 8px 30px;
  /* display: flex; */
  min-width: 180px;
  align-items: center;
  justify-content: space-around;
  color: #ffffff;
  text-shadow: -0.0555em -0.0555em 0 #000, 0.0555em -0.0555em 0 #000,
    -0.0555em 0.0555em 0 #000, 0.0555em 0.0555em 0 #000;
}
.map-view .train-version {
  margin-top: 5px;
}
.map-view .train-version span {
  display: inline-block;
  margin-left: 10px;
  margin-right: 10px;
}

.map-view .dialog-box {
  width: 270px;
  padding-bottom: 60px;
  background-image: url("../../asset/ui/dialogue.png");
  background-size: 100% 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -100%);
}

.map-view .dialog-box .element-box {
  display: flex;
  margin-left: 20px;
}

.map-view .dialog-box .element-box img {
  height: 23px;
  padding: 4px;
}
.map-view .dialog-box .level-limit {
  position: absolute;
  right: 0;
  top: 0;
  transform: translate(20%, 0);
  width: 40px;
  height: 40px;
  background-image: url("../../asset/ui/lv-icon.png");
  background-size: 100% 100%;
  line-height: 40px;
  font-size: 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 500;
  color: #fff;
  text-shadow: -0.0255em -0.0255em 0 #000, 0.0255em -0.0255em 0 #000,
    -0.0255em 0.0255em 0 #000, 0.0255em 0.0555em 0 #000;
}

.map-view .dialog-box .element-limit {
  width: 94%;
  margin: 5% auto 0;
  /* background-color: #b4b0a6; */
  /* border-radius: 0.4rem; */
  display: flex;
  justify-content: space-around;
}

.map-view .dialog-box .element-limit > div {
  width: 45%;
  line-height: 20px;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 0 3%;
  background-color: rgba(250, 223, 104, 0.8);
  border-radius: 4px;
  background-size: 100% 100%;
  font-size: 12px;
}

.map-view .dialog-box .coin-box {
  width: 88%;
  height: 32px;
  box-sizing: border-box;
  padding: 2% 5%;
  padding-left: 60%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  margin-top: 0.2rem;
  background-color: rgba(97, 97, 97, 0.5);
  border-radius: 010px;
  background-size: 100% 100%;
  align-items: center;
  flex-direction: row-reverse;
}

.map-view .dialog-box .coin-box img {
  height: 26px;
}

.map-view .dialog-box .enter-btn {
  width: 40%;
  margin: 4px auto 0;
  line-height: 45px;
  background-image: url("../../asset/ui/btn1.png");
  background-size: 100% 100%;
  font-size: 17px;
  font-weight: 500;
  text-shadow: -0.0555em -0.0555em 0 #000, 0.0555em -0.0555em 0 #000,
    -0.0555em 0.0555em 0 #000, 0.0555em 0.0555em 0 #000;
  color: #fff;
  cursor: pointer;
}

/* .map-view .dialog-box .unOpen {
  
} */
.map-view .train-box {
}

.map-view .normal-train {
  right: 2%;
  bottom: 10%;
  padding: 5%;
}

.map-view .wood-train {
  right: 30%;
  bottom: 32%;
  padding: 5%;
}

.map-view .wind-train {
  left: 5%;
  top: 17%;
  padding: 5%;
}
.map-view .fire-train {
  right: 37%;
  top: 39%;
  padding: 5%;
}
.map-view .water-train {
  right: 0%;
  top: 6%;
  padding: 5%;
}

.map-view div:nth-child(6) {
  left: 56.5%;
  top: 76%;
}

.map-view div:nth-child(7) {
  left: 75.5%;
  top: 64%;
}

.map-view div:nth-child(8) {
  left: 73.5%;
  top: 34%;
}
/*

.map-view div:nth-child(6) {
  left: 65%;
  top: 16%;
}

.map-view div:nth-child(7) {
  left: 62%;
  top: 42%;
}

.map-view div:nth-child(8) {
  left: 27%;
  top: 18%;
} */
</style>


