<template>
  <div
    id="game-container"
    class="game-container"
    ref="gameContainer"
    :style="{
      backgroundImage:
        'url(' +
        require('../../asset/ui/dungeon/' + trainType + '-bg.png') +
        ')',
    }"
  >
    <img
      src="../../asset/ui/common_return.png"
      alt
      class="return"
      @click="$parent.closeGameView"
    />
    <!-- <div class="title">{{ $t("training.name." + trainType) }}</div> -->
    <div
      class="title"
      :style="{
        backgroundImage:
          'url(' +
          require('../../asset/ui/dungeon/' + trainType + '-title.png') +
          ')',
      }"
      v-if="trainType"
    >
      {{ trainData[trainType]["name"] }} V2
    </div>

    <div class="mining-rtio">Mining Ratio: {{ miningRatio }}%</div>

    <div class="detail-container" v-if="activeDragon">
      <div class="top-box">
        <div class="dragon-box">
          <img
            src="https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/attr/attr-2.png"
            alt
            class="element"
          />
          <img
            :src="
              'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/dragons/dragon' +
              activeDragon['type'] +
              '.png'
            "
            alt
            class="dragon"
          />
          <img
            :src="
              'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/attr/base-' +
              activeDragon['element'] +
              '.png'
            "
            alt
            class="base"
          />
          <p class="level">Lv.{{ activeDragon["level"] }}</p>
        </div>

        <div class="right-content">
          <div class="reward-box">
            <img src="../../asset/ui/banner-1.png" alt class="banner" />

            <div class="reward-num">
              <p>
                <img src="../../asset/ui/coin.png" alt class="coin" />
                <span>{{ activeDragon["dgReward"] }}</span
                >DG
              </p>
              <div class="blocknumber">
                <div>StartBlock</div>
                <div>{{ activeDragon["blockNumber"] }}</div>
              </div>
              <div class="blocknumber">
                <div>ClaimBlock</div>
                <div>{{ activeDragon["claimBlock"] }}</div>
              </div>
            </div>

            <div class="buttons">
              <div class="button-receive" @click="claimDragonReward">
                {{ $t("common.claim") }}
                <i
                  class="el-icon-loading"
                  v-if="loadings['claimDragonReward']"
                ></i>
              </div>
              <div class="button-remove" @click="quitTrain">
                {{ $t("training.quit") }}
                <i
                  class="el-icon-loading"
                  v-if="loadings['quitTrain' + activeDragon.id]"
                ></i>
              </div>
              <!-- <p class="tip">Coming Soon</p> -->
            </div>
          </div>
        </div>
      </div>

      <div class="elementibutes-list">
        <div class="element-item">
          <div>
            <svg
              t="1634636781869"
              class="icon"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="3413"
              width="200"
              height="200"
            >
              <path
                d="M138.922667 680.561778C110.08 414.72 275.797333 155.249778 284.444444 133.006222 293.148444 110.762667 409.258667 10.126222 516.551111 48.924444c107.292444 38.570667 99.555556 118.897778 69.290667 155.818667-27.022222 75.150222-134.826667 52.053333-161.848889 34.702222-27.022222-17.351111-38.513778 15.36-38.513778 15.36 31.288889 118.101333 25.827556 240.071111 20.821333 295.936-0.170667 0-79.36 50.346667-87.267555 100.693334 0 0 6.257778-18.773333 83.683555-69.290667 109.795556-71.338667 219.648 32.881778 219.648 32.881778 28.842667-38.513778 106.211556-13.425778 106.211556-13.425778l0.967111 1.820444 1.137778-10.979555h123.164444c15.758222 0 25.770667 16.725333 18.488889 30.663111l-186.311111 349.809778a20.878222 20.878222 0 0 1-18.432 10.979555h-110.933333c6.542222-4.380444 12.686222-9.500444 18.773333-14.563555-33.848889-6.257778-54.385778-18.090667-67.242667-29.582222 44.657778-31.516444 48.526222-72.590222 48.526223-72.590223s-34.816 39.139556-69.063111 42.666667c-221.582222 9.671111-240.753778-100.238222-240.753778-100.238222s-17.351111 23.096889-92.501334-11.605334c-75.207111-34.702222-15.530667-117.418667-15.530666-117.418666z"
                p-id="3414"
                fill="#4794E4"
              />
            </svg>
            <span v-text="$t('dragon.strength')"></span>
          </div>
          <div class="value">{{ activeDragon["strength"] }}</div>
        </div>

        <div class="element-item">
