          </div>

          <div class="value">{{ activeDragon["spirit"] }}</div>
        </div>
      </div>
    </div>

    <div class="dragonsBase-container">
      <ul class="base-list">
        <li
          v-for="(dragon, index) in traningDragonsData"
          :key="index"
          @click="setActiveDragon(dragon)"
          :class="{ active: activeDragon === dragon }"
        >
          <img
            :src="
              'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/dragons/dragon' +
              dragon.type +
              '.png'
            "
            alt
            class="dragon"
          />
          <img
            :src="
              'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/attr/base-' +
              dragon['element'] +
              '.png'
            "
            class="base"
          />

          <div class="reward-num">
            <img src="../../asset/ui/coin.png" alt class="coin" />
            <span>{{ dragon.dgReward }}</span>
          </div>
        </li>
      </ul>
    </div>

    <div class="join-btn" @click="joinDialogVisiable = true">
      <img src="../../asset/ui/28906.png" alt class="center" />
      <p v-text="$t('training.join')"></p>
    </div>

    <div class="join-dialog center" v-if="joinDialogVisiable">
      <div class="dialog-content center">
        <img
          src="../../asset/ui/common_return.png"
          alt
          class="dialog-return"
          @click="joinDialogVisiable = false"
        />
        <div class="dialog-title">
          <p class="center">Chose Dragon</p>
        </div>
        <div class="limit-box" v-if="trainInfo">
          Limit
          <img
            v-for="(item, index) in trainInfo.element"
            :key="index"
            :src="require('../../asset/ui/element/element-' + item + '.png')"
            alt
            class="element"
          />

          <div class="attr-limit">
            <p v-for="(item, index) in trainInfo.limit" :key="index">
              {{ index }}
              <span>{{ item }}</span>
            </p>
          </div>

          <p>Lv.{{ trainInfo.level }}</p>
        </div>

        <div class="deep-content">
          <ul class="dragons-list">
            <li
              v-for="(item, index) in dragonData"
              :key="index"
              v-if="filterDragons(item)"
              @click="selectTrainDragon(item)"
            >
              <div
                class="dragon-item"
                :style="{
                  backgroundImage:
                    'url(' +
                    require('../../asset/ui/rarity/' + item.rarity + '.png') +
                    ')',
                }"
              >
                <img
                  class="elementibute"
                  :src="
                    'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/attr/attr-' +
                    item.element +
                    '.png'
                  "
                  alt
                />
                <p class="level">Lv.{{ item.level }}</p>
                <img :src="item.img" alt class="dragon center" />

                <img
                  class="quality"
                  :src="
                    'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/rarity/rarity_' +
                    item.rarity +
                    '.png'
                  "
                  alt
                />

                <p class="index">{{ $t("elements")[item.element] }}</p>
                <!-- attr -->

                <div class="attributes-list dragon-attrs">
                  <div class="attr-item">
                    <div>
                      <svg
                        t="1634636781869"
                        class="icon"
                        viewBox="0 0 1024 1024"
                        version="1.1"
                        xmlns="http://www.w3.org/2000/svg"
                        p-id="3413"
                        width="200"
                        height="200"
                      >
