                          p-id="3414"
                          fill="#4794E4"
                        />
                      </svg>
                    </div>
                    <div class="value">{{ item["strength"] }}</div>
                  </div>

                  <div class="attr-item">
                    <div>
                      <svg
                        t="1634637225040"
                        class="icon"
                        viewBox="0 0 1024 1024"
                        version="1.1"
                        xmlns="http://www.w3.org/2000/svg"
                        p-id="42850"
                        width="200"
                        height="200"
                      >
