                <!-- activeChoseDragonId ===  -->
                <div
                  class="active-gray center"
                  v-if="joinToTrainDragons.indexOf(item.id) > -1"
                >
                  <img
                    src="../../asset/ui/general_yes_flag.png"
                    alt
                    class="active center"
                  />
                </div>
              </div>
