          </ul>
        </div>
        <p class="select-num" v-if="false">
          <span>{{ joinToTrainDragons.length }}</span>
          / 50
        </p>
        <div class="buttons">
          <div
            class="template-btn4"
            v-if="false"
            @click="batchToJoinDragons"
            v-show="joinToTrainDragons.length < dragonData.length"
          >
            Batch Select
          </div>
          <div
            v-if="false"
            class="template-btn4"
            @click="joinToTrainDragons = []"
            v-show="joinToTrainDragons.length === dragonData.length"
          >
            Cancel selection
          </div>
          <div class="template-btn2" @click="joinToDragonTrain">
            Join
            <i
              class="el-icon-loading"
              v-show="loadings['joinToDragonTrain']"
            ></i>
          </div>
        </div>
      </div>

      <div class="tip-btn" v-if="0" @click="tipDialogVisiable = true">
        <img src="../../asset/ui/tip-icon.png" alt />
        <p>Tip</p>
      </div>

      <div class="tip-dialog" v-if="tipDialogVisiable">
        <div class="tip-dialog-content center">
          <div class="tip-title">Tips</div>
          <div class="tip-content">
            <p></p>
          </div>
        </div>
        <div class="tip-mask center" @click="tipDialogVisiable = false"></div>
      </div>
    </div>
  </div>
</template>

<script>
import DragonSprite from "../../utils/DragonSprite";
import store from "@/store/index";
const path = store.state.contractPath;
const abiJsons = require("../../contracts/deployments." + path + ".json");
const tokensJsons = require("../../contracts/tokens." + path + ".json");
const erc20AbiJosn = require("../../contracts/erc20.json");
const Config = require("../../data/config.json");
import Util from "../../utils/util";
export default {
  name: "TrackView",
  props: {
    trainType: String,
  },
  data() {
    return {
      // contract
      txs: {},
      activeChoseDragonId: null,
      tipDialogVisiable: false,
      stage: null,
      width: document.documentElement.clientWidth * 2,
      height: document.documentElement.clientHeight * 2,
      scale: 0.5,
      asset: null,
      canvas: null,
      traningDragonsData: [],

      activeDragon: null,
      loadings: {},

      dragonData: [],
      dragonAtlas: {},
      dragonSprites: {},
      spriteParticles: {},
      activeDragonIndex: 0,
      joinDialogVisiable: false,
      trainData: Config["train"],
      trainInfo: null,
      joinToTrainDragons: [],

      miningRatio: "--",
    };
  },

  mounted() {
    // if (this.width / 2 > 480) {
    //   this.width = (document.documentElement.clientHeight / 1.78) * 2;
    // }

    this.asset = this.$store.state.asset;
    this.initStage(this.$refs.gameContainer);
    this.init();
    this.trainInfo = Config["train"][this.trainType];
  },

  watch: {
    web3(val1, val2) {
      if (this.web3.web3Instance) {
        this.init();
      }
    },
  },

  computed: {
    web3() {
      return this.$store.state.web3;
    },
  },

  methods: {
    init() {
      let web3 = this.web3.web3Instance();
      let that = this;
      this.dragonAddress = abiJsons["Dragon"]["address"];
      this.dragonContract = new web3.eth.Contract(
        abiJsons["Dragon"]["abi"],
        this.dragonAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.dragonTrainAddress =
        abiJsons["DragonTrain"][this.trainType + "Address"];
      this.dragonTrainContract = new web3.eth.Contract(
        abiJsons["DragonTrain"]["abi"],
        this.dragonTrainAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.getInfo();
    },

    getInfo() {
      this.getDragonInfo();
      this.getDragonTrainInfo();
    },

    getDragonTrainInfo() {
      this.dragonTrainContract.methods
        .miningRate()
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          this.miningRatio = Number((1000 * res) / 100000).toFixed(2);
        });

      let that = this;
      this.dragonTrainContract.methods
        .getDragonNumber(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          if (!Number(res)) {
            this.$set(this, "traningDragonsData", []);
            return;
          }
          this.dragonTrainContract.methods
            .getDragons(this.web3.coinbase, 0, res)
            .call({ from: this.$store.state.contractFrom })
            .then((_res) => {
              let dragonList = [];
              var res = _res[0];
              var claimBlocks = _res[1];
              for (let i in res) {
                let _info = Util.getDragonInfo(res[i]["dragonId"]);
                that.$set(
                  _info,
                  "dgReward",
                  Number(res[i]["dgReward"] / Math.pow(10, 18)).toFixed(2)
                );
                that.$set(
                  _info,
                  "dkReward",
                  res[i]["dkReward"] / Math.pow(10, 18)
                );
                that.$set(_info, "blockNumber", res[i]["blockNumber"]);

                that.$set(_info, "claimBlock", claimBlocks[i]);
                this.getDragonDetailInfo(_info);

                dragonList.push(_info);
                if (
                  this.activeDragon &&
                  this.activeDragon["id"] === _info["id"]
                ) {
                  this.activeDragon = _info;
                }
                if (!this.activeDragon && i === 0) {
                  this.activeDragon = _info;
                }
              }

              dragonList.sort(function (a, b) {
                return a.type - b.type >= 0 ? -1 : 1;
              });
              this.traningDragonsData = dragonList;
              this.renderDragonSprites();
            });
        });
    },

    setActiveDragon(dragon) {
      this.activeDragon = dragon;
    },

    batchToJoinDragons() {
      
      this.dragonData.map((dragon) => {
        if (
          this.joinToTrainDragons.indexOf(dragon.id) === -1 &&
          this.joinToTrainDragons.length < 1
        ) {
          this.joinToTrainDragons.push(dragon.id);
        }
      });
    },

    selectTrainDragon(dragon) {
      if (this.joinToTrainDragons.indexOf(dragon.id) > -1) {
        this.joinToTrainDragons.splice(
          this.joinToTrainDragons.indexOf(dragon.id),
          1
        );
      } else {
        if (this.joinToTrainDragons.length < 50) {
          this.joinToTrainDragons.push(dragon.id);
        } else {
          // this.$message({
          //   message: "Added dragon has reached the limit",
          //   type: "info",
          //   center: true
          // });
        }
      }
    },

    joinToDragonTrain() {
      
      if (!this.joinToTrainDragons.length) {
        this.$message({
          message: "Please select the dragon to join the training",
          type: "info",
          center: true,
        });
        return;
      }
      let that = this;
      let _ids = this.joinToTrainDragons;
      let _loading = "joinToDragonTrain";
      if (that.loadings[_loading]) {
        return;
      }
      that.$set(that.loadings, _loading, true);
      this.dragonContract.methods
        .safeBatchTransferFrom(
          this.web3.coinbase,
          that.dragonTrainAddress,
          _ids,
          "0x01"
        )
        .estimateGas({ from: this.web3.coinbase }, function (err, gas) {
          
          if (err) {
            that.$set(that.loadings, _loading, false);
            that.$message({
              message: that.$t("common.failed") + Util.errMsg(err),
              type: "warning",
              center: true,
            });
            return;
          }
          that.dragonContract.methods
            .safeBatchTransferFrom(
              that.web3.coinbase,
              that.dragonTrainAddress,
              _ids,
              "0x01"
            )
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {
              
            })
            .on("receipt", function (receipt) {
              
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;
              that.$set(that.loadings, _loading, false);

              that.$message({
                message: that.$t("common.success"),
                type: "success",
                center: true,
              });
              that.joinToTrainDragons = [];
              that.joinDialogVisiable = false;

              that.getInfo();
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              
            })
            .on("error", function (err) {
              that.$message({
                message: Util.errMsg(err),
                type: "error",
                center: true,
              });
              that.$set(that.loadings, _loading, false);
              
              that.$message({
                message:
                  that.$t("dragon.join") +
                  that.$t("common.failed") +
                  Util.errMsg(err),
                type: "warning",
                center: true,
              });
            });
        });
    },

    claimDragonReward() {
      let that = this;
      let _loading = "claimDragonReward";
      let _ids = [this.activeDragon["id"]];
      if (that.loadings[_loading]) {
        return;
      }
      that.$set(that.loadings, _loading, true);

      this.dragonTrainContract.methods
        .claim(_ids)
        .estimateGas({ from: that.web3.coinbase }, function (err, gas) {
          if (err) {
            that.$set(that.loadings, _loading, false);
            that.$message({
              message:
                that.$t("common.claim") +
                that.$t("common.failed") +
                Util.errMsg(err),
              type: "warning",
              center: true,
            });
            return;
          }
          that.dragonTrainContract.methods
            .claim(_ids)
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {
              
            })
            .on("receipt", function (receipt) {
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;

              that.$set(that.loadings, _loading, false);
              that.getInfo();
              that.$message({
                message: that.$t("common.receive") + that.$t("common.success"),
                type: "success",
                center: true,
              });

              that.$EventBus.$emit("refreshBalance");
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              
            })
            .on("error", function (err) {
              that.$message({
                message:
                  that.$t("common.receive") +
                  that.$t("common.failed") +
                  Util.errMsg(err),
                type: "warning",
                center: true,
              });
              that.$set(that.loadings, _loading, false);
            });
        });
    },

    quitTrain() {
      let that = this;
      var id = this.activeDragon["id"];
      let _loading = "quitTrain" + id;
      let _ids = [id];
      if (that.loadings[_loading]) {
        return;
      }
      that.$set(that.loadings, _loading, true);
      this.dragonTrainContract.methods
        .quit(_ids)
        .estimateGas({ from: that.web3.coinbase }, function (err, gas) {
          if (err) {
            that.$set(that.loadings, _loading, false);
            that.$message({
              message:
                that.$t("training.quit") +
                that.$t("common.failed") +
                Util.errMsg(err),
              type: "warning",
              center: true,
            });
            return;
          }
          that.dragonTrainContract.methods
            .quit(_ids)
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {
              
            })
            .on("receipt", function (receipt) {
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.$set(that.loadings, _loading, false);

              that.txs[receipt.transactionHash] = receipt.transactionHash;
              if (that.dragonSprites[_ids[0]]) {
                that.dragonSprites[_ids[0]].removeFromParent();
                delete that.dragonSprites[_ids[0]];
              }

              that.activeDragon = null;
              that.$EventBus.$emit("refreshBalance");
              
              that.getInfo();
              that.$message({
                message: that.$t("training.quit") + that.$t("common.success"),
                type: "success",
                center: true,
              });
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              
            })
            .on("error", function (err) {
              that.$message({
                message:
                  that.$t("training.quit") +
                  that.$t("common.failed") +
                  Util.errMsg(err),
                type: "warning",
                center: true,
              });
              that.$set(that.loadings, _loading, false);
            });
        });
    },

    getDragonInfo() {
      this.dragonContract.methods
        .balanceOf(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          
          // this.myNftBalance = res;
          if (!Number(res)) {
            return;
          }
          this.dragonContract.methods
            .tokensOf(this.web3.coinbase, 0, res)
            .call({ from: this.$store.state.contractFrom })
            .then((res) => {
              let dragonList = [];
              for (let i in res) {
                let _info = Util.getDragonInfo(res[i]);
                this.getDragonDetailInfo(_info);

                dragonList.push(_info);
              }

              dragonList.sort(function (a, b) {
                return a.type - b.type >= 0 ? -1 : 1;
              });
              this.dragonData = dragonList;
            });
        });
    },

    getDragonDetailInfo(info) {
      let that = this;
      this.dragonContract.methods
        .getDragonInfo(info.id)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          let _power = 0;
          for (let key in res) {
            if (!Number(key)) {
              _power = _power + Number(res[key]);
            }
            that.$set(info, key == "spririt" ? "spirit" : key, res[key]);
          }
        });
    },

    initStage(container) {
      this.stage = new Hilo.Stage({
        renderType: "canvas",
        width: this.$refs["gameContainer"]["offsetWidth"] * 2,
        height: this.$refs["gameContainer"]["offsetHeight"] * 2,
        scaleX: this.scale,
        scaleY: this.scale,
        container: container,
      });
      this.stage.canvas.style.left = 0;
      this.stage.canvas.style.top = 0;
      this.stage.enableDOMEvent([
        Hilo.event.POINTER_START,
        Hilo.event.POINTER_MOVE,
        Hilo.event.POINTER_END,
      ]);
      this.ticker = new Hilo.Ticker(60);
      this.ticker.addTick(this.stage);
      this.ticker.addTick(Hilo.Tween);
      this.ticker.start(true);
      this.renderTrack();
    },

    renderTrack() {
      let trackImg = this.asset.track;
      this.track = new Hilo.Bitmap({
        id: "track",
        image: trackImg,
      });

      this.track.width = this.stage.width;

      // this.track.height = this.track.height / (_width / this.track.width);

      this.track.height = this.stage.height * 0.6;
      this.track.y = (this.height - this.track.height) * 0.45;
      // if (this.height > 1500) {
      //   this.track.height = this.height * 0.75;
      //   this.track.y = (this.height - this.track.height) * 0.35;
      // }

      // if (this.width > 1400) {
      //   let _width = (document.documentElement.clientHeight / 1.78) * 2;
      //   this.track.width = _width * 0.95;
      //   this.track.height =
      //     (this.track.height / (_width / this.track.width)) * 0.82;
      //   this.track.y = (this.height - this.track.height) * 0.5;
      // }
      this.track.pivotX = this.track.width / 2;
      this.track.x = this.stage.width / 2;
      this.stage.addChild(this.track);

      var flag_finish = (this.flag_finish = new Hilo.Bitmap({
        id: "flag_finish",
        image: this.asset.btn,
        rect: [512, 953, 162, 150],
        width: 163,
        height: 150,
      }));

      flag_finish.x = this.track.x + this.track.width * 0.88;
      flag_finish.y = this.track.y + this.track.height * 0.31924;

      flag_finish.pivotX = flag_finish.width / 2;
      flag_finish.pivotY = flag_finish.height / 2;

      this.stage.addChild(flag_finish);
    },

    renderDragonSprites() {
      let that = this;
      var dragons = [];
      var t = 400;
      this.traningDragonsData.map((dragon, index) => {
        if (index < 20) {
          setTimeout(
            function () {
              this.run(dragon.id, dragon.type);
            }.bind(this),
            200 * parseInt(index)
          );
        }
      });
    },

    run(dragonId, dragonType) {
      if (!this.dragonAtlas[dragonType]) {
        this.dragonAtlas[dragonType] = this.asset.getDragonAtlas(
          Number(dragonType)
        );
      }
      if (this.dragonSprites[dragonId]) {
        return;
      }

      let _dragonSprite = new DragonSprite({
        id: "sprite_" + dragonId,
        atlas: this.dragonAtlas[dragonType],
        level: dragonType,
        startX: 0,
        startY: 0,
        trackWidth: this.track.width,
        trackHeight: this.track.height,
        speedFactor: 1,
        track: this.track,
      });

      this.$set(this.dragonSprites, dragonId, _dragonSprite);

      _dragonSprite.on(
        "gold",
        function (e) {
          if (this.spriteParticles[dragonId]) {
            this.spriteParticles[dragonId].start();
          }
        }.bind(this)
      );

      this.stage.addChildAt(_dragonSprite);
      
      this._renderParticle(dragonId);
    },

    _renderParticle(dragonId) {
      var particleSystem = this.stage.getChildById(
        "dragon_sprite_particle" + dragonId
      );
      if (particleSystem != null) {
        particleSystem.removeFromParent();
      }

      particleSystem = new Hilo.ParticleSystem({
        x: this.track.x + this.track.width - 80,
        y: this.track.y + this.track.height / 4,
        emitTime: 0.01,
        emitNum: 20,
        totalTime: 0.01,
        id: "star",
        gy: 100,
        gyVar: 20,
        particle: {
          life: 1,
          alphaV: -0.01,
          vxVar: 100,
          vyVar: 100,
          axVar: 200,
          ayVar: 200,
          scale: 0.4,
          pivotX: 0.5,
          pivotY: 0.5,
          frame: [[0, 0, 60, 60]],
          image: this.asset.coin,
        },
      });
      this.stage.addChild(particleSystem);
      this.spriteParticles[dragonId] = particleSystem;
    },

    filterDragons(dragon) {
      let _elementLimit = this.trainInfo.limit;
      let isShow = true;
      if (this.trainInfo.element.indexOf(dragon.element) === -1) {
        isShow = false;
      } else {
        if (this.trainInfo.level > dragon.level) {
          isShow = false;
        } else {
          for (let key in _elementLimit) {
            if (dragon[key.toLowerCase()] < _elementLimit[key]) {
              isShow = false;
            }
          }
        }
      }
      return isShow;
    },
  },
};
</script>

