  li
  .dragon-item
  .attributes-list
  .attr-item
  svg {
  width: 22px;
  height: 22px;
}

.join-dialog .dialog-content .select-num {
  line-height: 26px;
  font-size: 18px;
  font-weight: 600;
  color: #fff;
  text-shadow: -0.0455em -0.0455em 0 #000, 0.0455em -0.0455em 0 #000,
    -0.0455em 0.0455em 0 #000, 0.0455em 0.0455em 0 #000;
}

.join-dialog .dialog-content .select-num span {
  color: rgb(113, 238, 113);
}

.join-dialog .dialog-content .buttons {
  margin-top: 26px;
  display: flex;
  justify-content: space-around;
}

.join-dialog .dialog-content .buttons > div {
  margin: 0 auto;
  padding: 3% 0;
  width: 30%;
  white-space: nowrap;
}

.mining-rtio {
  font-size: 12px;
  line-height: 24px;
  padding: 0.1rem 0;
  width: 30%;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  border-radius: 0.8rem;
  border: 1px solid #fff;
  position: absolute;
  top: 66%;
  left: 50%;
  z-index: 5;
  transform: translate(-50%, 0);
}
</style>



