<template>
  <div id="container">
    <div class="detail-container">
      <div class="detail-content">
        <div class="dragon-box">
          <div class="rebirth-btn" @click="rebirthDialogVisiable = true">
            <img src="../../asset/ui/rebirth-icon.png" alt />
            <p>Rebirth</p>
          </div>

          <div
            class="trade-btn"
            @click="transferDialogVisable = true"
            v-if="activeDragonId"
          >
            <img src="../../asset/ui/trade-icon.png" alt />
            <p>{{ $t("common.transfer") }}</p>
          </div>

          <div class="breakthrough-btn" v-if="false">
            <img src="../../asset/ui/breakthrough-icon.png" alt />
            <p>{{ $t("dragon.breakthrough") }}</p>
          </div>

          <div class="dragon-title">
            <img
              :src="
                '../../asset/ui/element/element-' +
                dragonData[activeDragonId]['element'] +
                '.png'
              "
              alt
              v-if="dragonData[activeDragonId]"
            />
            <p v-if="dragonData[activeDragonId]">
              {{
                $t("dragonElement")[dragonData[activeDragonId]["element"] - 1]
              }}
            </p>
          </div>

          <div class="dragon-pos">
            <img
              :src="dragonData[activeDragonId].img"
              alt
              class="dragon"
              v-if="dragonData[activeDragonId]"
            />
            <img src="../../asset/ui/base2.png" alt class="base" />

            <div class="quality-bar">
              <img
                class="quality"
                :src="
                  'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/rarity/rarity_' +
                  dragonData[activeDragonId]['rarity'] +
                  '.png'
                "
                alt
                v-if="dragonData[activeDragonId]"
              />
            </div>
          </div>
        </div>

        <div class="information-box" v-show="activeInfoTab === 'info'">
          <div class="info-attr-container">
            <div class="info-title">
              <span>{{ $t("dragon.attributes") }}</span>
              <img
                src="../../asset/ui/info.png"
                alt
                @click="detailDialogVisiable = true"
              />
            </div>

            <div class="attributes-list" v-if="dragonData[activeDragonId]">
              <div class="attr-item">
