                      p-id="42851"
                      fill="#9B63DE"
                    />
                  </svg>
                  <span v-text="$t('dragon.agility')"></span>
                </div>
                <div class="value">
                  {{ dragonData[activeDragonId]["agility"] }}
                </div>
              </div>
              <div class="attr-item">
                <div>
                  <svg
                    t="1634637399662"
                    class="icon"
                    viewBox="0 0 1024 1024"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    p-id="48099"
                    width="200"
