                  >
                    <path
                      d="M592.7 575.1h-38.1c-1.1 0-2 0.9-2 2v38.1c0 21.6-16.5 40-38 41.5-23.7 1.6-43.7-17.3-43.7-40.8v-38.8c0-1.1-0.9-2-2-2h-38.1c-21.6 0-40-16.5-41.5-38-1.6-23.7 17.3-43.7 40.8-43.7h38.8c1.1 0 2-0.9 2-2v-38.1c0-21.6 16.5-40 38-41.5 23.7-1.6 43.7 17.4 43.7 40.8v38.8c0 1.1 0.9 2 2 2h38.8c23.4 0 42.4 20 40.8 43.7-1.5 21.6-19.9 38-41.5 38z"
