                      fill="#FF4C24"
                      p-id="48101"
                    />
                  </svg>
                  <span v-text="$t('dragon.constitution')"></span>
                </div>
                <div class="value">
                  {{ dragonData[activeDragonId]["constitution"] }}
                </div>
              </div>

              <div class="attr-item">
                <div>
                  <svg
                    t="1634637485382"
                    class="icon"
                    viewBox="0 0 1024 1024"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    p-id="51199"
