                  {{ dragonData[activeDragonId]["spirit"] }}
                </div>
              </div>
            </div>
          </div>

          <div class="level" v-if="dragonData[activeDragonId]">
            <div>
              <img src="../../asset/ui/info.png" alt class="info" />
              <span>Lv.{{ dragonData[activeDragonId]["level"] }}</span>
            </div>
            <div class="template-btn2" @click="toUpgradeDragonLevel">
              {{ $t("common.upgrade") }}
            </div>
          </div>

          <div class="power-value" v-if="dragonData[activeDragonId]">
            <div>
              <img src="../../asset/ui/power.png" alt />
              {{ $t("dragon.power") }}
            </div>
            <span>{{ dragonData[activeDragonId]["powerValue"] }}</span>
          </div>
        </div>

        <div class="equipment-box" v-show="activeInfoTab === 'equip'">
          <div class="info-title">
            <span>{{ $t("dragon.equipment") }}</span>
          </div>

          <div class="equipment-content">
            <img src="../../asset/ui/3003.png" alt class="center" />

            <div
              class="equipment-item"
              @click="
                equipmentDialogVisiable = true;
                equipmentType = 'gemstone';
              "
            >
              <img
                src="https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/gemstone/6.png"
                alt
                class="gray-icon center"
              />
            </div>
            <div
              class="equipment-item"
              @click="
                equipmentDialogVisiable = true;
                equipmentType = 'medal';
              "
            >
              <img
                src="https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/medal/6.png"
                alt
                class="gray-icon center"
              />
            </div>
            <div
              class="equipment-item"
              @click="
                equipmentDialogVisiable = true;
                equipmentType = 'rune';
              "
            >
              <img
                src="https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/rune/6.png"
                alt
                class="gray-icon center"
              />
            </div>
            <div
              class="equipment-item"
              @click="
                equipmentDialogVisiable = true;
                equipmentType = 'arms';
              "
            >
              <img
                src="https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/arms/1.png"
                alt
                class="gray-icon center"
              />
            </div>
          </div>
        </div>
      </div>
      <div class="switch-bar">
        <div
          :class="{ active: activeInfoTab === 'info' }"
          @click="activeInfoTab = 'info'"
        >
          <p class="center">{{ $t("dragon.dragon") }}</p>
        </div>
        <div
          :class="{ active: activeInfoTab === 'equip' }"
          @click="activeInfoTab = 'equip'"
        >
          <p class="center">{{ $t("dragon.equipment") }}</p>
        </div>
      </div>
    </div>
    <div class="dragons-container">
      <div class="dragons-content">
        <div class="dragons-list">
          <div
            class="dragon-item"
            v-for="(item, index) in dragonData"
            :key="index"
            v-show="activeAttr === item['element'] || activeAttr === 0"
          >
            <!-- {{item}} -->
            <div
              class="item-inner"
              :style="{
                backgroundImage:
                  'url(https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/rarity/' +
                  item.rarity +
                  '.png)',
              }"
            >
              <div
                :class="{ active: activeDragonId === item.id }"
                class="active-box"
                @click="activeDragonId = item.id"
              >
                <img
                  class="attribute"
                  :src="
                    require('../../asset/ui/element/element-' +
                      item.element +
                      '.png')
                  "
                  alt
                />
                <p class="level">Lv.{{ item.level }}</p>
                <img :src="item.img" alt class="dragon-icon" />

                <img
                  class="quality"
                  :src="
                    'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/rarity/rarity_' +
                    item.rarity +
                    '.png'
                  "
                  alt
                />

                <p class="index">{{ $t("elements")[item.element] }}</p>
              </div>
            </div>
          </div>
          <div
            class="dragon-item"
            v-for="(item, key) in 4"
            :key="key"
            style="height: 0px; padding: 0; margin: 0"
          ></div>
        </div>
      </div>

      <div class="filter-nav">
        <div :class="{ active: activeAttr === 0 }" @click="activeAttr = 0">
          <img src="../../asset/ui/elements.png" class="center" alt />
        </div>
        <div :class="{ active: activeAttr === 1 }" @click="activeAttr = 1">
          <img src="../../asset/ui/element/element-1.png" class="center" alt />
        </div>
        <div :class="{ active: activeAttr === 2 }" @click="activeAttr = 2">
          <img src="../../asset/ui/element/element-2.png" class="center" alt />
        </div>
        <div :class="{ active: activeAttr === 3 }" @click="activeAttr = 3">
          <img src="../../asset/ui/element/element-3.png" class="center" alt />
        </div>
        <div :class="{ active: activeAttr === 4 }" @click="activeAttr = 4">
          <img src="../../asset/ui/element/element-4.png" class="center" alt />
        </div>
        <div
          v-if="false"
          :class="{ active: activeAttr === 5 }"
          @click="activeAttr = 5"
        >
          <img src="../../asset/ui/element/element-5.png" alt />
        </div>
      </div>
    </div>

    <!-- DIALOG -->
    <div class="common-dialog detail-dialog" v-show="detailDialogVisiable">
      <div class="dialog-content center">
        <div class="close" @click="detailDialogVisiable = false">
