            />
          </svg>
        </div>

        <div class="title">{{ $t("dragon.propertyDetails") }}</div>

        <div class="detail-content" v-if="dragonData[activeDragonId]">
          <div class="detail-item">
            <span>{{ $t("dragon.life") }}</span>
            <span>
              {{
                5 *
                Number(dragonData[activeDragonId]["constitution"]) *
                (1 + 0.2 * Number(dragonData[activeDragonId]["level"]))
              }}
            </span>
          </div>

          <div class="detail-item">
            <span>{{ $t("dragon.attack") }}</span>
            <span v-show="dragonData[activeDragonId]['element'] === 1">
              {{
                Number(dragonData[activeDragonId]["strength"]) *
                (1 + 0.2 * (Number(dragonData[activeDragonId]["level"]) - 1))
              }}
            </span>
            <span v-show="dragonData[activeDragonId]['element'] === 2">
              {{
                parseInt(
                  Number(dragonData[activeDragonId]["strength"]) *
                    (1 +
                      0.2 * (Number(dragonData[activeDragonId]["level"]) - 1))
                )
              }}
            </span>
            <span v-show="dragonData[activeDragonId]['element'] === 3">
              {{
                parseInt(
                  Number(dragonData[activeDragonId]["intelligence"]) *
                    (1 +
                      0.2 * (Number(dragonData[activeDragonId]["level"]) - 1))
                )
              }}
            </span>
            <span v-show="dragonData[activeDragonId]['element'] === 4">
              {{
                parseInt(
                  Number(dragonData[activeDragonId]["strength"]) *
                    (1 +
                      0.2 * (Number(dragonData[activeDragonId]["level"]) - 1))
                )
              }}
            </span>
            <!-- <span v-show="dragonData[activeDragonId]['element'] === 5">{{Number( dragonData[activeDragonId][""] )*(1+0.2*(Number(dragonData[activeDragonId]["level"])-1))}}</span> -->
          </div>

          <div class="detail-item">
            <span>{{ $t("dragon.defense") }}</span>
            <span>
              {{
                parseInt(
                  Number(dragonData[activeDragonId]["willpower"]) *
                    (1 +
                      0.2 * (Number(dragonData[activeDragonId]["level"]) - 1))
                )
              }}
            </span>
          </div>

          <div class="detail-item">
            <span>{{ $t("dragon.magic") }}</span>

            <span>
              {{
                parseInt(
                  Number(dragonData[activeDragonId]["intelligence"]) *
                    (1 +
                      0.2 * (Number(dragonData[activeDragonId]["level"]) - 1) +
                      0)
                )
              }}
            </span>
          </div>
        </div>
      </div>
    </div>

