                <span>{{ upgradeDgNum }}</span>
              </p>
            </div>
            <div class="dg-coin">
              DK
              <img src="../../asset/ui/dk.png" alt />
              <p>
                <span>{{ upgradeDkNum }}</span>
              </p>
            </div>
          </div>

          <div class="upgrade-button">
            <div
              class="button"
              @click="toUpgradeDragon"
              v-if="unApproveDragonUpgrade && approveCurrentToken"
            >
              {{ $t("common.upgrade") }}
              <i
                class="el-icon-loading"
                v-show="loadings['toUpgradeDragon']"
              ></i>
            </div>

            <div v-if="approveDragonUpgrade || approveCurrentToken">
              <div
                class="button"
                @click="approveDragonUpgrade"
                v-if="!unApproveDragonUpgrade"
              >
                {{ $t("common.approve") + "NFT" }}
                <i
                  class="el-icon-loading"
                  v-show="loadings['approveDragonUpgrade']"
                ></i>
              </div>

              <div
                class="button"
                @click="
                  approvalToken('DG', dragonUpgradeAddress, 'approveTokenDg')
                "
                v-if="!unApproveToken['DG'] && currentUpgradeToken === 'DG'"
              >
                {{ $t("common.approve") }}DG
                <i
                  class="el-icon-loading"
                  v-show="loadings['approveTokenDg']"
                ></i>
              </div>

              <div
                class="button"
                @click="
                  approvalToken('DK', dragonUpgradeAddress, 'approveTokenDk')
                "
                v-if="!unApproveToken['DK'] && currentUpgradeToken === 'DK'"
              >
                {{ $t("common.approve") }}DK
                <i
                  class="el-icon-loading"
                  v-show="loadings['approveTokenDk']"
                ></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="rebirth-dialog" v-if="rebirthDialogVisiable">
      <div class="rebirth-content center">
        <div class="title">Rebirth</div>
        <img
          @click="rebirthDialogVisiable = false"
          src="../../asset/ui/common_close.png"
          alt
          class="close"
        />
        <div class="add-icon">
          <img
            v-if="!rebirthDragon1"
            @click="selectRebirth(1)"
            src="../../asset/ui/add.png"
            alt
            class="dragon-icon"
          />

          <img
            v-if="rebirthDragon1"
            @click="selectRebirth(1)"
            :src="
              'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/dragons/dragon' +
              rebirthDragon1['type'] +
              '.png'
            "
            alt
            class="dragon"
          />

          <img
            v-if="!rebirthDragon2"
            @click="selectRebirth(2)"
            src="../../asset/ui/add.png"
            alt
            class="dragon-icon"
          />

          <img
            @click="selectRebirth(2)"
            v-if="rebirthDragon2"
            :src="
              'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/dragons/dragon' +
              rebirthDragon2['type'] +
              '.png'
            "
            alt
            class="dragon"
          />
        </div>

        <div class="coin-num">
          <img src="../../asset/ui/dg.png" alt />
          10000
        </div>

        <div
          v-if="!dragonRebirthTokenApprove"
          class="template-btn2"
          @click="approvalToken('DG', dragonRebirthAddress, 'approveRTokenDg')"
        >
          {{ $t("common.approve") }}DG
          <i class="el-icon-loading" v-show="loadings['approveRTokenDg']"></i>
        </div>
        <div
          v-if="dragonRebirthTokenApprove"
          class="template-btn2"
          @click="rebirthDragon"
        >
          Rebirth
          <i class="el-icon-loading" v-show="loadings['reborn']"></i>
        </div>

        <div class="button hatch-button" @click="hatch()">
          Hatch({{ myEggNumber }})
          <i class="el-icon-loading" v-show="loadings['hatch']"></i>
        </div>
      </div>
    </div>

