      <div class="dialog-content center">
        <img
          src="../../asset/ui/common_close.png"
          alt
          class="close"
          @click="equipmentDialogVisiable = false"
        />
        <div class="title">Please choose {{ $t("prop." + equipmentType) }}</div>

        <div class="equipment-content">
          <div class="equipment-list">
            <div class="equipment-box" v-for="(item, index) in 0" :key="index">
              <div
                class="equipment-item"
                @click="
                  equipmentDialogVisiable = false;
                  equipmentDetailVisiable = true;
                "
              >
                <img
                  :src="
                    'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/' +
                    equipmentType +
                    '/6.png'
                  "
                  alt
                  class="center"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="equipmentDetail-dialog center" v-if="equipmentDetailVisiable">
      <div class="dialog-content center">
        <img
          src="../../asset/ui/common_return.png"
          alt
          class="return"
          @click="
            equipmentDialogVisiable = true;
            equipmentDetailVisiable = false;
          "
        />
        <div class="title">Equipment</div>

        <div class="inner-content">
          <div class="top-part">
            <div class="detail-border">
              <img
                src="https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/medal/6.png"
                alt
                class="center"
              />
            </div>

            <p>Medal name</p>
          </div>

          <div class="center-part">
            <p>Statistics</p>
            <div>
              <div class="buffer">
                <p>+100</p>
              </div>
            </div>
          </div>

          <div class="template-btn2">Mosaic</div>
        </div>
      </div>
    </div>

    <!-- DIALOG -->
    <div class="transferdialog" v-if="transferDialogVisable">
      <div class="transfer-content">
        <div class="title-bar">Transfer</div>
        <img
          src="../../asset/ui/common_close.png"
          alt
          class="close"
          @click="
            transferDialogVisable = false;
            confirmTransferNft = false;
            loadings['transfer'] = false;
          "
        />
        <div class="main-box">
          <div class="totransfer-box">
            <el-input
              type="text"
              :placeholder="$t('common.transferHolder')"
              v-model="transferAddress"
              maxlength="42"
              show-word-limit
            ></el-input>

            <div class="bottom-btns">
              <div class="template-btn2" @click="toTransferNft">
                {{ $t("common.confirm") }}
                <i class="el-icon-loading" v-show="loadings['transfer']"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <SummonResultView
      v-if="resultDialogVisiable"
      :summonResultDragons="summonResultDragons"
    ></SummonResultView>
  </div>
</template>

<script>
import store from "@/store/index";
const path = store.state.contractPath;
const abiJsons = require("../../contracts/deployments." + path + ".json");
const tokensJsons = require("../../contracts/tokens." + path + ".json");
const erc20AbiJosn = require("../../contracts/erc20.json");

import Util from "../../utils/util";

import SummonResultView from "../shop/SummonResult.vue";

export default {
  name: "dragonView",
  components: {
    SummonResultView,
  },
  data() {
    return {
      dialogVisible: false,
      transferAddress: "",
      dragonsVisable: false,
      transferDialogVisable: false,
      confirmTransferNft: false,
      tokenContracts: {},
      tokens: ["DG", "DK"],
      dragonRebirthTokenApprove: false,
      dragonUpgradeTokenApprove: false,
      currentTransfer: {
        img: "1.png",
      },
      activeName: "dragon",

      activeDragon: null,
      currentQualityGrade: "All",
      dragonData: {},
      txs: {},
      rebirthActiveDragon: {
        img: "1.png",
        id: null,
        type: 0,
      },
      upgradeActiveDragon: {
        img: "1.png",
        id: null,
        type: 0,
      },
      upgradeActiveIndex: 0,
      mergeDragon: {
        img: "1.png",
      },
      loadings: {},

      mergeDragons: {
        dragon1: {},
        dragon2: {},
      },
      upgradeDragons: [],

      harvestData: [],
      harvestType: null,

      ///
      activeAttr: 0,
      activeInfoTab: "info",
      activeDragonId: 0,
      upgradeDialogVisiable: false,
      detailDialogVisiable: false,
      rebirthDialogVisiable: false,
      rebirthDragonDialogVisiable: false,
      equipmentDialogVisiable: false,
      equipmentType: "",
      equipmentDetailVisiable: false,
      upgradeDkNum: 0,
      upgradeDgNum: 0,

      unApproveDragonUpgrade: false,
      unApproveToken: {},
      approveCurrentToken: false,
      currentUpgradeToken: null,

      rebirthIndex: 1,
      rebirthDragon1: null,
      rebirthDragon2: null,

      myEggNumber: 0,
      resultDialogVisiable: false,
      summonResultDragons: [],
    };
  },
  methods: {
    init() {
      if (!this.web3 || !this.web3.web3Instance) {
        return;
      }
      let web3 = this.web3.web3Instance();
      let that = this;

      this.dragonAddress = abiJsons["Dragon"]["address"];
      this.dragonContract = new web3.eth.Contract(
        abiJsons["Dragon"]["abi"],
        this.dragonAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.dragonUpgradeAddress = abiJsons["DragonUpgrade"]["address"];
      this.dragonUpgradeContract = new web3.eth.Contract(
        abiJsons["DragonUpgrade"]["abi"],
        this.dragonUpgradeAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.dragonRebirthAddress = abiJsons["DragonRebirth"]["address"];
      this.dragonRebirthContract = new web3.eth.Contract(
        abiJsons["DragonRebirth"]["abi"],
        this.dragonRebirthAddress,
        {
          from: this.web3.coinbase,
        }
      );

      // this.dragonUpgradeAddress = abiJsons["DragonUpgrade"]["address"];

      for (var i in this.tokens) {
        that.tokenContracts[this.tokens[i]] = new web3.eth.Contract(
          erc20AbiJosn,
          tokensJsons[this.tokens[i]]["address"],
          {
            from: that.web3.coinbase,
          }
        );
      }

      this.getInfo();
    },

    getInfo() {
      this.getMyDragons();
      this.getApproveInfo();
      this.getTokenAllowance("DG", this.dragonUpgradeAddress); //
      this.getTokenAllowance("DK", this.dragonUpgradeAddress); //

      this.getTokenAllowance("DG", this.dragonRebirthAddress); //

      this.getMyEggs();
    },

    getApproveInfo() {
      this.dragonContract.methods
        .isApprovedForAll(this.web3.coinbase, this.dragonUpgradeAddress)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          this.unApproveDragonUpgrade = res;

          // this.isApprovedForAll = res;
        });
    },

    getDragonAttrs(dragonIds, cb) {
      let that = this;

      this.dragonContract.methods
        .getDragonAttrExts(dragonIds)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          cb(res[0]);
        });
    },

    getTokenAllowance(token, address) {
      let that = this;
      if (!this.tokenContracts[token]) {
        return;
      }
      this.tokenContracts[token].methods
        .allowance(this.web3.coinbase, address)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          if (res > 0) {
            res = true;
          } else {
            res = false;
          }

          if (address === this.dragonUpgradeAddress) {
            this.approveCurrentToken =
              this.unApproveToken[this.currentUpgradeToken];

            that.$set(that.unApproveToken, token, res);
          } else if (address == this.dragonRebirthAddress) {
            this.dragonRebirthTokenApprove = res;
          }
        });
    },

    getMyEggs() {
      this.dragonRebirthContract.methods
        .eggsOfOwner(this.web3.coinbase, 0, 0)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          this.myEggIds = res[0];
          var myEggNumber = 0;
          for (var i in res[1]) {
            myEggNumber += parseInt(res[1][i]);
          }
          this.myEggNumber = myEggNumber;
        });
    },

    /**
     * token Approve
     */
    approvalToken(token, address, loadings) {
      if (this.loadings[loadings]) {
        return;
      }
      var that = this;
      that.$set(that.loadings, loadings, true);
      this.tokenContracts[token].methods
        .approve(address, Util.infinity)
        .send({
          gasPrice: this.web3.gasPrice,
        })
        .on("transactionHash", function (hash) {
          
        })
        .on("receipt", function (receipt) {
          if (that.txs[receipt.transactionHash]) {
            return;
          }
          that.txs[receipt.transactionHash] = receipt.transactionHash;
          that.$set(that.loadings, loadings, false);
          that.getTokenAllowance(token, address);
        })
        .on("confirmation", function (confirmationNumber, receipt) {
          
        })
        .on("error", function (err) {
          that.$message({
            message: Util.errMsg(err),
            type: "error",
            center: true,
          });
          that.$set(that.loadings, loadings, false);
          
        });
    },

    toUpgradeDragonLevel() {
      this.dragonUpgradeContract.methods
        .getLevelConfig(Number(this.dragonData[this.activeDragonId]["level"]))
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          this.upgradeDgNum = (res[0] / Math.pow(10, 18)).toFixed(0);
          this.upgradeDkNum = res[1] / Math.pow(10, 18).toFixed(0);

          if (this.upgradeDgNum > 0) {
            this.currentUpgradeToken = "DG";
            this.approveCurrentToken = this.unApproveToken["DG"];
          }

          if (this.upgradeDkNum > 0) {
            this.currentUpgradeToken = "DK";
            this.approveCurrentToken = this.unApproveToken["DK"];
          }
          this.upgradeDialogVisiable = true;
          this.$store.dispatch("ASET_WALLETVISIABLE", true);
        });
    },

    approveDragonUpgrade() {
      let _loadings = "approveDragonUpgrade";
      let that = this;
      if (that.loadings[_loadings]) {
        return;
      }
      that.$set(that.loadings, _loadings, true);
      this.dragonContract.methods
        .setApprovalForAll(this.dragonUpgradeAddress, true)
        .estimateGas({ from: that.web3.coinbase }, function (err, gas) {
          
          if (err) {
            that.$message({
              message: Util.errMsg(err),
              type: "error",
              center: true,
            });
            that.$set(that.loadings, _loadings, false);
            return;
          }
          that.dragonContract.methods
            .setApprovalForAll(that.dragonUpgradeAddress, true)
            .send({
              gas: gas,
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {
              
            })
            .on("receipt", function (receipt) {
              
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.$set(that.loadings, _loadings, false);
              that.$message({
                message: "Approve Success",
                type: "success",
                center: true,
              });
              that.txs[receipt.transactionHash] = receipt.transactionHash;
              that.getApproveInfo();
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              
            })
            .on("error", function (err) {
              that.$message({
                message: Util.errMsg(err),
                type: "error",
                center: true,
              });
              that.$set(that.loadings, _loadings, false);
              
            });
        });
    },

    toUpgradeDragon() {
      let that = this;
      let _id = this.dragonData[this.activeDragonId]["id"];
      let _level = Number(this.dragonData[this.activeDragonId]["level"]);
      // this.dragonUpgradeContract.methods.upgrade();
      ///////
      that.$set(that.loadings, "toUpgradeDragon", true);
      this.dragonUpgradeContract.methods
        .upgrade(_id)
        .estimateGas({ from: that.web3.coinbase }, function (err, gas) {
          if (err) {
            

            that.$set(that.loadings, "toUpgradeDragon", false);
            that.$message({
              message: Util.errMsg(err),
              type: "warning",
              center: true,
            });
            return;
          }
          that.dragonUpgradeContract.methods
            .upgrade(_id)
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {
              
            })
            .on("receipt", function (receipt) {
              
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;
              let _isSuccess =
                receipt["events"]["Upgrade"]["returnValues"]["status"];

              if (_isSuccess) {
                that.$message({
                  message: "Upgrade Success",
                  type: "success",
                  center: true,
                });
              } else {
                that.$message({
                  message: "The upgrade failed, the dragon is dead...",
                  type: "error",
                  center: true,
                });
              }
              that.$set(that.loadings, "toUpgradeDragon", false);

              that.upgradeDialogVisiable = false;
              that.$store.dispatch("ASET_WALLETVISIABLE", false);
              that.getMyDragons();
              that.$EventBus.$emit("refreshBalance");
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              
              that.$set(that.loadings, "toUpgradeDragon", false);
            })
            .on("error", function (err) {
              that.$set(that.loadings, "toUpgradeDragon", false);
            });
        });
    },

    selectRebirth(index) {
      this.rebirthIndex = index;
      this.rebirthDragonDialogVisiable = true;
    },

    selectRebirthDragon(dragon) {
      if (this.rebirthIndex == 1) {
        this.rebirthDragon1 = dragon;
        if (this.rebirthDragon2 && this.rebirthDragon2.id == dragon.id) {
          this.rebirthDragon2 = null;
        }
      } else {
        this.rebirthDragon2 = dragon;

        if (this.rebirthDragon1 && this.rebirthDragon1.id == dragon.id) {
          this.rebirthDragon1 = null;
        }
      }

      this.rebirthDragonDialogVisiable = false;
    },

    rebirthDragon() {
      let that = this;
      if (!this.rebirthDragon1 || !this.rebirthDragon2) {
        this.$message({
          message: "Click to add the rebirth dragon",
          type: "warning",
          center: true,
        });
        return;
      }
      if (this.loadings["reborn"]) {
        return;
      }
      let dragonsIds = [this.rebirthDragon1.id, this.rebirthDragon2.id];

      that.$set(that.loadings, "reborn", true);
      this.dragonContract.methods
        .safeBatchTransferFrom(
          this.web3.coinbase,
          this.dragonRebirthAddress,
          dragonsIds,
          "0x01"
        )
        .estimateGas({ from: this.web3.coinbase }, function (err, gas) {
          
          if (err) {
            that.$set(that.loadings, "reborn", false);
            that.$message({
              message:
                that.$t("dragon.rebirth") +
                that.$t("common.failed") +
                Util.errMsg(err),
              type: "warning",
              center: true,
            });
            // return;
          }
          that.dragonContract.methods
            .safeBatchTransferFrom(
              that.web3.coinbase,
              that.dragonRebirthAddress,
              dragonsIds,
              "0x01"
            )
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {
              
            })
            .on("receipt", function (receipt) {
              
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;
              that.$set(that.loadings, "reborn", false);

              that.$message({
                message: that.$t("dragon.rebirth") + that.$t("common.success"),
                type: "success",
                center: true,
              });

              that.getInfo();
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              
              if (confirmationNumber == 5) {
                that.getInfo();
              }
            })
            .on("error", function (err) {
              that.$message({
                message: Util.errMsg(err),
                type: "error",
                center: true,
              });
              that.$set(that.loadings, "reborn", false);
              
              that.$message({
                message:
                  that.$t("dragon.rebirth") +
                  that.$t("common.failed") +
                  Util.errMsg(err),
                type: "warning",
                center: true,
              });
            });
        });
    },

    hatch() {
      let that = this;
      let _loadings = "hatch";
      var eggIds = this.myEggIds;
      if (eggIds.length == 0) {
        this.$message({
          message: "Please buy egg, then hatch egg",
          type: "warning",
          center: true,
        });
        return;
      }
      if (this.loadings[_loadings]) {
        this.$message({
          message: "Calling, please wait",
          type: "info",
          center: true,
        });
        return;
      }

      that.$set(that.loadings, _loadings, true);

      this.dragonRebirthContract.methods
        .open(eggIds)
        .estimateGas({ from: that.web3.coinbase }, function (err, gas) {
          if (err) {
            that.$set(that.loadings, _loadings, false);

            that.$message({
              message:
                that.$t("store.hatch") +
                that.$t("common.failed") +
                Util.errMsg(err),
              type: "error",
              center: true,
            });
            return;
          }
          that.dragonRebirthContract.methods
            .open(eggIds)
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {
              that.$set(that.loadings, _loadings, true);
            })
            .on("receipt", function (receipt) {
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;

              that.$set(that.loadings, _loadings, false);
              
              that.getSummonDragonsInfo(
                receipt["events"]["MintEvent"]["returnValues"]
              );
              that.getInfo();
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              if (confirmationNumber == 6) {
                that.getInfo();
              }
            })
            .on("error", function (err) {
              that.$message({
                message:
                  that.$t("store.hatch") +
                  that.$t("common.failed") +
                  Util.errMsg(err),
                type: "error",
                center: true,
              });
              that.$set(that.loadings, _loadings, false);
            });
        });
    },

    getSummonDragonsInfo(returnValues) {
      let dragonIds = returnValues["dragonIds"];
      let attrs = returnValues["attrs"];

      dragonIds.map((dragonId, index) => {
        let dragon = Util.getDragonInfoWithAttr(dragonId, attrs[index]);
        this.summonResultDragons.push(dragon);
      });
      this.$EventBus.$emit("refreshBalance");

      this.resultDialogVisiable = true;
    },

    toTransferNft() {
      let that = this;
      if (!this.transferAddress || this.transferAddress.length != 42) {
        this.$message({
          message: that.$t("common.transferAddressError"),
          type: "error",
          center: true,
        });
        return;
      }
      that.$set(that.loadings, "transfer", true);

      var dragonId = this.activeDragonId;

      this.dragonContract.methods
        .transferFrom(this.web3.coinbase, this.transferAddress, dragonId)
        .send({
          gasPrice: that.web3.gasPrice,
        })
        .on("transactionHash", function (hash) {
          
        })
        .on("receipt", function (receipt) {
          if (that.txs[receipt.transactionHash]) {
            return;
          }
          that.txs[receipt.transactionHash] = receipt.transactionHash;
          that.$set(that.loadings, "transfer", false);
          that.transferDialogVisable = false;
          that.getInfo();
          that.transferAddress = "";
          that.$message({
            message: that.$t("common.success"),
            type: "success",
            center: true,
          });
        })
        .on("confirmation", function (confirmationNumber, receipt) {
          
        })
        .on("error", function (err) {
          that.$message({
            message: that.$t("common.failed") + Util.errMsg(err),
            type: "error",
            center: true,
          });
          
          that.$set(that.loadings, "transfer", false);
        });
    },

    getMyDragons() {
      let that = this;
      this.dragonContract.methods
        .balanceOf(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          
          if (!Number(res)) {
            return;
          }
          this.dragonContract.methods
            .tokensOf(this.web3.coinbase, 0, res)
            .call({ from: this.$store.state.contractFrom })
            .then((dragonIds) => {
              let dragonList = [];

              this.getDragonAttrs(dragonIds, (dragonAttrs) => {
                for (var i in dragonIds) {
                  let dragonInfo = Util.getDragonInfoWithAttr(
                    dragonIds[i],
                    dragonAttrs[i]
                  );

                  if (i == 0 && !this.activeDragonId) {
                    that.activeDragonId = dragonInfo.id;
                  }

                  dragonList.push(dragonInfo);
                }
                dragonList.sort(function (a, b) {
                  return a.type - b.type >= 0 ? -1 : 1;
                });

                that.dragonData = {};
                dragonList.map((dragon, index) => {
                  that.$set(that.dragonData, dragon.id, dragon);
                });
              });
            });
        });
    },

    closeDialog() {
      this.$parent.closeDialog("dragon");
    },
    attrFilter(val) {
      switch (val) {
        case 1:
          return this.$t("dragon.water");
        case 2:
          return this.$t("dragon.fire");
        case 3:
          return this.$t("dragon.wind");
      }
    },

    resetActiveDragon(dragon, type, index) {
      let _width = 0;
      switch (dragon.quality) {
        case "SSS":
          _width = 150;
          break;
        case "SS":
          _width = 140;
          break;
        case "S":
          _width = 130;
          break;
        case "A":
          _width = 100;
          break;
        case "B":
          _width = 80;
          break;
        case "C":
          _width = 70;
          break;
      }
      if (type === "rebirth") {
        if (this.loadings["reborn"]) {
          this.$message({
            message: "Respawning and cannot be changed",
            type: "warning",
            center: true,
          });
          return;
        }
        this.rebirthActiveDragon = dragon;
        this.$set(this.rebirthActiveDragon, "width", _width);
      }
      if (type === "upgrade") {
        if (this.loadings["mergedragon"]) {
          this.$message({
            message: "Upgrading and cannot be changed",
            type: "warning",
            center: true,
          });
          return;
        }
        this.upgradeActiveDragon = dragon;
        this.upgradeActiveIndex = index;
        this.$set(this.upgradeActiveDragon, "width", _width);
      }
    },
  },
  computed: {
    web3() {
      return this.$store.state.web3;
    },
  },
  watch: {
    web3(val1, val2) {},
  },

  mounted() {
    this.init();
    this.$store.dispatch("ASET_WALLETVISIABLE", false);
  },
};
</script>

