}

.detail-container .detail-content .information-box {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.detail-container .detail-content .information-box .info-attr-container {
  width: 100%;
  height: 75%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.detail-container .detail-content .information-box .info-title,
.detail-container .detail-content .equipment-box .info-title {
  width: 100%;
  height: 12%;
  font-size: 22px;
