.join-dialog .dialog-content {
  width: 94%;
  height: 60%;
  background-image: url("../../asset/ui/dialog_bg_img.png");
  background-size: 100% 100%;
  position: relative;
  box-sizing: border-box;
  padding: 2% 2%;
}

.join-dialog .dialog-content .dialog-return {
  position: absolute;
  bottom: -10px;
  right: -10px;
  z-index: 1;
  width: 50px;
  cursor: pointer;
}

.join-dialog .dialog-content .dialog-title {
  width: 90%;
  height: 22%;
  background-image: url("../../asset/ui/common-dialog-title.png");
  background-size: 100% 100%;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -75%);
  top: 0;

  font-size: 30px;
  font-weight: 500;
  color: rgb(250, 249, 246);
  text-shadow: -0.0455em -0.0455em 0 #000, 0.0455em -0.0455em 0 #000,
    -0.0455em 0.0455em 0 #000, 0.0455em 0.0455em 0 #000;
}
.join-dialog .dialog-content .dialog-title p {
  top: 60%;
}

.join-dialog .dialog-content .deep-content {
  width: 86%;
  margin: 5px auto 0;
  height: 90%;
  background-size: 100% 100%;
  overflow: hidden;
  background-color: #ece7d281;
  border-radius: 20px;
}

.join-dialog .dialog-content .deep-content .dragons-list {
  width: 98%;
  height: 98%;
