  overflow-y: auto;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}

.join-dialog .dialog-content .deep-content .dragons-list li {
  width: 25%;
  padding-bottom: 30%;
  position: relative;
  border-radius: 10%;
  margin-bottom: 15%;
}

.join-dialog .dialog-content .deep-content .dragons-list li .dragon-item {
  position: absolute;
  width: 90%;
  padding-bottom: 90%;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  background-image: url("../../asset/ui/detail-border.png");
  background-size: 100% 100%;
  cursor: pointer;
}

.join-dialog
  .dialog-content
  .deep-content
  .dragons-list
  li
  .dragon-item
  .dragon {
  width: 65%;
  top: 44% !important;
}

.join-dialog
  .dialog-content
  .deep-content
  .dragons-list
  li
  .dragon-item
  .elementibute {
  width: 20%;
  position: absolute;
  left: 0;
}

.join-dialog
  .dialog-content
  .deep-content
  .dragons-list
  li
  .dragon-item
  .level {
  height: 16px;
  line-height: 16px;
  font-size: 12px;
  background: rgba(0, 0, 0, 0.37);
  border-radius: 0 0 10px 10px;
  white-space: nowrap;
  text-shadow: 0.2px 0.4px 0 rgb(7 7 7), 0.2px 0.4px 0 rgb(7 7 7);
  position: absolute;
  color: #fff;
  bottom: 0;

  width: 84%;
  left: 50%;
  top: 100%;
  transform: translate(-50%, -130%);
  background-color: rgba(0, 0, 0, 0.493);
  border-radius: 0 0 10px 10px;
}

.join-dialog
  .dialog-content
  .deep-content
  .dragons-list
  li
  .dragon-item
  .quality {
  position: absolute;
  right: 0;
  top: 0px;
  transform: translate(-10%, 0%);
  height: 26px;
}

.join-dialog
  .dialog-content
  .deep-content
  .dragons-list
  li
  .dragon-item
  .index {
  font-size: 12px;
  font-weight: 600;
  position: absolute;
  left: 50%;
  bottom: 0;
  -webkit-transform: translate(-50%, 100%);
  transform: translate(-50%, 100%);
  border-radius: 5px;
  white-space: nowrap;
  color: #fdfdfd;
  font-size: 13px;
  font-weight: 500;
  text-shadow: -0.0555em -0.0555em 0 #000, 0.0555em -0.0555em 0 #000,
    -0.0555em 0.0555em 0 #000, 0.0555em 0.0555em 0 #000;
  color: #fbfaf9;
}

.join-dialog
  .dialog-content
  .deep-content
  .dragons-list
  li
  .dragon-item
  .active-gray {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
}

.join-dialog
  .dialog-content
  .deep-content
  .dragons-list
  li
  .dragon-item
  .attributes-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  position: absolute;
  bottom: 0;
  transform: translate(0, 120%);
}

.join-dialog
  .dialog-content
  .deep-content
  .dragons-list
  li
  .dragon-item
  .attributes-list
  .attr-item {
  width: 47%;
  display: flex;
  justify-content: space-around;
}

.join-dialog
  .dialog-content
  .deep-content
  .dragons-list
  li
  .dragon-item
  .attributes-list
  .attr-item
  svg {
  width: 22px;
  height: 22px;
}

.hatch-button {
  width: 40%;
  height: 60px;
  line-height: 40px;
  background-image: url("../../asset/ui/creat-btn-red.png");
  margin: 10px auto 0;
  background-size: 100% 100%;
  color: #fff;
  cursor: pointer;
  font-size: 26px;
  font-weight: 500;
}
/* DIALOG */
</style>


