import store from "../store/index";

const Web3Modal = window.Web3Modal.default;
const WalletConnectProvider = window.WalletConnectProvider.default;
let ethereum = window.ethereum;
let web3Modal;
let provider;
let providerOptions;

async function init(chainName) {

  if (chainName == "testnet") {
    providerOptions = {
      package: WalletConnectProvider,
      walletconnect: {
        options: {
          rpc: {
            97: "https://data-seed-prebsc-1-s1.binance.org:8545/",
          },
          chainId: 97,
        },
      },
    };
  } else {
    providerOptions = {
      walletconnect: {
        package: WalletConnectProvider,
        options: {
          rpc: {
            56: "https://bsc-dataseed.binance.org/",
          },
          chainId: "56",
        },
      },
    };
  }
  let providerChainId = providerOptions.walletconnect.options.chainId;

  if (ethereum.chainId != providerChainId && chainName == "mainnet") {
    await ethereum.request({
      method: "wallet_addEthereumChain",
      params: [{
        chainId: "0x38", //todo
        chainName: "Binance Main", //todo
        nativeCurrency: {
          name: "BSC",
          symbol: "BNB",
          decimals: 18
        },
        rpcUrls: ["https://bsc-dataseed.binance.org/"],
        blockExplorerUrls: ["https://bscscan.com"]
      }]
    });
  } else if (ethereum.chainId != providerChainId && chainName == "testnet") {
    await ethereum.request({
      method: "wallet_addEthereumChain",
      params: [{
        chainId: "0x61", //todo
        chainName: "BSC TestNet", //todo
        nativeCurrency: {
          name: "BSC",
          symbol: "BNB",
          decimals: 18
        },
        rpcUrls: ["https://data-seed-prebsc-1-s1.binance.org:8545/"],
        blockExplorerUrls: ["https://testnet.bscscan.com/"]
      }]
    });
  }
  web3Modal = new Web3Modal({
    network: chainName,
    cacheProvider: true, // optional
    providerOptions, // required
  });
  store.commit("setProvider", {
    web3Modal,
    providerOptions
  })

}
async function fetchAccountData() {
  const web3 = new Web3(provider);
  const chainId = await web3.eth.getChainId();
  const accounts = await web3.eth.getAccounts();
  const gasPrice = await web3.eth.getGasPrice()
  let payload = {
    coinbase: accounts[0],
    networkId: chainId,
    gasPrice: gasPrice,
    web3() {
      return web3
    },
  };
  store.commit("registerWeb3Instance", payload);
  const rowResolvers = accounts.map(async (address) => {
    const balance = await web3.eth.getBalance(address);
    const ethBalance = web3.utils.fromWei(balance, "ether");
    const humanFriendlyBalance = parseFloat(ethBalance).toFixed(4);
  });
  await Promise.all(rowResolvers);
}

/**
 * Fetch account data for UI when
 * - User switches accounts in wallet
 * - User switches networks in wallet
 * - User connects wallet initially
 */
async function refreshAccountData() {
  await fetchAccountData(provider);
}

/**
 * Connect wallet button pressed.
 */
async function onConnect() {
  try {
    provider = await store.state.web3Modal.connect();
  } catch (e) {
    return;
  }
  // Subscribe to accounts change
  provider.on("accountsChanged", (accounts) => {
    fetchAccountData();
  });
  await refreshAccountData();
}
async function reConnect() {
  try {
    provider = await store.state.web3Modal.connect();
  } catch (e) {
    return;
  }
  fetchAccountData();
  await refreshAccountData();
}
/**
 * Disconnect wallet button pressed.
 */
async function onDisconnect() {
  if (provider.close) {
    await provider.close();
    await web3Modal.clearCachedProvider();
    provider = null;
  }
  store.commit("clearWalletCache")
}
ethereum.on("chainChanged", async (chainId) => {
  await init()
})
export default {
  onDisconnect,
  onConnect,
  init,
  fetchAccountData,
};



