const DragonConfig = require("../data/config.json");

export default {

  infinity: "0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff",
  none: "0x0000000000000000000000000000000000000000",

  // nftSign  propType    propID   attr  piece number   padding mintTime    index
  // 1        31          32       32    16    16       24      40          64
  // 255      224         192      160   144   128      104     64          0

  getDragonPropInfoByHex: function (propId) {
    var _propId = propId.slice(2, 64)
    var category = parseInt("0x" + _propId.slice(1, 8))
    var typeId = parseInt("0x" + _propId.slice(8, 16))
    var attr = parseInt("0x" + _propId.slice(16, 24))
    var isPiece = parseInt("0x" + _propId.slice(24, 28))
    var number = parseInt("0x" + _propId.slice(28, 32))
    var padding = parseInt("0x" + _propId.slice(32, 38))

    var type = DragonConfig['dragonProp'][category]['type']
    var img = "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/" + type + "/" + number + ".png";

    return {
      id: _propId.toString(),
      category: category,
      typeId: typeId,
      type: type,
      subType: DragonConfig['dragonProp'][category][typeId],
      isPiece: isPiece,
      attr: attr,
      number: number,
      img: img,
      padding: padding
    };
  },

  getDragonPropInfo: function (propId) {
    // this.getDragonPropInfoByHex(propId)
    propId = propId.toString().toLowerCase()
    var t = new BigInteger(propId);

    // nftSign  propType    propID   attr  piece number   padding mintTime    index
    // 1        31          32       32    16    16       24      40          64
    // 255      224         192      160   144   128      104     64          0

    // 1 diamond 1000
    // 2 food    2000
    // 3 diamond piece 3000 

    var category = this.getIntegerBits(t, 224, 23)
    var typeId = this.getIntegerBits(t, 192, 32)
    var attr = this.getIntegerBits(t, 160, 32)
    var isPiece = this.getIntegerBits(t, 144, 16)
    var number = this.getIntegerBits(t, 128, 16)
    var padding = this.getIntegerBits(t, 104, 24)

    var type = DragonConfig['dragonProp'][category]['type']
    var img = "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/" + type + "/" + number + ".png";


    return {
      id: t.toString(),
      category: category,
      typeId: typeId,
      type: type,
      subType: DragonConfig['dragonProp'][category][typeId],
      isPiece: isPiece,
      isPiece: isPiece,
      attr: attr,
      number: number,
      img: img,
      padding: padding
    };
  },

  getPropPieceInfo: function (piece) {
    var type = DragonConfig['dragonProp'][piece['propType']]['type']
    var img = "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/" + type + "/" + piece['number'] + ".png";

    return {
      // category: category,
      // typeId: typeId,
      // subType: DragonConfig['dragonProp'][category][typeId],
      propType: piece['propType'],
      type: type,
      isPiece: piece["isPiece"],
      attr: piece["attr"],
      number: piece["number"],
      img: img,
      padding: piece["padding"]
    };
  },

  getEggPieceInfoByHex: function (pieceId) {
    var _pieceId = pieceId.slice(2, 64)

    // nftSign  factory     type      padding mintTime    index
    // 1        31          64        56      40          64
    // 255      224         160       104     64          0

    var type = parseInt("0x" + _pieceId.slice(8, 24))
    var img = "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/egg/" + type + ".png";
    var breakImg = "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/egg/break" + type + ".png";
    return {
      id: pieceId.toString(),
      type: type,
      img: img,
      breakImg: breakImg
    };
  },

  getDragonEggInfoByHex: function (eggId) {
    var _eggId = eggId.slice(2, 64)

    // nftSign  factory  type           padding     mintTime    index
    // 1        31          64          56          40      40          64
    // 255      224         160         144         104     64          0

    var type = parseInt("0x" + _eggId.slice(8, 16))
    var img = "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/egg/" + type + ".png";
    let duration = DragonConfig['dragonEgg']["duration"][type];
    let probability = DragonConfig['dragonEgg']["probability"][type];
    let hatchedImg = "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/egg/hatched" + type + ".png";
    return {
      id: _eggId.toString(),
      type: type,
      duration: duration,
      img: img,
      hatchedImg: hatchedImg,
      desc: "desc",
      probability: probability
    };
  },

  getEggPieceInfo: function (pieceId) {
    pieceId = pieceId.toString().toLowerCase()
    var t = new BigInteger(pieceId);

    // nftSign  factory     type      padding mintTime    index
    // 1        31          64        56      40          64
    // 255      224         160       104     64          0

    var type = this.getIntegerBits(t, 160, 64)
    var img = "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/egg/" + type + ".png";
    return {
      id: t.toString(),
      type: type,
      img: img
    };
  },

  getDragonEggInfo: function (eggId) {
    eggId = eggId.toString().toLowerCase()
    var t = new BigInteger(eggId);

    // nftSign  factory  type           padding     mintTime    index
    // 1        31          64          56          40      40          64
    // 255      224         160         144         104     64          0

    var type = this.getIntegerBits(t, 160, 64)
    var img = "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/egg/" + type + ".png";
    let duration = DragonConfig['dragonEgg']["duration"][type];
    let probability = DragonConfig['dragonEgg']["probability"][type];
    let hatchedImg = "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/egg/hatched" + type + ".png";
    return {
      id: t.toString(),
      type: type,
      duration: duration,
      img: img,
      hatchedImg: hatchedImg,
      desc: "",
      probability: probability
    };
  },

  getDragonInfoByHex: function (dragonId) {
    var _dragonId = dragonId.slice(2, 64)

    // nftSign type element  rarity padding  mintTime    index
    // 1       31   32    32        56      40            64
    // 255     224  192   160       104     64          0

    var mintTime = parseInt("0x" + _dragonId.slice(38, 48))
    var type = parseInt("0x" + _dragonId.slice(1, 8))
    var img = "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/dragons/dragon" + type + ".png"
    return {
      id: dragonId.toString(),
      type: type,
      img: img,
      element: parseInt("0x" + _dragonId.slice(8, 16)),
      rarity: parseInt("0x" + _dragonId.slice(16, 24)),
      padding: parseInt("0x" + _dragonId.slice(24, 38)),
      mintTime: mintTime,
      index: parseInt("0x" + _dragonId.slice(48, 64)),
    };
  },

  getDragonInfo: function (dragonId) {
    dragonId = dragonId.toString().toLowerCase()
    var t = new BigInteger(dragonId);

    // nftSign type element  rarity padding  mintTime    index
    // 1       31   32    32        56      40            64
    // 255     224  192   160       104     64          0

    var mintTime = this.getIntegerBits(t, 64, 40);
    var type = this.getIntegerBits(t, 224, 31)
    var img = "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/dragons/dragon" + type + ".png"
    return {
      id: t.toString(),
      type: type,
      img: img,
      attack: 0,
      life: 0,
      maxLife: 0,
      defense: 0,
      magicResistance: 0,
      element: this.getIntegerBits(t, 192, 32),
      rarity: this.getIntegerBits(t, 160, 32),
      padding: this.getIntegerBits(t, 104, 56),
      mintTime: mintTime,
      index: this.getIntegerBits(t, 0, 64),

      strength: 0,
      agility: 0,
      constitution: 0,
      intelligence: 0,
      willpower: 0,
      spirit: 0,
      level: 0,
      breed: 0,
      star: 0,
      skin: 0,
      powerValue: 0
    };
  },

  getDragonAttr: function (attr) {
    attr = attr.toString().toLowerCase()
    var t = new BigInteger(attr);

    var attrs = []
    var powerValue = 0;
    for (var i = 0; i < 10; i++) {
      attrs[i] = this.getIntegerBits(t, 256 - 24 - 24 * i, 24)
      if (i < 6) {
        powerValue += attrs[i]
      }
    }

    return {
      attr: attr,
      strength: attrs[0],
      agility: attrs[1],
      constitution: attrs[2],
      intelligence: attrs[3],
      willpower: attrs[4],
      spirit: attrs[5],
      level: attrs[6],
      breed: attrs[7],
      star: attrs[8],
      skin: attrs[9],
      powerValue: powerValue
    };
  },

  getDragonInfoWithAttr: function (dragonId, attr) {
    var dragon = this.getDragonInfo(dragonId);
    var attrObj = this.getDragonAttr(attr);

    return Object.assign(dragon, attrObj)
  },

  errMsg(err) {
    var msg = err.toString()
    console.log(err, msg)
    if (msg.indexOf("User denied transaction") > 0) {
      return "User denied transaction signature"
    } else if (msg.indexOf("execution reverted:") > 0) {
      msg = msg.split("execution reverted:")[1];
      return msg.split('"')[0]
    }
    try {
      return err.data.message
    } catch (err1) {
      err = err.toString();
      var s = err.indexOf('"message":');
      var e = err.indexOf('"data":');
      return err.slice(s, e);
    }

  },

  randomString(len) {
    len = len || 32;
    var $chars = "abcdefghijklmnopqrstuvwxyz0123456789";
    var maxPos = $chars.length;
    var pwd = "";
    for (var i = 0; i < len; i++) {
      pwd += $chars.charAt(Math.floor(Math.random() * maxPos));
    }
    return pwd;
  },

  formatDate: function formatDate(t, fmt) {
    var d = new Date(1000 * parseInt(t))
    var o = {
      "M+": d.getMonth() + 1,
      "d+": d.getDate(),
      "h+": d.getHours(),
      "m+": d.getMinutes(),
      "s+": d.getSeconds(),
      "q+": Math.floor((d.getMonth() + 3) / 3),
      "S": d.getMilliseconds()
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (d.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o) {
      if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length));
    }
    return fmt;
  },

  isMobile: function () {
    return (navigator.userAgent.match(/AppleWebKit.*Mobile.*/) ||
      !!navigator.userAgent.match(/iOS38*/) ||
      navigator.userAgent.match(/iPhone/) ||
      navigator.userAgent.match(/Android/)
    ) ? true : false
  },
  getIntegerBits: function (t, e, r) {
    var n = t.shiftRight(e).and(BigInteger.ONE.shiftLeft(r).subtract(BigInteger.ONE));
    return Number(n.toString())
  },
  toNumber: function (amount, decimals, number) {
    number = decimals > number ? number : decimals;
    var t = new BigInteger(amount.toString());
    var n = t.divide(new BigInteger(10 ** (decimals - number) + ""));
    return Number(parseFloat(n.toString()) / 10 ** number).toFixed(number);
  },

  toBigInteger: function (t) {
    return t = t.toString().toLowerCase(),
      this.stringStartsWith(t, "0x") ? new BigInteger(t.substring(2), 16) : new BigInteger(t)
  },

  stringStartsWith: function (t, e) {
    return t.substring(0, e.length) == e
  },

  add(t1, t2) {
    var t = new BigInteger(t1.toString());
    return t.add(new BigInteger(t2.toString()));
  },
  sub(t1, t2) {
    var t = new BigInteger(t1.toString());
    return t.subtract(new BigInteger(t2.toString()));
  },

  mul(t1, t2) {
    var t = new BigInteger(t1.toString());
    return t.multiply(new BigInteger(t2.toString()));
  },

  div(t1, t2) {
    var t = new BigInteger(t1.toString());
    return t.divide(new BigInteger(t2.toString()));
  },

  compareTo(t1, t2) {
    t1 += ""
    var t = new BigInteger(t1.toString());
    return t.compareTo(new BigInteger(t2.toString()));
  },


  countdownHour: function (t) {
    if (t < 0) {
      return '--:--:--'
    }
    var str =
      parseInt(t / 3600) > 9 ? parseInt(t / 3600) : "0" + parseInt(t / 3600);
    str += ":"
    t = t % 3600
    str +=
      parseInt(t / 60) > 9 ? parseInt(t / 60) : "0" + parseInt(t / 60);
    str += ':'
    str +=
      parseInt(t % 60) > 9 ? parseInt(t % 60) : "0" + parseInt(t % 60);

    return str;
  },
  offset: function (element) {
    if (!element) return;
    var pos = {
      left: 0,
      top: 0
    };
    var parents = element.offsetParent;

    pos.left += element.offsetLeft;
    pos.top += element.offsetTop;

    while (parents && !/html|body/i.test(parents.tagName)) {
      pos.left += parents.offsetLeft;
      pos.top += parents.offsetTop;

      parents = parents.offsetParent;
    }
    return pos;
  },


  filterNumber(value) {
    return Number(value / 10 ** 18).toFixed(6)
  },

  filterTokenAmount(value) {
    return Number(value / 1).toFixed(2);
  },


  filterEggType(value) {
    switch (value) {
      case 0:
        return "Creation Dragon Egg";
      case 1:
        return "Super Dragon Egg";
      case 2:
        return "Precious Dragon Egg";
      case 3:
        return "Ordinary Dragon Egg";
    }
  },

  compareArr(property) {
    return function (a, b) {
      var value1 = a[property];
      var value2 = b[property];
      return value2 - value1;
    };
  },
  _keyStr: "ABCKEFGHIJMLDNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789",
  // _keyStr: "A9CDEFGHIJKLMNOPQRSTUVWXYZabcdeBghijklmnopqrstuvwxyz012345678f",
  encode: function (e) {
    var t = "";
    var n, r, i, s, o, u, a;
    var f = 0;
    // e = Base64._utf8_encode(e);
    while (f < e.length) {
      n = e.charCodeAt(f++);
      r = e.charCodeAt(f++);
      i = e.charCodeAt(f++);
      s = n >> 2;
      o = (n & 3) << 4 | r >> 4;
      u = (r & 15) << 2 | i >> 6;
      a = i & 63;
      if (isNaN(r)) {
        u = a = 64
      } else if (isNaN(i)) {
        a = 64
      }
      t = t + this._keyStr.charAt(s) + this._keyStr.charAt(o) + this._keyStr.charAt(u) + this._keyStr.charAt(a)
    }
    return t
  },

  decode: function (e) {
    var t = "";
    var n, r, i;
    var s, o, u, a;
    var f = 0;
    e = e.replace(/[^A-Za-z0-9+/=]/g, "");
    while (f < e.length) {
      s = this._keyStr.indexOf(e.charAt(f++));
      o = this._keyStr.indexOf(e.charAt(f++));
      u = this._keyStr.indexOf(e.charAt(f++));
      a = this._keyStr.indexOf(e.charAt(f++));
      n = s << 2 | o >> 4;
      r = (o & 15) << 4 | u >> 2;
      i = (u & 3) << 6 | a;
      t = t + String.fromCharCode(n);
      if (u != 64) {
        t = t + String.fromCharCode(r)
      }
      if (a != 64) {
        t = t + String.fromCharCode(i)
      }
    }
    // t = Base64._utf8_decode(t);
    return t
  },

  getDragonDetailAttr(dragon) {
    let _equipment = 0;
    let attack = 0;
    switch (Number(dragon['element'])) {
      case 1:
        attack = dragon['strength'];
        break;
      case 2:
        attack = dragon['strength'];
        break;
      case 3:
        attack = dragon['intelligence'];
        break;
      case 4:
        attack = dragon['strength'];
        break;
    }

    let lifeValue = parseInt(5 * dragon['constitution'] * (1 + 0.2 * (dragon['level'] - 1) + _equipment));
    let attackValue = parseInt(Number(attack) * (1 + 0.2 * (dragon['level'] - 1)));
    let defense = parseInt(Number(dragon['willpower']) * (1 + 0.2 * (dragon['level'] - 1)));
    let magicResistance = parseInt(Number(dragon['spirit']) * (1 + 0.2 * (dragon['level'] - 1)));
    dragon["life"] = lifeValue;
    dragon["maxLife"] = lifeValue;
    dragon["attack"] = attackValue;
    dragon["defense"] = defense;
    dragon["magicResistance"] = magicResistance;
    return dragon
  },

  getMonsterInfo: function (monsterId) {
    monsterId = monsterId.toString().toLowerCase()
    var t = new BigInteger(monsterId);
    let strength = this.getIntegerBits(t, 224, 32);
    let agility = this.getIntegerBits(t, 192, 32);
    var type = this.getIntegerBits(t, 32, 32);
    let intelligence = this.getIntegerBits(t, 128, 32);
    let constitution = this.getIntegerBits(t, 160, 32);
    var img = "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/monster/avatar-" + type + ".png"
    let life = 0;
    let attack = 0;
    let defense = 0;
    if (type === 1 || type === 3) {
      attack = parseInt(strength);
    } else if (type === 2) {
      attack = parseInt(intelligence);
    }

    defense = parseInt(Number(this.getIntegerBits(t, 96, 32)))
    life = parseInt(5 * constitution);
    let spirit = this.getIntegerBits(t, 64, 32);
    let magicResistance = parseInt(Number(spirit));

    return {
      id: t.toString(),
      type: type,
      img: img,
      strength: strength,
      agility: agility,
      intelligence: intelligence,
      constitution: constitution,
      willpower: this.getIntegerBits(t, 96, 32),
      spirit: spirit,
      life: life,
      attack: attack,
      magicResistance: magicResistance,
      defense: defense
    };
  },

  getDungeonRecord(record) {
    let dragon = this.getDragonInfo(record['dragonId'])
    let monsters = [];
    record['monsters'].map((monsterId) => {
      monsters.push(this.getMonsterInfo(monsterId))
    })

    return {
      level: record.dungeonLevel,
      dragon: dragon,
      monsters: monsters,
      reward: record['reward'],
      status: record['status']
    }
  },

  getCyberDragonType(address) {
    let _allAddress = DragonConfig['CyberCardAddress'];

    let _type = null;
    for (let key in _allAddress) {
      if (_allAddress[key] === address) {
        _type = key;
      }
    }

    return _type;
  },

  getCyberDragonDetailAttr(cyber) {
    let constitutionValue = cyber[0][2];
    let strengthValue = cyber[0][0];
    if (cyber['profession'] === 'mage') {
      strengthValue = cyber[0][3];
    }
    let lifeValue = 5 * constitutionValue * 1;
    let attack = parseInt(Number(strengthValue) * 1);
    let defense = attack;

    cyber["life"] = lifeValue;
    cyber["attack"] = attack;
    cyber["defense"] = defense;

    return cyber
  }

}



