var MonsterSprite = Hilo.Class.create({
  Extends: Hilo.Sprite,

  Mixes: Hilo.EventMixin,

  constructor: function constructor(properties) {
    MonsterSprite.superclass.constructor.call(this, properties);

    this.addFrame(properties.atlas.getSprite("monster"));
  },

  leader: null,
  viewWidth: 0,
  viewHeight: 0,
  startPosition: {
    x: 0,
    y: 0
  },
  index: 0,
  monsterLength: 0,

  // state: "sideleft",


  // centerTopX: 0,
  // centerTopY: 0,
  // centerBottomX: 0,
  // centerBottomY: 0,
  // angleTop: 1,
  // angleBottom: 0.5,
  // rotation: 0,

  // leftTopX: 0,
  // leftTopY: 0,
  // leftBottomX: 0,
  // leftBottomY: 0,
  // rightTopX: 0,
  // rightTopY: 0,
  // rightBottomX: 0,
  // rightBottomY: 0,

  // trackHeight: 0,
  // trackWidth: 0,
  // track: null,
  // status: "sideleft",
  // curframe: "sideleft",
  // speed: 14,
  // speedFactor: 1,

  // level: 0,
  // leftSlope: 0,
  // rightSlope: 0,
  // radiusTop: 0,
  // radiusBottom: 0,
  // gold: true,
  getReady: function getReady() {},



  start: function start(params) {},

  stop: function stop(params) {},


  onUpdate: function onUpdate() {}
});
export default MonsterSprite



