<template>
  <div
    class="container"
    :class="{ isTop: selectDialogVisiable || battleViewVisiable }"
  >
    <!-- <img
      src="../../asset/ui/common_return.png"
      alt=""
      class="close-btn"
      @click="$parent.closeDialog('dungeon')"
    />-->

    <div class="round-btn record-btn" @click="showRecordDialog()">
      <img src="../../asset/ui/icon_record.png" alt />
      <p>Record</p>
    </div>

    <div class="claim-btn" @click="showRewardDialog()">
      <img src="../../asset/ui/reward-icon.gif" alt />
      <p>Prize</p>
    </div>

    <!-- <div class="dungeon-1 dungeon-btn" v-if="0">
      <img src="../../asset/ui/dungeon-2.png" alt />
      <div class="dungeon-title">Coming soon</div>
    </div>-->
    <div class="dungeon-2 dungeon-btn">
      <!-- <img src="../../asset/ui/dungeon-3.png" alt /> -->
      <div class="dungeon-title">Coming soon</div>
    </div>
    <div class="dungeon-3 dungeon-btn">
      <!-- <img src="../../asset/ui/dungeon-4.png" alt /> -->
      <div class="dungeon-title">Coming soon</div>
    </div>
    <div class="dungeon-4 dungeon-btn">
      <!-- <img src="../../asset/ui/dungeon-5.png" alt /> -->
      <div class="dungeon-title">Coming soon</div>
    </div>
    <div class="dungeon-5 dungeon-btn" @click="dungeonDialogVisiable = true">
      <!-- <img src="../../asset/ui/dungeon-6.png" alt /> -->
      <div class="dungeon-title">{{ $t("dungeon.titles")[0] }}</div>
    </div>

    <div class="dungeon-dialog center" v-if="dungeonDialogVisiable">
      <div class="dialog-content center">
        <img
          src="../../asset/ui/common_return.png"
          alt
          class="dialog-close"
          @click="dungeonDialogVisiable = false"
        />
        <div class="dialog-title">
          <p class="center">{{ $t("dungeon.titles")[0] }}</p>
        </div>

        <div class="dialog-main">
          <ul class="boss-list">
            <li v-for="(item, index) in 10" :key="index">
              <div class="center">
                <div class="icon">
                  <img
                    :src="
                      'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/dungeon/icon_' +
                      (index + 1) +
                      '.png'
                    "
                    alt
                  />
                </div>

                <div class="right-part">
                  <div class="power-value">
                    <div class="lv">LV</div>
                    <span>{{ item }}</span>
                  </div>
                  <!-- <div
                    class="start-btn template-btn4"
                    v-show="item < 4"
                    @click="enterDungeonByLevel(item)"
                  >
                    Join
                  </div> -->

                  <div class="start-btn template-btn4" v-show="item < 4">
                    Upgrading
                  </div>

                  <div class="start-btn template-btn5" v-show="item > 3">
                    Not open
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <div class="select-dialog" v-if="selectDialogVisiable">
      <img
        src="../../asset/ui/common_return.png"
        alt
        class="dialog-close"
        @click="
          (selectDialogVisiable = false),
            $store.dispatch('ASET_WALLETVISIABLE', false)
        "
      />
      <div class="dragon-attr" v-if="activeDragon">
        <div>
          <img src="../../asset/ui/combat.png" class="icon" alt />
          <p>{{ activeDragon["attack"] }}</p>
        </div>
        <div>
          <img src="../../asset/ui/defense.png" class="icon" alt />
          <p>{{ activeDragon["defense"] }}</p>
        </div>
        <div>
          <img src="../../asset/ui/life.png" class="icon" alt />
          <p>{{ activeDragon["life"] }}</p>
        </div>
      </div>

      <div class="ready-member">
        <img
          v-if="activeDragon"
          :src="activeDragon['img']"
          alt
          class="dragon"
        />
      </div>
      <div
        v-if="activeDragon && unApproveToken['DG']"
        class="start-btn"
        @click="toBattle"
      >
        <!-- @click="getBattleInfo" -->
        <span>Start</span>
        <i class="el-icon-loading" v-show="loadings['toBattle']"></i>

        <div class="spend-box">
          <div class="coin-nums" v-if="ticketAmount['DG'] > 0">
            <img src="../../asset/ui/dg.png" alt />
            <p>{{ ticketAmount["DG"] }}</p>
          </div>
          <div class="coin-nums" v-if="ticketAmount['DK'] > 0">
            <img src="../../asset/ui/dk.png" alt />
            <p>{{ ticketAmount["DK"] }}</p>
          </div>
        </div>
      </div>

      <div
        v-if="!unApproveToken['DG']"
        class="start-btn"
        @click="approveToken('DG', dragonAdventureAddress, 'approveTokenDG')"
      >
        <span>{{ $t("common.approve") }}DG</span>
        <i class="el-icon-loading" v-if="loadings['approveTokenDG']"></i>
      </div>

      <div class="dragons-content">
        <p class="desc">{{ $t("dungeon.choseTips") }}</p>
        <div class="challenge-num">
          <p>Challenges</p>
          <p class="num center" v-if="activeDragon">
            {{ dragonsRemainNumbers[activeDragon["id"]] }}
          </p>
        </div>
        <ul class="dragons-list">
          <li
            v-for="(item, index) in dragonData"
            :key="index"
            @click="activeDragon = item"
            v-show="
              (item.level > battleLevel - 1 && activeAttr === 0) ||
              (item.level > battleLevel - 1 && activeAttr === item.element)
            "
          >
            <div
              class="dragon-item"
              :style="{
                backgroundImage:
                  'url(' +
                  'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/rarity/' +
                  item.rarity +
                  '.png)',
              }"
            >
              <img
                src="../../asset/ui/box-light.png"
                alt
                class="active"
                v-if="activeDragon && activeDragon['id'] === item.id"
              />
              <img
                class="elementibute"
                :src="'../../asset/ui/element/element-' + item.element + '.png'"
                alt
              />
              <p class="level">Lv.{{ item.level }}</p>
              <img :src="item.img" alt class="dragon center" />

              <img
                class="quality"
                :src="
                  'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/rarity/rarity_' +
                  item.rarity +
                  '.png'
                "
                alt
              />

              <p class="index">{{ $t("elements")[item.element] }}</p>
            </div>
          </li>
        </ul>

        <div class="quality-bar">
          <div :class="{ active: activeAttr === 0 }" @click="activeAttr = 0">
            <img src="../../asset/ui/elements.png" class="center" alt />
          </div>
          <div :class="{ active: activeAttr === 1 }" @click="activeAttr = 1">
            <img src="../../asset/ui/element/element-1.png" alt />
          </div>
          <div :class="{ active: activeAttr === 2 }" @click="activeAttr = 2">
            <img src="../../asset/ui/element/element-2.png" alt />
          </div>
          <div :class="{ active: activeAttr === 3 }" @click="activeAttr = 3">
            <img src="../../asset/ui/element/element-3.png" alt />
          </div>
          <div :class="{ active: activeAttr === 4 }" @click="activeAttr = 4">
            <img src="../../asset/ui/element/element-4.png" alt />
          </div>
          <div
            v-if="false"
            :class="{ active: activeAttr === 5 }"
            @click="activeAttr = 5"
          >
            <img src="../../asset/ui/element/element-5.png" alt />
          </div>
        </div>
      </div>

      <div class="round-button" @click="detailAttrDialogVisiable = true">
        <img src="../../asset/svg/info.svg" alt="" class="center" />
        <p>{{ $t("dungeon.detail") }}</p>
      </div>

      <div class="tip-btn" v-if="0" @click="tipDialogVisiable = true">
        <img src="../../asset/ui/tip-icon.png" alt />
        <p>Tip</p>
      </div>

      <div class="tip-dialog" v-if="tipDialogVisiable">
        <div class="tip-dialog-content center">
          <div class="tip-title">Tips</div>
          <div class="tip-content">
            <p></p>
          </div>
        </div>
        <div class="tip-mask center" @click="tipDialogVisiable = false"></div>
      </div>
    </div>
    <div class="property-dialog" v-show="detailAttrDialogVisiable">
      <div class="content">
        <img
          src="../../asset/svg/close.svg"
          class="close"
          @click="detailAttrDialogVisiable = false"
          alt=""
        />

        <div class="attributes-list" v-if="activeDragon">
          <div class="attr-item">
            <div>
              <img src="../../asset/svg/strength.svg" alt="" />
              <span v-text="$t('dragon.strength')"></span>
            </div>
            <div class="value">{{ activeDragon["strength"] }}</div>
          </div>

          <div class="attr-item">
            <div>
              <img src="../../asset/svg/agility.svg" alt="" />
              <span v-text="$t('dragon.agility')"></span>
            </div>
            <div class="value">{{ activeDragon["agility"] }}</div>
          </div>

          <div class="attr-item">
            <div>
              <img src="../../asset/svg/constitution.svg" alt="" />
              <span v-text="$t('dragon.constitution')"></span>
            </div>
            <div class="value">{{ activeDragon["constitution"] }}</div>
          </div>

          <div class="attr-item">
            <div>
              <img src="../../asset/svg/intelligence.svg" alt="" />
              <span v-text="$t('dragon.intelligence')"></span>
            </div>
            <div class="value">{{ activeDragon["intelligence"] }}</div>
          </div>

          <div class="attr-item">
            <div>
              <img src="../../asset/svg/willpower.svg" alt="" />
              <span v-text="$t('dragon.willpower')"></span>
            </div>

            <div class="value">{{ activeDragon["willpower"] }}</div>
          </div>

          <div class="attr-item">
            <div>
              <img src="../../asset/svg/spirit.svg" alt="" />
              <span v-text="$t('dragon.spirit')"></span>
            </div>

            <div class="value">{{ activeDragon["spirit"] }}</div>
          </div>
        </div>
      </div>
    </div>

    <div class="record-dialog center" v-show="recordDialogVisiable">
      <img
        src="../../asset/ui/common_return.png"
        alt
        class="return"
        @click="recordDialogVisiable = false"
      />

      <div class="record-title">Record</div>

      <div class="record-content">
        <div v-for="(item, index) in userDungeonRecords" :key="index">
          <div class="battle-result">
            <p v-if="item.status" class="victory">VICTORY</p>
            <p v-if="!item.status" class="defeat">DEFEAT</p>
          </div>
          <img :src="item['dragon']['img']" alt class="dragon" />
          <div class="item-top">
            <div class="dungeon">
              <div class="name">
                {{ $t("dungeon.titles.0") }}
                <div class="level">{{ item.level }}</div>
              </div>
            </div>
          </div>

          <div class="battle-info">
            <div class="monster">
              <div
                class="part-item"
                v-for="(monster, i) in item.monsters"
                :key="i"
              >
                <img :src="monster.img" alt />
              </div>
            </div>
            <img src="../../asset/ui/pic_fringe.png" alt class="line" />
            <div class="reward-list">
              <div
                class="reward-item"
                v-if="Number(item['reward']['dgAmount'] / 10 ** 18).toFixed(0)"
              >
                <img class="center" src="../../asset/ui/dg.png" alt />
                <p>
                  {{ Number(item["reward"]["dgAmount"] / 10 ** 18).toFixed(0) }}
                </p>
              </div>
              <div
                class="reward-item"
                v-if="Number(item['reward']['dkAmount'] / 10 ** 18) > 0"
              >
                <img class="center" src="../../asset/ui/dk.png" alt />
                <p>{{ Number(item["reward"]["dkAmount"] / 10 ** 18) }}</p>
              </div>
              <div class="reward-item" v-if="item['reward']['props'] > 0">
                <img
                  class="center"
                  src="https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/key/key-1.png"
                  alt
                />
                <p>{{ item["reward"]["props"] }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="claim-dialog" v-if="claimDialogVisiable">
      <div class="dialog-content">
        <div class="claim-items">
          <div class="claim-item">
            <img src="../../asset/ui/dg.png" alt />
            <p>
              {{ Number(adventurePrize["dgAmount"] / 10 ** 18).toFixed(0) }}
            </p>
          </div>
          <div class="claim-item">
            <img src="../../asset/ui/dk.png" alt />
            <p>
              {{ Number(adventurePrize["dkAmount"] / 10 ** 18).toFixed(3) }}
            </p>
          </div>
          <div class="claim-item">
            <img
              src="https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/key/key-1.png"
              alt
            />
            <p>{{ adventurePrize["props"] }}</p>
          </div>
        </div>
        <div class="claim-btns" @click="claimRewards">
          Claim
          <i class="el-icon-loading" v-show="loadings['claimRewards']"></i>
        </div>
      </div>
      <div
        class="claim-dialog-modal"
        @click="
          claimDialogVisiable = false;
          $store.dispatch('ASET_WALLETVISIABLE', false);
        "
      ></div>
    </div>

    <battleView
      v-if="battleViewVisiable"
      :dragon="activeDragon"
      :battleLevel="battleLevel"
      :battleInfo="battleInfo"
      :battleRewards="battleRewards"
    ></battleView>
  </div>
</template>

<script>
import store from "@/store/index";
const path = store.state.contractPath;
const abiJsons = require("../../contracts/deployments." + path + ".json");
const tokensJsons = require("../../contracts/tokens." + path + ".json");
const erc20AbiJosn = require("../../contracts/erc20.json");

import Util from "../../utils/util";
import battleView from "./BattleView.vue";
export default {
  name: "Dungeon",
  components: {
    battleView,
  },
  data() {
    return {
      dungeonDialogVisiable: false,
      selectDialogVisiable: false,
      battleViewVisiable: false,
      detailAttrDialogVisiable: false,
      tipDialogVisiable: false,
      recordDialogVisiable: false,
      claimDialogVisiable: false,
      activeAttr: 0,
      dragonData: {},
      activeDragon: null,
      battleLevel: 0,
      dragonsRemainNumbers: {},
      battleLifeValueData: [100],
      loadings: {},
      txs: {},
      monstersData: [],
      remainHp: {},
      battleReward: null,
      battleInfo: [],

      tokenContracts: {},
      tokens: ["DG", "DK"],
      unApproveToken: {},

      ticketAmount: {
        DG: 0,
        DK: 0,
      },

      userDungeonRecords: [],
      battleRewards: {},
      adventurePrize: {
        dgAmount: 0,
        dkAmount: 0,
        props: 0,
      },
    };
  },

  mounted() {
    this.init();
    this.$store.dispatch("ASET_WALLETVISIABLE", false);
  },

  watch: {
    web3(val1, val2) {
      if (this.web3.web3Instance) {
        this.init();
      }
    },
  },

  computed: {
    web3() {
      return this.$store.state.web3;
    },
    env() {
      return this.$store.state.contractPath;
    },
  },

  methods: {
    init() {
      // if (this.env != "testnet") {
      //   return;
      // }
      if (!this.web3 || !this.web3.web3Instance) {
        return;
      }
      let web3 = this.web3.web3Instance();
      let that = this;
    
      this.dragonAddress = abiJsons["Dragon"]["address"];
      this.dragonContract = new web3.eth.Contract(
        abiJsons["Dragon"]["abi"],
        this.dragonAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.dragonAdventureAddress = abiJsons["DragonAdventure"]["address"];
      this.dragonAdventureContract = new web3.eth.Contract(
        abiJsons["DragonAdventure"]["abi"],
        this.dragonAdventureAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.dragonAdventureContract1 = new web3.eth.Contract(
        abiJsons["DragonAdventure"]["abi"],
        abiJsons["DragonAdventure"]["address1"],
        {
          from: this.web3.coinbase,
        }
      );

      for (let i in this.tokens) {
        this.tokenContracts[this.tokens[i]] = new web3.eth.Contract(
          erc20AbiJosn,
          tokensJsons[this.tokens[i]]["address"],
          {
            from: this.web3.coinbase,
          }
        );
      }

      this.adventurePrizeAddress = abiJsons["AdventurePrize"]["address"];
      this.adventurePrizeContract = new web3.eth.Contract(
        abiJsons["AdventurePrize"]["abi"],
        this.adventurePrizeAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.getInfo();
    },

    showRewardDialog() {
      this.$store.dispatch("ASET_WALLETVISIABLE", true);
      this.claimDialogVisiable = true;
      this.getRewardInfo();
    },
    showRecordDialog() {
      this.recordDialogVisiable = true;
      this.getRecordInfo();
    },
    getInfo() {
      this.getDragonInfo();
      this.getTokenAllowance("DK", this.dragonAdventureAddress);
      this.getTokenAllowance("DG", this.dragonAdventureAddress);
      this.getRecordInfo();
      this.getRewardInfo();
    },

    getTokenAllowance(token, address) {
      let that = this;
      if (!this.tokenContracts[token]) {
        
        return;
      }
      this.tokenContracts[token].methods
        .allowance(this.web3.coinbase, address)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          if (res > 0) {
            res = true;
          } else {
            res = false;
          }

          that.$set(that.unApproveToken, token, res);
          
        });
    },

    getFee(level) {
      this.dragonAdventureContract.methods
        .getFee(level)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          let _dgAmount = (res[0] / Math.pow(10, 18)).toFixed(0);
          let _dkAmount = (res[1] / Math.pow(10, 18)).toFixed(0);

          this.$set(this.ticketAmount, "DG", _dgAmount);
          this.$set(this.ticketAmount, "DK", _dkAmount);
        });
    },

    getDragonInfo() {
      this.dragonContract.methods
        .balanceOf(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          if (!Number(res)) {
            return;
          }
          this.dragonContract.methods
            .tokensOf(this.web3.coinbase, 0, res)
            .call({ from: this.$store.state.contractFrom })
            .then((res) => {
              let dragonList = [];
              for (let i in res) {
                let _info = Util.getDragonInfo(res[i]);
                this.getDragonRemainNumber(_info["id"]);
                this.getDragonDetailInfo(_info, (info) => {
                  dragonList.push(info);
                  if (dragonList.length === res.length) {
                    dragonList.sort(function (a, b) {
                      return a.type - b.type >= 0 ? -1 : 1;
                    });
                    if (!this.activeDragon) {
                      this.activeDragon = dragonList[0];
                    }
                    this.dragonData = dragonList;
                  }
                });
              }
            });
        });
    },
    getRecordInfo() {
      var that = this;
      this.dragonAdventureContract.methods
        .getUserRecordLen(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          if (!Number(res)) {
            return;
          }
          this.dragonAdventureContract.methods
            .getUserRecords(this.web3.coinbase, 0, res)
            .call({ from: this.$store.state.contractFrom })
            .then((res) => {
              let _userDungeonRecords = [];
              for (let i in res) {
                if (i < 100) {
                  let _info = Util.getDungeonRecord(res[i]);
                  _userDungeonRecords.push(_info);
                }
              }
              _userDungeonRecords.reverse();

              that.userDungeonRecords = _userDungeonRecords;

              this.dragonAdventureContract1.methods
                .getUserRecords(this.web3.coinbase, 0, 0)
                .call({ from: this.$store.state.contractFrom })
                .then((res) => {
                  let _userDungeonRecords1 = [];
                  for (let i in res) {
                    let _info = Util.getDungeonRecord(res[i]);
                    _userDungeonRecords1.push(_info);
                  }
                  _userDungeonRecords1.reverse();
                  that.userDungeonRecords =
                    _userDungeonRecords.concat(_userDungeonRecords1);
                });
            });
        });
    },

    getRewardInfo() {
      let that = this;
      this.adventurePrizeContract.methods
        .userRewards(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          for (let i in res) {
            if (!Number(i)) {
              this.$set(this.adventurePrize, i, res[i]);
            }
          }
        });
    },

    getDragonDetailInfo(info, cb) {
      let that = this;
      this.dragonContract.methods
        .getDragonInfo(info.id)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          let _power = 0;
          for (let key in res) {
            if (!Number(key)) {
              _power = _power + Number(res[key]);
            }
            that.$set(info, key == "spririt" ? "spirit" : key, res[key]);
          }

          cb(Util.getDragonDetailAttr(info));
        });
    },

    approveToken(token, address, loading) {
      if (this.loadings[loading]) {
        return;
      }
      var that = this;
      that.$set(that.loadings, loading, true);
      this.tokenContracts[token].methods
        .approve(address, Util.infinity)
        .send({
          gasPrice: this.web3.gasPrice,
        })
        .on("transactionHash", function (hash) {
          
        })
        .on("receipt", function (receipt) {
          if (that.txs[receipt.transactionHash]) {
            return;
          }
          that.txs[receipt.transactionHash] = receipt.transactionHash;
          that.$set(that.loadings, loading, false);
          that.getTokenAllowance(token, address);
        })
        .on("confirmation", function (confirmationNumber, receipt) {
          
        })
        .on("error", function (err) {
          that.$message({
            message: Util.errMsg(err),
            type: "error",
            center: true,
          });
          that.$set(that.loadings, loading, false);
          
        });
    },

    enterDungeonByLevel(item) {
      this.battleLevel = item;
      this.activeDragon = null;
      this.getFee(item);
      this.selectDialogVisiable = true;
      this.$store.dispatch("ASET_WALLETVISIABLE", true);
    },

    toBattle() {
      let that = this;
      let level = this.battleLevel;
      let dragonId = this.activeDragon["id"];
      let loading = "toBattle";
      if (this.loadings[loading]) {
        return;
      }
      that.$set(that.loadings, loading, true);

      this.dragonAdventureContract.methods
        .battle(level, dragonId)
        .estimateGas({ from: this.web3.coinbase }, function (err, gas) {
          if (err) {
            that.$set(that.loadings, loading, false);
            that.$message({
              message: that.$t("common.failed") + Util.errMsg(err),
              type: "warning",
              center: true,
            });
            return;
          }

          that.dragonAdventureContract.methods
            .battle(level, dragonId)
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {
              
            })
            .on("receipt", function (receipt) {
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;
              that.$set(that.loadings, loading, true);
              
              try {
                that.renderFightEvents(receipt["events"]["FightEvent"]);
              } catch (err) {
                console.log(err);
              }
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              
            })
            .on("error", function (err) {
              that.$set(that.loadings, loading, false);
              
            });
        });
    },

    claimRewards() {
      let that = this;
      let loading = "claimRewards";
      if (this.loadings[loading]) {
        return;
      }
      var amount = this.web3.web3Instance().utils.toWei("0.002");
      that.$set(that.loadings, loading, true);
      this.adventurePrizeContract.methods.claim().estimateGas(
        {
          from: this.web3.coinbase,
          value: amount,
        },
        function (err, gas) {
          if (err) {
            that.$set(that.loadings, loading, false);
            that.$message({
              message: that.$t("common.failed") + Util.errMsg(err),
              type: "warning",
              center: true,
            });
            return;
          }

          that.adventurePrizeContract.methods
            .claim()
            .send({
              gasPrice: that.web3.gasPrice,
              value: amount,
            })
            .on("transactionHash", function (hash) {
              
            })
            .on("receipt", function (receipt) {
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;
              that.$set(that.loadings, loading, false);
              
              that.$EventBus.$emit("refreshBalance");
              that.getRewardInfo();
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              
            })
            .on("error", function (err) {
              that.$set(that.loadings, loading, false);
              
            });
        }
      );
    },

    renderFightEvents(events) {
      
      let fightEvent = events;
      let renturnValue = fightEvent["returnValues"];
      let dragonId = renturnValue["dragonId"];
      let monsters = renturnValue["monsters"];
      let seedNum = renturnValue["seedNum"];
      let rounds = renturnValue["rounds"];
      

      this.battleRewards = {
        dgAmount: (
          renturnValue["reward"]["dgAmount"] / Math.pow(10, 18)
        ).toFixed(0),
        dkAmount: renturnValue["reward"]["dkAmount"] / Math.pow(10, 18),
        props: renturnValue["reward"]["props"],
      };

      this.monstersData = monsters;

      let _monsterData = [];
      monsters.map((_monster) => {
        _monsterData.push(Util.getMonsterInfo(_monster));
      });
      this.monstersData = _monsterData;

      this.getBattleInfo(dragonId, seedNum, monsters, rounds);
    },

    getBattleInfo(dragonId, seedNum, monsters, rounds) {
      this.dragonAdventureContract.methods
        .getBattleInfo(dragonId, seedNum, this.battleLevel, monsters, rounds)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          this.battleInfo = res;
          this.getInfo();
          this.$set(this.loadings, "toBattle", false);
          this.dungeonDialogVisiable = false;
          this.selectDialogVisiable = false;
          this.$store.dispatch("ASET_WALLETVISIABLE", false);
          this.battleViewVisiable = true;
          this.$EventBus.$emit("refreshBalance");
        });
    },

    getDragonRemainNumber(id) {
      this.dragonAdventureContract.methods
        .getRemainNumber([id])
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          this.$set(this.dragonsRemainNumbers, id, res[0]);
        });
    },

    backDungeonHome() {
      this.getInfo();
      this.battleViewVisiable = false;
      this.dungeonDialogVisiable = false;
    },
  },
};
</script>

