  bottom: -10%;
  left: 50%;
  transform: translate(-50%, 0%);
}

.record-dialog {
  width: 96%;
  height: 77vh;
  background-image: url("../../asset/ui/gmwbz_bg_hs.png");
  background-size: 100% 100%;
  position: absolute;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  z-index: 4;
}

.record-dialog .return {
  position: absolute;
  right: 0;
  bottom: 3%;
  width: 50px;
}

.record-dialog .record-title {
  font-size: 40px;
  font-weight: 500;
  color: #ffffff;
  text-shadow: -0.03555em -0.03555em 0 #5a5650, 0.03555em -0.03555em 0 #5a5650,
    -0.03555em 0.03555em 0 #5a5650, 0.03555em 0.03555em 0 #5a5650;
  color: #fff;
}

.record-dialog .record-content {
  padding-top: 30px;
  width: 82%;
  margin: 4% auto;
  height: 76%;
  /* background-color: #fff; */
  overflow-y: auto;
  overflow-x: hidden;
}

.record-dialog .record-content > div {
  width: 100%;
  margin-bottom: 40px;
  background-image: url("../../asset/ui/pic_frame.png");
  background-size: 100% 100%;
  position: relative;
  box-sizing: border-box;
  padding: 20px 12px;
  background-color: rgba(63, 62, 61, 0.541);
}

.record-dialog .record-content > div .item-top {
  width: 100%;
  background-color: #fab58183;
  display: flex;
}

.record-dialog .record-content > div .battle-info {
  width: 100%;
  justify-content: space-between;
  margin-top: 12px;
}

.record-dialog .record-content > div .battle-info .reward-list {
  width: 50%;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
}

.record-dialog .record-content > div .battle-info .reward-list .reward-item {
  width: 60px;
  height: 60px;
  background-image: url("../../asset/ui/reward.png");
  background-size: 100% 100%;
  position: relative;
}

.record-dialog
  .record-content
  > div
  .battle-info
  .reward-list
  .reward-item
  img {
  width: 70%;
}

.record-dialog .record-content > div .battle-info .reward-list .reward-item p {
  font-weight: 400;
  color: #fdc54e;
  font-size: 20px;
  text-shadow: -0.02555em -0.02555em 0 #000, 0.02555em -0.02555em #000,
    -0.02555em 0.02555em 0 #000, 0.02555em 0.02555em 0 #000;
  position: absolute;
  bottom: 1%;
  right: 4%;
}

.record-dialog .record-content > div .monster > div {
  flex: 1;
}

.record-dialog .record-content > div .battle-info .monster {
  width: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
}

.record-dialog .record-content > div .battle-info .line {
  width: 100%;
}

.record-dialog .record-content > div .monster > div img {
  height: 50px;
}
.record-dialog .record-content > div .item-top .dungeon {
  width: 40%;
}

.record-dialog .record-content > div .item-top .dungeon .icon {
  height: 36px;
  width: 36px;
  background-image: url("../../asset/ui/dragon.png");
  background-size: 100% 100%;
  margin: 0 auto;
  border-radius: 50%;
  background-color: #fff;
}

.record-dialog .record-content > div .item-top .dungeon .name {
  font-size: 15px;
  font-weight: 500;
  color: #faf2eb;
  text-shadow: -0.0455em -0.0455em 0 #000, 0.0455em -0.0455em 0 #000,
    -0.0455em 0.0455em 0 #000, 0.0455em 0.0455em 0 #000;
  display: flex;
  align-items: center;
}

.record-dialog .record-content > div .item-top .dungeon .level {
  width: 30px;
  height: 30px;
  background-image: url("../../asset/ui/round-blue.png");
  background-size: 100% 100%;
  line-height: 30px;
  margin-left: 5px;
}

.record-dialog .record-content > div .battle-result {
  position: absolute;
  top: 0;
  right: 2%;
  transform: translate(0, -50%);
  width: 160px;
  /* background-color: #fff; */
  font-size: 30px;
  font-weight: 500;
}

.record-dialog .record-content > div .dragon {
  position: absolute;
  top: 0;
  left: 2%;
  transform: translate(0, -50%);
  width: 70px;
}

.record-dialog .record-content > div .victory {
  width: 100%;
  background-image: linear-gradient(to bottom, #ffb95e, rgb(250, 101, 75));
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

.record-dialog .record-content > div .defeat {
  width: 100%;
  background-image: linear-gradient(to bottom, #4d445e, #acb4de);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

.record-dialog .record-content > div .result-list {
  height: 100%;
  width: 20%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.record-dialog .record-content > div .result-list .result-item {
  width: 100%;
  height: 28%;
  background-color: #e7c7ab71;
  border: 1px solid #4d3c378c;
  border-radius: 10px;
  box-sizing: border-box;
  padding: 2% 8%;
  display: flex;
  justify-content: space-between;
}

.record-dialog .record-content > div .result-list .result-item img {
  height: 100%;
}

.claim-dialog {
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  z-index: 4;
  background-color: rgba(0, 0, 0, 0.6);
}

.claim-dialog .dialog-content {
  width: 100%;
  height: 35%;
  background-image: url("../../asset/ui/cwsx_img.png");
  background-size: 100% 100%;
  background-position: 50% 0;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 60px 5% 0;
  position: absolute;
  top: 15vh;
  z-index: 150;
}

.claim-dialog .dialog-content .claim-items {
  align-content: flex-start;
  position: relative;
  align-items: flex-start;
  display: flex;
  justify-content: space-around;
}

.claim-dialog .dialog-content .claim-item {
  width: 22%;
  padding-bottom: 28%;
  background-image: url("../../asset/ui/activity-border.png");
  background-size: 100% 100%;
  box-sizing: border-box;
  position: relative;
  margin-top: 14px;
}

.claim-dialog .dialog-content .claim-item img {
  width: 45%;
  position: absolute;
  bottom: 20%;
  left: 50%;
  transform: translate(-50%, 0);
}

.claim-dialog .dialog-content .claim-item p {
  position: absolute;
  top: 6%;
  width: 100%;
  text-align: center;
  font-size: 22px;
  font-weight: 500;
  color: #ffffff;
  text-shadow: -0.0555em -0.0555em 0 #000, 0.0555em -0.0555em 0 #000,
    -0.0555em 0.0555em 0 #000, 0.0555em 0.0555em 0 #000;
  color: #fff;
}

.claim-dialog .dialog-content .claim-btns {
  /* position: absolute; */
  position: relative;
  width: 30%;
  padding: 3% 0 5% 0;
  background-image: url("../../asset/ui/common_blue_btn.png");
  background-size: 100% 100%;
  /* left: 50%; */
  /* top: 14%; */
  /* transform: translate(-50%, 0%); */
  cursor: pointer;
  text-align: center;
  margin: 0 auto;
  margin-top: 20px;
  font-size: 22px;
}

.claim-dialog .claim-dialog-modal {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 100;
}
</style>



