<template>
  <div class="container">
    <img src="../../asset/ui/reward_bg_img.png" alt class="card-bg" />

    <div class="egg-container center" ref="eggContainer"></div>

    <div class="bottom-part" v-if="tokenAllowance[currentToken] > 0">
      <div class="summon-buttons">
        <div class="summon-1">
          <div class="price-box">
            <img src="../../asset/ui/dk.png" alt class="coin" />

            <span>1</span>
          </div>
          <div class="button" @click="toSummonDragon(1)">
            Buy
            <i class="el-icon-loading" v-show="loadings['toSummonDragon1']"></i>
          </div>
        </div>
        <div class="summon-10">
          <div class="price-box">
            <img src="../../asset/ui/dk.png" alt class="coin" />

            <span>5</span>
          </div>
          <div class="button" @click="toSummonDragon(5)">
            Buy 5
            <i class="el-icon-loading" v-show="loadings['toSummonDragon5']"></i>
          </div>
        </div>
      </div>
      <div class="summon-buttons">
        <div class="button hatch-button" @click="hatch()">
          Hatch({{ myEggNumber }})
          <i class="el-icon-loading" v-show="loadings['hatch']"></i>
        </div>
      </div>
    </div>

    <div class="bottom-part" v-if="tokenAllowance[currentToken] == 0">
      <div class="summon-buttons">
        <div
          class="template-btn2"
          @click="
            toApprovalToken(currentToken, dragonShopAddress, 'toApprovalToken')
          "
        >
          {{ $t("common.approve") }} DK
          <i class="el-icon-loading" v-show="loadings['toApprovalToken']"></i>
        </div>
      </div>
    </div>

    <div class="tip-btn" v-if="0" @click="tipDialogVisiable = true">
      <img src="../../asset/ui/tip-icon.png" alt />
      <p>Tip</p>
    </div>

    <div class="tip-dialog" v-if="tipDialogVisiable">
      <div class="tip-dialog-content center">
        <div class="tip-title">Tips</div>
        <div class="tip-content"></div>
      </div>
      <div class="tip-mask center" @click="tipDialogVisiable = false"></div>
    </div>

    <div class="tip-dialog" v-if="hatchDialogVisiable">hhhhhh</div>

    <SummonResultView
      v-if="resultDialogVisiable"
      :summonResultDragons="summonResultDragons"
    ></SummonResultView>
  </div>
</template>

<script>
import store from "@/store/index";
const path = store.state.contractPath;
const abiJsons = require("../../contracts/deployments." + path + ".json");
const tokensJsons = require("../../contracts/tokens." + path + ".json");
const erc20AbiJosn = require("../../contracts/erc20.json");

import Util from "../../utils/util";
import SummonResultView from "./SummonResult.vue";

export default {
  name: "SummonView",
  components: {
    SummonResultView,
  },
  data() {
    return {
      txs: {},
      loadings: {},
      currentToken: "DK",
      tokenContracts: {},
      tokenAllowance: {},
      resultDialogVisiable: false,
      summonResultDragons: [],
      // canvas
      stage: null,
      eggStage: null,
      width: document.documentElement.clientWidth * 2,
      height: document.documentElement.clientHeight * 2,
      eggContainerWidth: 0,
      eggContainerHeight: 0,
      scale: 0.5,
      asset: null,
      canvas: null,
      dragonShopAddress: "",
      tipDialogVisiable: false,

      hatchDialogVisiable: false,

      myEggNumber: 0,
      myEggIds: [],
    };
  },

  computed: {
    web3() {
      return this.$store.state.web3;
    },
  },
  watch: {
    web3(val1, val2) {},
  },

  mounted() {
    if (this.width > 480) {
      this.width = (document.documentElement.clientHeight / 1.78) * 2;
    }

    this.asset = this.$store.state.asset;
    this.initStage(this.$refs.resultContainer);

    this.eggContainerHeight = this.$refs.eggContainer.clientHeight;
    this.eggContainerWidth = this.$refs.eggContainer.clientWidth;

    this.initEggStage(this.$refs.eggContainer);
    this.init();
  },

  methods: {
    init() {
      if (!this.web3 || !this.web3.web3Instance) {
        return;
      }
      let that = this;
      let web3 = this.web3.web3Instance();

      this.dragonShopAddress = abiJsons["DragonShop"]["address"];
      this.dragonShopContract = new web3.eth.Contract(
        abiJsons["DragonShop"]["abi"],
        this.dragonShopAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.dragonAddress = abiJsons["Dragon"]["address"];
      this.dragonContract = new web3.eth.Contract(
        abiJsons["Dragon"]["abi"],
        this.dragonAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.tokenContracts[this.currentToken] = new web3.eth.Contract(
        erc20AbiJosn,
        tokensJsons[this.currentToken]["address"],
        {
          from: that.web3.coinbase,
        }
      );

      this.getInfo();
    },

    getInfo() {
      this.$store.dispatch("ASET_WALLETVISIABLE", true);
      this.getTokenAllowance(this.currentToken, this.dragonShopAddress);

      this.getMyEggs();
    },

    getTokenAllowance(token, address) {
      let that = this;
      if (!this.tokenContracts[token]) {
        return;
      }
      this.tokenContracts[token].methods
        .allowance(this.web3.coinbase, address)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          that.$set(that.tokenAllowance, that.currentToken, res);
        });
    },

    toApprovalToken(token, address, loadings) {
      let that = this;
      if (this.loadings[loadings]) {
        return;
      }
      that.$set(that.loadings, loadings, true);
      this.tokenContracts[token].methods
        .approve(address, Util.infinity)
        .send({
          gasPrice: this.web3.gasPrice,
        })
        .on("transactionHash", function (hash) {})
        .on("receipt", function (receipt) {
          if (that.txs[receipt.transactionHash]) {
            return;
          }
          that.txs[receipt.transactionHash] = receipt.transactionHash;
          that.$set(that.loadings, loadings, false);
          that.getTokenAllowance(token, address);
          that.$message({
            message: that.$t("common.approve") + that.$t("common.success"),
            type: "success",
            center: true,
          });
        })
        .on("confirmation", function (confirmationNumber, receipt) {
          
        })
        .on("error", function (err) {
          that.$message({
            message: Util.errMsg(err),
            type: "error",
            center: true,
          });
          that.$set(that.loadings, loadings, false);
          
        });
    },

    getMyEggs() {
      this.dragonShopContract.methods
        .eggsOfOwner(this.web3.coinbase, 0, 0)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          this.myEggIds = res[0];
          var myEggNumber = 0;
          for (var i in res[1]) {
            myEggNumber += parseInt(res[1][i]);
          }
          this.myEggNumber = myEggNumber;
        });
    },
    toSummonDragon(buyNum) {
      let that = this;
      let _loadings = "toSummonDragon" + buyNum;
      if (this.loadings[_loadings]) {
        this.$message({
          message: "Calling, please wait",
          type: "info",
          center: true,
        });
        return;
      }
      that.$set(that.loadings, _loadings, true);
      this.dragonShopContract.methods
        .buy(buyNum)
        .estimateGas({ from: that.web3.coinbase }, function (err, gas) {
          if (err) {
            that.$set(that.loadings, _loadings, false);

            that.$message({
              message:
                that.$t("store.purchase") +
                that.$t("common.failed") +
                Util.errMsg(err),
              type: "error",
              center: true,
            });
            return;
          }
          that.dragonShopContract.methods
            .buy(buyNum)
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {
              that.$set(that.loadings, _loadings, true);
            })
            .on("receipt", function (receipt) {
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;

              that.$set(that.loadings, _loadings, false);
              // that.getSummonDragonsInfo(
              //   receipt["events"]["MintEvent"]["returnValues"]
              // );
              that.getInfo();
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              that.$set(that.loadings, _loadings, false);
            })
            .on("error", function (err) {
              that.$message({
                message:
                  that.$t("store.purchase") +
                  that.$t("common.failed") +
                  Util.errMsg(err),
                type: "error",
                center: true,
              });
              that.$set(that.loadings, _loadings, false);
            });
        });
    },

    hatch() {
      let that = this;
      let _loadings = "hatch";
      var eggIds = this.myEggIds;
      if (eggIds.length == 0) {
        this.$message({
          message: "Please buy egg, then hatch egg",
          type: "warning",
          center: true,
        });
        return;
      }
      if (this.loadings[_loadings]) {
        this.$message({
          message: "Calling, please wait",
          type: "info",
          center: true,
        });
        return;
      }

      that.$set(that.loadings, _loadings, true);

      this.dragonShopContract.methods
        .open(eggIds)
        .estimateGas({ from: that.web3.coinbase }, function (err, gas) {
          if (err) {
            that.$set(that.loadings, _loadings, false);

            that.$message({
              message:
                that.$t("store.hatch") +
                that.$t("common.failed") +
                Util.errMsg(err),
              type: "error",
              center: true,
            });
            return;
          }
          that.dragonShopContract.methods
            .open(eggIds)
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {
              that.$set(that.loadings, _loadings, true);
            })
            .on("receipt", function (receipt) {
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;

              that.$set(that.loadings, _loadings, false);
              
              that.getSummonDragonsInfo(
                receipt["events"]["MintEvent"]["returnValues"]
              );
              that.getInfo();
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              that.$set(that.loadings, _loadings, false);
            })
            .on("error", function (err) {
              that.$message({
                message:
                  that.$t("store.hatch") +
                  that.$t("common.failed") +
                  Util.errMsg(err),
                type: "error",
                center: true,
              });
              that.$set(that.loadings, _loadings, false);
            });
        });
    },

    getSummonDragonsInfo(returnValues) {
      let dragonIds = returnValues["dragonIds"];
      let attrs = returnValues["attrs"];

      dragonIds.map((dragonId, index) => {
        let dragon = Util.getDragonInfoWithAttr(dragonId, attrs[index]);
        this.summonResultDragons.push(dragon);
      });
      this.$EventBus.$emit("refreshBalance");

      this.resultDialogVisiable = true;
    },

    showHatchDialog() {
      this.hatchDialogVisiable = true;
    },

    // casnvas
    initStage(container) {
      this.stage = new Hilo.Stage({
        renderType: "canvas",
        width: this.width,
        height: this.height,
        scaleX: 0.5,
        scaleY: 0.5,
        container: container,
      });
      this.stage.canvas.style.left = 0;
      this.stage.canvas.style.top = 0;
      this.stage.enableDOMEvent([
        Hilo.event.POINTER_START,
        Hilo.event.POINTER_MOVE,
        Hilo.event.POINTER_END,
      ]);
      this.ticker = new Hilo.Ticker(60);
      this.ticker.addTick(this.stage);
      this.ticker.addTick(Hilo.Tween);
      this.ticker.start(true);
      // this.renderExplode();
    },

    initEggStage(container) {
      this.eggStage = new Hilo.Stage({
        renderType: "canvas",
        width: this.eggContainerWidth * 2,
        height: this.eggContainerHeight * 2,
        scaleX: 0.5,
        scaleY: 0.5,
        container: container,
      });
      this.eggStage.canvas.style.left = 0;
      this.eggStage.canvas.style.top = 0;
      this.stage.enableDOMEvent([
        Hilo.event.POINTER_START,
        Hilo.event.POINTER_MOVE,
        Hilo.event.POINTER_END,
      ]);
      this.ticker = new Hilo.Ticker(60);
      this.ticker.addTick(this.eggStage);
      this.ticker.addTick(Hilo.Tween);
      this.ticker.start(true);
      // this.renderExplode();
      this.renderEgg();
    },

    renderEgg() {
      let that = this;
      let atlas = this.asset.getEggStatic();
      let eggSprite = new Hilo.Sprite({
        id: "eggSprite",
        atlas: atlas,
      });

      eggSprite.addFrame(atlas.getSprite("sprite"));
      eggSprite.interval = 5;
      let _scale = this.eggContainerWidth / eggSprite.width;
      eggSprite.width = _scale * eggSprite.width * 5.5;
      eggSprite.height = _scale * eggSprite.height * 5.5;
      eggSprite.pivotX = eggSprite.width / 2;
      eggSprite.pivotY = eggSprite.height / 2;
      eggSprite.x = this.eggContainerWidth;
      eggSprite.y = this.eggContainerHeight;
      this.eggStage.addChild(eggSprite);
      
    },

    renderExplode() {
      let that = this;
      let atlas = this.asset.getExplodeAtlas();
      let explodeSprite = new Hilo.Sprite({
        id: "explode",
        atlas: atlas,
      });
      explodeSprite.addFrame(atlas.getSprite("sprite"));
      explodeSprite.interval = 5;
      let _scale = this.width / explodeSprite.width;
      explodeSprite.width = _scale * explodeSprite.width;
      explodeSprite.height = _scale * explodeSprite.height;
      explodeSprite.pivotX = explodeSprite.width / 2;
      explodeSprite.pivotY = explodeSprite.height / 2;
      explodeSprite.x = this.width * 0.55;
      explodeSprite.y = this.height * 0.4;
      this.stage.addChild(explodeSprite);

      // setTimeout(() => {
      //   explodeSprite.removeFromParent();
      // }, 2000);
    },
  },
};
</script>

<style scoped>
.isTop {
  z-index: 5 !important;
}
.container {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-image: url("../../asset/ui/1c1eca.jpg");
  background-size: 100% 100%;

  z-index: 2;
}

.card-bg {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bottom-part {
  width: 100%;
  height: 25%;
  position: absolute;
  bottom: 12%;
  box-sizing: border-box;
  padding: 0 10%;
  justify-content: space-around;
  z-index: 99999;
}

.bottom-part .summon-buttons {
  display: flex;
  justify-content: center;
}

.bottom-part .summon-buttons > div {
  /* background-color: rgba(255, 255, 255, 0.445); */
  width: 50%;
  height: 60%;
}

.bottom-part .summon-buttons > div {
  /* background-color: rgba(255, 255, 255, 0.445); */
  width: 50%;
  height: 60%;
}

.bottom-part .hatch-buttons > div {
  /* background-color: rgba(255, 255, 255, 0.445); */
  width: 50%;
  height: 60%;
}

.bottom-part .template-btn2 {
  height: 20% !important;
  font-size: 20px;
  max-width: 160px;
}

.bottom-part .price-box {
  width: 52%;
  box-sizing: border-box;
  padding: 6px 5%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 25px;
  font-weight: 500;
  color: #ffffff;
  text-shadow: -0.0555em -0.0555em 0 #000, 0.0555em -0.0555em 0 #000,
    -0.0555em 0.0555em 0 #000, 0.0555em 0.0555em 0 #000;
  color: #fff;
  background-image: url("../../asset/ui/inner-tab.png");
  background-size: 100% 100%;
}

.bottom-part .price-box .coin {
  height: 36px;
}

.bottom-part .button {
  font-size: 26px;
  font-weight: 500;
  color: #ffffff;
  text-shadow: -0.0444em -0.0444em 0 #000, 0.0444em -0.0444em 0 #000,
    -0.0444em 0.0444em 0 #000, 0.0444em 0.0444em 0 #000;
  color: #fff;
  margin: 10% auto 0;
  cursor: pointer;
  width: 65%;
  line-height: 60px;
  background-image: url("../../asset/ui/common_btn_confirm.png");
  background-size: 100% 100%;
}

.summon-1 .button {
  width: 65%;
  line-height: 60px;
  background-image: url("../../asset/ui/btn2.png");
}

.summon-buttons .hatch-button {
  margin-top: 5%;
  width: 50%;
  line-height: 80px;
  background-image: url("../../asset/ui/creat-btn-red.png");
  margin: 5% auto 0;
}

.egg {
  width: 10%;
}

.result-dialog {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: rgb(0, 0, 0, 0.85);
}

.result-dialog .confirm-btn {
  width: 20%;
  position: absolute;
  bottom: 14%;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 10;
}

.card-list {
  width: 100%;
  /* height: 70%; */
  margin-top: 4vh;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-content: flex-start;
  align-items: center;
  /* border:1px solid #fff; */
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
}

.card-list .dragon-card {
  width: 24%;
  padding-bottom: 34.28%;
  margin-bottom: 3vh;
  box-sizing: border-box;
  position: relative;
}

.card-list .dragon-card .card-front {
  width: 100%;
  z-index: 1;
}

.card-list .dragon-card .attr {
  position: absolute;
  z-index: 3;
  width: 25%;
  left: 6%;
  top: 3%;
}

.card-list .dragon-card .name {
  width: 100%;
  /* background-color: rgba(255, 255, 255, 0.301); */
  position: absolute;
  top: 8%;
  z-index: 2;
  font-size: 15px;
  font-weight: 500;
  color: #ffffff;
  text-shadow: -0.0555em -0.0555em 0 #000, 0.0555em -0.0555em 0 #000,
    -0.0555em 0.0555em 0 #000, 0.0555em 0.0555em 0 #000;
  color: #fff;
}

.card-list .dragon-card .dragon {
  z-index: 5;
  width: 70%;
}

.card-list .dragon-card .qua {
  height: 1em;
  position: absolute;
  bottom: 8%;
  z-index: 3;
  left: 50%;
  transform: translate(-50%, 0);
}

.card-list .dragon-card .card {
  width: 100%;
  z-index: 9;
}

.egg-container {
  width: 40%;
  height: 30%;
  /* background-color: rgba(80, 27, 45, 0.377); */
}

.egg-container .tips {
  width: 100%;
  font-size: 18px;
  color: #fff;
  position: absolute;
  bottom: 0;
  text-shadow: -0.0355em -0.0355em 0 #000, 0.0355em -0.0355em 0 #000,
    -0.0355em 0.0355em 0 #000, 0.0355em 0.0355em 0 #000;
}
</style>



