<template>
  <div class="challenge-container">
    <div class="dialog-title">Choose</div>
    <img
      src="../../asset/ui/common_return.png"
      alt
      class="dialog-close"
      @click="$parent.challengeViewVisiable = false"
    />

    <div class="ready-member">
      <img
        :src="ringRival['img']"
        :class="{ dragonRival: ringRival['type'] === 'dragon' }"
        alt
      />

      <img
        v-if="activeHero && ringRival['type'] === 'dragon'"
        :src="
          'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/bnx/hero-' +
          activeHero['type'] +
          '.png'
        "
        alt
        class="dragon"
      />
      <img
        v-if="activeHero && ringRival['type'] === 'cyber'"
        :src="activeHero['img']"
        alt
        class="dragon"
      />
    </div>

    <div class="start-btn" v-if="activeHero" @click="toFight">
      <span>Fight</span>
      <i class="el-icon-loading" v-show="loadings['toFight']"></i>
    </div>
    <div class="chose-box">
