<script>
import store from "@/store/index";
const path = store.state.contractPath;
const abiJsons = require("../../contracts/deployments." + path + ".json");
const tokensJsons = require("../../contracts/tokens." + path + ".json");
const erc20AbiJosn = require("../../contracts/erc20.json");
import Util from "../../utils/util";
export default {
  name: "challengeView",
  props: {
    ringRival: Object,
  },
  data() {
    return {
      txs: {},
      loadings: {},
      challengeViewVisiable: false,
      bnxContracts: {
        warrior: null,
        rogue: null,
        mage: null,
        ranger: null,
      },
      userDragonsData: [],
      userCyberDragonsData: [],
      dragonsRemainNumbers: {},
      activeHero: null,
    };
  },

  watch: {
    web3(val1, val2) {
      if (this.web3.web3Instance) {
        this.init();
      }
    },
  },

  computed: {
    web3() {
      return this.$store.state.web3;
    },
  },
  mounted() {
    this.init();
  },

  methods: {
    init() {
      if (!this.web3 || !this.web3.web3Instance) {
        return;
      }
      let web3 = this.web3.web3Instance();
      let that = this;

      this.dragonAddress = abiJsons["Dragon"]["address"];
      this.dragonContract = new web3.eth.Contract(
        abiJsons["Dragon"]["abi"],
        this.dragonAddress,
        {
          from: this.web3.coinbase,
        }
      );

      for (let i in this.bnxContracts) {
        let _address = abiJsons["CyberCard"][i];
        this.bnxContracts[i] = new web3.eth.Contract(
          abiJsons["CyberCard"]["abi"],
          _address,
          {
            from: this.web3.coinbase,
          }
        );
      }

      this.cyberDragonAddress = abiJsons["CyberDragon"]["address"];
      this.cyberDragonContract = new web3.eth.Contract(
        abiJsons["CyberDragon"]["abi"],
        this.cyberDragonAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.dragonWarAddress = abiJsons["DragonWar"]["address"];
      this.dragonWarContract = new web3.eth.Contract(
        abiJsons["DragonWar"]["abi"],
        this.dragonWarAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.getInfo();
    },

    getInfo() {
      if (this.ringRival.type === "dragon") {
        this.getCyberCardInfo();
      } else {
        this.getDragonInfo();
      }
    },

    getCyberCardInfo() {
      let that = this;
      that.userCyberDragonsData = [];
      for (let i in this.bnxContracts) {
        this.getCyberDragonByType(i);
      }
    },

    getCyberDragonByType(type) {
      let that = this;
      this.bnxContracts[type].methods
        .balanceOf(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          if (!Number(res)) {
            return;
          }
          for (let index = 0; index < res; index++) {
            this.bnxContracts[type].methods
              .tokenOfOwnerByIndex(this.web3.coinbase, index)
              .call({ from: this.$store.state.contractFrom })
              .then((tokenId) => {
                // cyberDragonContract
                this.getDragonRemainNumber(tokenId);

                that.cyberDragonContract.methods
                  .getPlayerInfoBySet(tokenId)
                  .call({ from: this.$store.state.contractFrom })
                  .then((res) => {
                    let _info = res;
                    let _type = 0;

                    switch (type) {
                      case "mage":
                        _type = 2;
                        break;

                      case "ranger":
                        _type = 3;
                        break;

                      case "rogue":
                        _type = 4;
                        break;

                      case "warrior":
                        _type = 5;
                        break;
                    }

                    that.$set(_info, "type", _type);
                    that.$set(_info, "profession", type);
                    that.$set(_info, "id", tokenId);
                    that.userCyberDragonsData.push(_info);
                  });
              });
          }
        });
    },

    getDragonInfo() {
      this.dragonContract.methods
        .balanceOf(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          // this.myNftBalance = res;
          if (!Number(res)) {
            return;
          }
          this.dragonContract.methods
            .tokensOf(this.web3.coinbase, 0, res)
            .call({ from: this.$store.state.contractFrom })
            .then((res) => {
              let dragonList = [];
              for (let i in res) {
                let _info = Util.getDragonInfo(res[i]);
                this.getDragonRemainNumber(_info["id"]);
                this.getDragonDetailInfo(_info, (info) => {
                  dragonList.push(info);
                  if (dragonList.length === res.length) {
                    dragonList.sort(function (a, b) {
                      return a.type - b.type >= 0 ? -1 : 1;
                    });
                    if (!this.activeDragon) {
                      this.activeDragon = dragonList[0];
                    }
                    this.userDragonsData = dragonList;
                  }
                });
              }
            });
        });
    },

    getDragonRemainNumber(id) {
      this.dragonWarContract.methods
        .getRemainNumber([id])
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          this.$set(this.dragonsRemainNumbers, id, res[0]);
        });
    },

    getDragonDetailInfo(info, cb) {
      let that = this;
      this.dragonContract.methods
        .getDragonInfo(info.id)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          let _power = 0;
          for (let key in res) {
            if (!Number(key)) {
              _power = _power + Number(res[key]);
            }
            that.$set(info, key == "spririt" ? "spirit" : key, res[key]);
          }

          cb(Util.getDragonDetailAttr(info));
        });
    },

    toFight() {
      let that = this;
      let loading = "toFight";
      if (this.loadings[loading]) {
        return;
      }
      that.$set(that.loadings, loading, true);
      let nftAddress = "";
      let homeId = this.ringRival["id"];
      let tokenId = this.activeHero["id"];
      if (this.ringRival.type === "dragon") {
        nftAddress = abiJsons["CyberCard"][this.activeHero["profession"]];
      } else {
        nftAddress = this.dragonAddress;
      }

      this.dragonWarContract.methods
        .fight(homeId, nftAddress, tokenId)
        .estimateGas({ from: this.web3.coinbase }, function (err, gas) {
          if (err) {
            that.$set(that.loadings, loading, false);
            that.$message({
              message: that.$t("common.failed") + Util.errMsg(err),
              type: "warning",
              center: true,
            });
            return;
          }

          that.dragonWarContract.methods
            .fight(homeId, nftAddress, tokenId)
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {})
            .on("receipt", function (receipt) {
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;
              that.$set(that.loadings, loading, true);
              let fightEvent = receipt["events"]["FightEvent"];
              let renturnValue = fightEvent["returnValues"];

              that.$parent.enterBattle(
                homeId,
                renturnValue,
                that.ringRival.type
              );
            })
            .on("confirmation", function (confirmationNumber, receipt) {})
            .on("error", function (err) {
              that.$set(that.loadings, loading, false);
              // this.dragonWarContract.methods
              //   .isRing(this.ringRival["id"])
              //   .call({ from: this.$store.state.contractFrom })
              //   .then((isRing) => {

              // if (!isRing) {
              //   this.$message({
              //     message: "The ring has been challenged",
              //     type: "warning",
              //     center: true,
              //   });
              //   that.$parent.challengeViewVisiable = false;
              //   that.$parent.getInfo();
              // }
              // });
            });
        });
      // });
    },
  },
};
</script>

