<template>
  <div id="app" ref="app">
    <router-view />
  </div>
</template>

<script>
import { setup } from "./locales/index";
import Asset from "./utils/Asset";
export default {
  name: "App",
  data() {
    return {
      windowWidth: document.documentElement.clientWidth,
      windowHeight: document.documentElement.clientHeight,
      dialogVisible: false,
      asset: null,
    };
  },

  methods: {
    openMes() {
      this.$message({
        message: "Wrong address",
        type: "info",
        center: true,
        duration: 0,
      });
    },
  },
  mounted() {
    if (this.windowWidth > 480) {
      this.windowWidth = this.windowHeight / 1.78;
    }
    this.$refs.app.style.width = this.windowWidth + "px";
    this.$refs.app.style.height = this.windowHeight + "px";

    this.asset = new Asset();

    this.asset.on(
      "complete",
      function () {
        this.asset.off("complete");
        this.$store.dispatch("ASET_ASSET", this.asset);
      }.bind(this)
    );
    this.asset.load();
  },

  beforeCreate() {
    var lang = this.$route.query.lang;
    if (lang == "en-us" || lang == "en") {
      setup("en");
    }
  },
};
</script>

