<script>
import store from "@/store/index";
const path = store.state.contractPath;
const abiJsons = require("../../contracts/deployments." + path + ".json");
const tokensJsons = require("../../contracts/tokens." + path + ".json");
const erc20AbiJosn = require("../../contracts/erc20.json");
import connector from "../../utils/walletConnect";
import Util from "../../utils/util";
export default {
  name: "wallet",
  props: {
    windowWidth: Number,
    windowHeight: Number,
  },
  data() {
    return {
      walletDialogVisiable: false,
      selectDialogVisiable: false,
    };
  },

  computed: {},
  created() {
    this.$EventBus.$on("userUnLogin", () => {
      this.connectToWallet();
    });
  },

  mounted() {
    var contractPath = this.$store.state.contractPath;
    connector.init(contractPath);
    this.getWalletCache();
  },

  filters: {
    addressFilter(address) {
      let _address = address.slice(0, 4);
      _address += "****";
      _address += address.slice(address.length - 4, address.length);
      return _address;
    },
  },

  methods: {
    async connectToWallet() {
      if (!this.$store.state.web3Modal) {
        await connector.init();
      }
      connector.onConnect();
    },
    disconnectWallet() {
      this.$store.state.web3Modal.clearCachedProvider();
      connector.onDisconnect();
      this.walletDialogVisiable = false;
    },
    getWalletCache() {
      if (
        this.$store.state.web3Modal &&
        this.$store.state.web3Modal.cachedProvider
      ) {
        this.connectToWallet();
      }
    },

    checkLogin() {
      if (this.$store.state.web3.coinbase) {
        this.walletDialogVisiable = true;
      } else {
        this.connectToWallet();
      }
    },
  },
};
</script>

<style scoped>
.wallet-container {
  width: 100%;
  box-sizing: border-box;
  padding-top: 4%;
  position: absolute;
  left: 50%;
