  align-items: center;
}

.wallet-container .coin-part {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.wallet-container .coin-box {
  width: 35%;
  padding-bottom: 8%;
  /* height: 34px; */
  background-image: url("../../asset/ui/common_top.png");
  background-size: 100% 100%;
