  box-sizing: border-box;
  font-size: 24px;
  font-weight: 500;
  color: #ffffff;
  text-shadow: -0.0355em -0.0355em 0 #000, 0.0355em -0.0355em 0 #000,
    -0.0355em 0.0355em 0 #000, 0.0355em 0.0355em 0 #000;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.wallet-container .select-wallet {
  width: 54px;
  height: 54px;
  background-image: url("../../asset/ui/nav_bg.png");
  background-size: 100% 100%;
  position: relative;
  /* position: absolute;
  right: 2%;
  top: 17%; */
}

.wallet-container .select-wallet svg {
  width: 45%;
  height: 45%;
}

.wallet-container .coin-box .coin-icon {
  width: 28%;
  margin-left: -5%;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(-10%, -54%);
}

.wallet-container .coin-box p {
  top: 45%;
}

.add-btn {
  width: 28%;
  background-image: url("../../asset/ui/add-icon.png");
  background-size: 100% 100%;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(40%, -50%);
  cursor: pointer;
}

.add-btn svg {
  width: 70%;
  height: 70%;
  top: 45%;
}

.wallet-dialog {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.65);
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 99;
  width: 100%;
}

.wallet-dialog .wallet-content {
  width: 65%;
  background-image: url("../../asset/ui/small_dialog_img.png");
  background-size: 100% 100%;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -5%);
  box-sizing: border-box;
  padding: 13% 5% 5%;
}

.wallet-dialog .wallet-content .disconnect-btn {
  background-image: url("../../asset/ui/img_board.png");
  background-size: 100% 100%;
  width: 30%;
  line-height: 30px;
  margin: 4px auto;
}
.wallet-dialog .wallet-content .address {
  line-height: 60px;
  font-size: 24px;
}
.wallet-dialog .wallet-content .coin-balance {
  width: 100%;
  display: flex;
  box-sizing: border-box;
  padding: 10px 5%;
  border-radius: 10px;
  background-color: #9f978f;
  margin-bottom: 10px;
  justify-content: space-between;
  align-items: center;
}
.wallet-dialog .wallet-content .coin-balance div:nth-child(1) {
  display: flex;
  align-items: center;
}
.wallet-dialog .wallet-content .coin-balance .coin {
  height: 46px;
}

.wallet-dialog .wallet-content .coin-balance .num {
  line-height: 2;
  font-size: 22px;
  font-weight: 600;
  color: #ffffff;
  text-shadow: -0.0355em -0.0355em 0 #000, 0.0355em -0.0355em 0 #000,
    -0.0355em 0.0355em 0 #000, 0.0355em 0.0355em 0 #000;
}
.wallet-dialog .wallet-content .coin-balance .go-btn {
  padding: 12px 40px;
  background-image: url("../../asset/ui/btn2.png");
  background-size: 100% 100%;
}
.wallet-dialog .wallet-content .return {
  width: 50px;
  position: absolute;
  right: 0;
  bottom: 0;
}

.select-dialog {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.65);
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 99;
  width: 100%;
}

.select-dialog .select-content {
  width: 65%;
  background-image: url("../../asset/ui/small_dialog_img.png");
  background-size: 100% 100%;
