  width: 50px;
  position: absolute;
  right: 0;
  bottom: 0;
}

.select-dialog .select-content .wallet-part {
  width: 100%;
  height: 80px;
  background-color: rgba(46, 46, 46, 0.521);
  margin: 10px 0;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 0 4%;
  align-items: center;
}

.select-dialog .select-content .wallet-part .icon {
  width: 23%;
}

.select-dialog .select-content .wallet-part .name {
  font-weight: 500;
  color: #ffffff;
  text-shadow: -0.0355em -0.0355em 0 #000, 0.0355em -0.0355em 0 #000,
    -0.0355em 0.0355em 0 #000, 0.0355em 0.0355em 0 #000;
}

.select-dialog .select-content .wallet-part .btn-connect {
  padding: 12px 20px;
  background-image: url("../../asset/ui/btn2.png");
  background-size: 100% 100%;
}
