<template>
  <div id="backpack-container">
    <img
      @click="closeDialog"
      src="../../asset/ui/common_return.png"
      alt=""
      class="return"
    />
    <div class="backpack-content">
      <div class="title">
        <img src="../../asset/ui/backpack.png" alt="" />
        Inventory
      </div>
      <div class="main-content center">
        <div class="prop-list" v-if="activeNavIndex === 1">
          <div
            class="prop-item"
            v-for="(value, key) in propsData"
            :key="key"
            v-show="value > 0"
          >
            <img
              :src="'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/key/key-1.png'"
              alt=""
              class="icon center"
            />
            <div class="num">{{ value }}</div>
          </div>
        </div>
      </div>
      <div class="navs-list">
        <div
          class="nav-item"
          :class="{ activeNav: activeNavIndex === 1 }"
          @click="activeNavIndex = 1"
        >
          <img src="../../asset/ui/bag-nav-1.png" alt="" class="icon" />
          <p>Consumable</p>
        </div>
        <div
          class="nav-item"
          :class="{ activeNav: activeNavIndex === 2 }"
          @click="activeNavIndex = 2"
          v-if="false"
        >
          <img src="../../asset/ui/bag-nav-2.png" alt="" class="icon" />
          <p>Food</p>
        </div>
        <div
          v-if="false"
          class="nav-item"
          :class="{ activeNav: activeNavIndex === 3 }"
          @click="activeNavIndex = 3"
        >
          <img src="../../asset/ui/bag-nav-3.png" alt="" class="icon" />
          <p>Other</p>
        </div>
      </div>
    </div>

    <el-dialog
      class="backpack-dialog dialog-toptitle"
      width="90%"
      :title="$t('nav.backpack')"
      @close="closeDialog"
      :visible.sync="dialogVisible"
      :modal="false"
      v-if="0"
    >
      <el-tabs
        v-model="activeName"
        id="container-tabs"
        class="container-tabs"
        type="card"
        v-show="!$coming"
      >
        <el-tab-pane :label="$t('prop.gemstone')" name="gestone">
          <el-tabs v-model="gestoneActiveName" id="inside-tabs" type="card">
            <el-tab-pane :label="$t('prop.gemstone')" name="gemstone">
              <div class="content">
                <p class="blankTips" v-if="!gemstonesData.length">
                  {{ $t("prop.noGemstone") }}
                </p>
                <ul class="proplist list-template">
                  <li
                    v-for="(item, index) in gemstonesData"
                    :key="index"
                    @click="
                      transferDialogVisable = true;
                      currentTransfer = item;
                      currentTransferType = 'gemstone';
                    "
                  >
                    <div class="templete-item">
                      <img :src="item.img" alt />
                      <p class="name">{{ item.subType }}</p>
                    </div>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane
              :label="$t('common.fragment')"
              name="gemstone-fragment"
            >
              <div class="content">
                <p class="blankTips" v-if="!gemstonePieceData.length">
                  {{ $t("prop.noGemstoneFragment") }}
                </p>
                <ul
                  class="proplist list-template"
                  v-if="gemstonePieceData.length"
                >
                  <li v-for="(item, index) in gemstonePieceData" :key="index">
                    <div class="templete-item">
                      <img :src="item.img" alt />
                      <div class="piece-icon"></div>
                      <p class="name">
                        {{ item.subType }}{{ $t("common.fragment") }}
                      </p>
                    </div>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
          </el-tabs>
          <div class="bottom-btns">
            <div
              class="confirm-btn template-btn3"
              @click="showUpgradeDialog('gemstone')"
            >
              {{ $t("common.upgrade") }}
            </div>
            <div
              class="confirm-btn template-btn2 mergeVisiableBtn"
              @click="showMergeDialog('gemstone')"
            >
              {{ $t("common.merge") }}
            </div>
          </div>
        </el-tab-pane>

        <el-tab-pane :label="$t('prop.rune')" name="rune">
          <el-tabs v-model="runeActiveName" id="inside-tabs" type="card">
            <el-tab-pane :label="$t('prop.rune')" name="rune">
              <div class="content">
                <p class="blankTips" v-if="!runesData.length">
                  {{ $t("prop.noRune") }}
                </p>
                <ul class="proplist list-template">
                  <li
                    v-for="(item, index) in runesData"
                    :key="index"
                    @click="
                      transferDialogVisable = true;
                      currentTransfer = item;
                      currentTransferType = 'rune';
                    "
                  >
                    <div class="templete-item">
                      <img :src="item.img" alt />
                      <p class="name">{{ item.subType }}</p>
                    </div>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane :label="$t('common.fragment')" name="rune-fragment">
              <div class="content">
                <p class="blankTips" v-if="!runePieceData.length">
                  {{ $t("prop.noRuneFragment") }}
                </p>
                <ul class="proplist list-template" v-if="runePieceData.length">
                  <li v-for="(item, index) in runePieceData" :key="index">
                    <div class="templete-item">
                      <div class="piece-box">
                        <img
                          src="../../asset/ui/icon-fragment.png"
                          alt=""
                          class="piece-icon"
                        />
                        <div class="piece">
                          <img :src="item.img" alt />
                        </div>
                      </div>

                      <p class="name">
                        {{ item.subType }}{{ $t("common.fragment") }}
                      </p>
                    </div>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
          </el-tabs>

          <div class="bottom-btns">
            <div
              class="confirm-btn template-btn3"
              @click="showUpgradeDialog('rune')"
            >
              {{ $t("common.upgrade") }}
            </div>
            <div
              class="confirm-btn template-btn2 mergeVisiableBtn"
              @click="showMergeDialog('rune')"
            >
              {{ $t("common.merge") }}
            </div>
          </div>
        </el-tab-pane>

        <el-tab-pane :label="$t('prop.medal')" name="medal">
          <el-tabs v-model="medalActiveName" id="inside-tabs" type="card">
            <el-tab-pane :label="$t('prop.medal')" name="medal">
              <div class="content">
                <p class="blankTips" v-if="!medalsData.length">
                  {{ $t("prop.noMedal") }}
                </p>
                <ul class="proplist list-template" v-if="medalsData.length">
                  <li
                    v-for="(item, index) in medalsData"
                    :key="index"
                    @click="
                      transferDialogVisable = true;
                      currentTransfer = item;
                      currentTransferType = 'medal';
                    "
                  >
                    <div class="templete-item">
                      <img :src="item.img" alt />
                      <p class="name">{{ item.subType }}</p>
                    </div>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane :label="$t('common.fragment')" name="medal-fragment">
              <div class="content">
                <p class="blankTips" v-if="!medalPieceData.length">
                  {{ $t("prop.noMedalFragment") }}
                </p>
                <ul class="proplist list-template" v-if="medalPieceData.length">
                  <li v-for="(item, index) in medalPieceData" :key="index">
                    <div class="templete-item">
                      <div class="piece-box">
                        <img
                          src="../../asset/ui/icon-fragment.png"
                          alt=""
                          class="piece-icon"
                        />
                        <div class="piece">
                          <img :src="item.img" alt />
                        </div>
                      </div>

                      <p class="name">
                        {{ item.subType }}{{ $t("common.fragment") }}
                      </p>
                    </div>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
          </el-tabs>
          <div class="bottom-btns">
            <div
              class="confirm-btn template-btn3"
              @click="showUpgradeDialog('medal')"
            >
              {{ $t("common.upgrade") }}
            </div>
            <div
              class="confirm-btn template-btn2 mergeVisiableBtn"
              @click="showMergeDialog('medal')"
            >
              {{ $t("common.merge") }}
            </div>
          </div>
        </el-tab-pane>

        <el-tab-pane :label="$t('prop.food')" name="food">
          <div class="content">
            <p class="blankTips" v-if="!foodsData.length">
              {{ $t("prop.noFood") }}
            </p>
            <ul class="proplist list-template">
              <li
                v-for="(item, index) in foodsData"
                :key="index"
                @click="
                  transferDialogVisable = true;
                  currentTransfer = item;
                  currentTransferType = 'food';
                "
              >
                <div class="templete-item">
                  <img :src="item.img" alt />
                  <p class="name">{{ $t("prop.detailType." + item.typeId) }}</p>
                </div>
              </li>
            </ul>
          </div>
        </el-tab-pane>
        <!-- <el-tab-pane label="medicinal liquid" name="liquid">
          <el-tabs v-model="liquidActiveName" id="inside-tabs" type="card">
            <el-tab-pane label="Speed Potion" name="speedup-liquid">
              <div class="content">
                <ul class="proplist list-template">
                  <li v-for="(item, index) in liquidsData" :key="index">
                    <div class="templete-item">
                      <img :src="item.img" alt />
                      <p class="name">{{ item.subType }}</p>
                    </div>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="Restoration Potion" name="recovery-liquid">Restoration Potion-tab-pane>
          </el-tabs>
        </el-tab-pane>-->
      </el-tabs>

      <!-- <div class="coming-soon" v-if="$coming">
        <img src="../../asset/ui/coming-2.png" alt="" />
        <p>{{ $t("common.comesoon") }}</p>
      </div> -->
    </el-dialog>

    <div class="transferdialog" v-if="0" v-show="transferDialogVisable">
      <div class="transfer-content">
        <div class="title-bar">{{ $t("common.detail") }}</div>
        <img
          src="../../asset/ui/common_close.png"
          alt
          class="close"
          @click="
            transferDialogVisable = false;
            loadings['transfer'] = false;
            confirmTransferNft = false;
          "
        />
        <div class="main-box">
          <div class="top">
            <div class="icon-box">
              <div class="icon">
                <img
                  v-if="currentTransferType === 'food'"
                  style="width: 40px"
                  :src="currentTransfer.img"
                  alt
                />

                <img
                  v-if="currentTransferType != 'food'"
                  :src="currentTransfer.img"
                  alt
                />
              </div>
            </div>
            <div class="desc-box">
              <p>
                {{ $t("prop.type") }}
                <span v-if="currentTransferType != ''">{{
                  $t("prop." + currentTransferType)
                }}</span>
              </p>
              <p>
                {{ $t("prop.details") }}
                <span v-if="currentTransferType != ''">{{
                  $t("prop.detailType." + currentTransfer["typeId"])
                }}</span>
              </p>
              <p>
                {{ $t("prop.speedBonus") }}
                <span>{{ currentTransfer["attr"] }}</span>
              </p>

              <!-- <p
                v-for="(info, index) in currentTransfer"
                :key="index"
                v-show="
                  index !== 'id' &&
                  index !== 'img' &&
                  index !== 'padding' &&
                  index !== 'category' &&
                  index !== 'subType' &&
                  index !== 'type' &&
                  index !== 'index'
                "
              >
                {{ index }}
                <span>{{ info }}</span>
              </p>-->
            </div>
          </div>

          <div class="bottom-btns">
            <div
              class="template-btn1"
              @click="confirmTransferNft = true"
              v-show="!confirmTransferNft"
            >
              {{ $t("common.transfer") }}
            </div>
          </div>

          <div class="totransfer-box" v-show="confirmTransferNft">
            <el-input
              type="text"
              :placeholder="$t('common.transferHolder')"
              v-model="transferAddress"
              maxlength="42"
              show-word-limit
            ></el-input>
            <div class="bottom-btns">
              <div class="template-btn2" @click="toTransferNft">
                {{ $t("common.confirmTransfer") }}
                <i class="el-icon-loading" v-show="loadings['transfer']"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <el-dialog
      :modal="false"
      v-if="0"
      class="fragment-merge-dialog dialog-notitle"
      :visible.sync="mergeDialogVisible"
      width="100%"
      @close="
        mergeDialogVisible = false;
        dialogVisible = true;
      "
    >
      <div class="prop-container">
        <ul class="prop-list">
          <li
            v-for="(item, index) in 6"
            :key="index"
            :class="{ active: index === mergePropActive }"
            @click="mergePropActive = index"
          >
            <div class="gram">
              <img
                v-if="mergePropDatas[index]"
                :src="mergePropDatas[index]['img']"
                alt=""
                class="prop"
              />
              <img
                v-else
                src="../../asset/ui/light_add.png"
                alt=""
                class="add"
              />
            </div>
          </li>
        </ul>

        <!-- <div class="merge-status">
          <ul>
            <li
              v-for="(item, index) in 12"
              :key="index"
              class="merge-num"
              @click="mergePropActive = index"
            >
              {{ index }}
            </li>
          </ul>

          <div class="merge-btn">synthesis</div>
        </div> -->
        <!-- <div class="start-btn">{{ mergeName }}</div> -->
        <!-- Add item box -->
        <!-- <div class="additem-box" v-show="propPiecesVisiable">
          <ul class="list-template">
            <li
              v-for="(item, index) in mergePiecetData"
              :key="index"
              :class="{ active: index === activeItemIndex }"
              @click="
                activeItemIndex = index;
                activeItemId = item.id;
              "
              v-show="framentShowFilter(item)"
            >
              <div class="templete-item">
                <div class="piece-box">
                  <div class="piece">
                    <img v-if="item['img']" :src="item.img" alt />
                  </div>
                </div>
                <p class="num">
                  {{ $t("prop." + item.type) }}{{ $t("common.fragment") }}
                </p>
              </div>
            </li>
          </ul>
          <div class="bottom-btns">
            <div class="template-btn2" @click="propPiecesVisiable = false">
              {{ $t("common.cancel") }}
            </div>
            <div class="template-btn1 putdragon-btn" @click="addToMergePiece">
              {{ $t("common.confirm") }}
            </div>
          </div>
        </div> -->
      </div>
      <div class="merge-btn template-btn3" @click="toMergePiece">
        {{ $t("common.merge") }}
        <i class="el-icon-loading" v-show="loadings['merge']"></i>
      </div>

      <div class="fragment-container">
        <ul class="fragment-list">
          <li
            v-for="(item, index) in 12"
            :key="index"
            @click="addToMergePropData(item)"
            v-show="mergePiecetData[index + 1]"
          >
            <div class="fragment-item">
              <img
                src="../../asset/ui/icon-fragment.png"
                alt=""
                class="fragment-icon"
              />
              <img
                :src="
                  'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/' +
                  mergeType +
                  '/' +
                  (index + 1) +
                  '.png'
                "
                alt=""
                class="icon"
              />
              <p class="fragment-num" v-if="mergePiecetData[index + 1]">
                {{ mergePiecetData[index + 1].length }}
              </p>
              <div
                class="selected"
                v-if="
                  mergePiecetData[index + 1] &&
                  mergePropDatas.indexOf(mergePiecetData[index + 1][0]) > -1
                "
              >
                Selected
              </div>
            </div>
          </li>
        </ul>
      </div>
    </el-dialog>

    <el-dialog
      :modal="false"
      class="fragment-upgrade-dialog dialog-notitle"
      :visible.sync="upgradeDialogVisible"
      width="100%"
      @close="
        upgradeDialogVisible = false;
        dialogVisible = true;
      "
      v-if="0"
    >
      <div class="place-content"></div>
      <div class="member-content"></div>
    </el-dialog>
  </div>
</template>

<script>
import store from "@/store/index";
const path = store.state.contractPath;
const abiJsons = require("../../contracts/deployments." + path + ".json");
const tokensJsons = require("../../contracts/tokens." + path + ".json");
const erc20AbiJosn = require("../../contracts/erc20.json");
const Config = require("../../data/config.json");
import Util from "../../utils/util";

export default {
  name: "backpackView",
  props: {
    closeFunc: Function,
  },
  data() {
    return {
      activeName: "gestone",
      eggActiveName: "egg-egg",
      activeNavIndex: 1,
      confirmTransferNft: false,
      dialogVisible: true,
      transferImg:
        "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/dragons/dragon1.png",
      dragonEggData: {},
      dragonData: {},
      dragonPropsData: {},
      unApprove: false,
      mergePropActive: 0,

      liquidActiveName: "speedup-liquid",
      gestoneActiveName: "gemstone-fragment",
      runeActiveName: "rune",
      medalActiveName: "medal",

      eggGenesisNum: null,
      tokenContracts: {},
      myAllowances: {},
      currentTransfer: {
        img: "0.png",
      },
      currentTransferType: "",
      transferAddress: "",
      transferDialogVisable: false,
      trasnferImg: null,
      transferData: {
        id: 0,
        img: "1.png",
        index: "",
        level: "",
        mintTime: "",
        padding: "",
        rarity: "",
        speed: "",
        type: "",
      },
      inputTransferLocation: "",
      openTransferBox: false,
      upgradeDialogVisible: true,
      loadings: {
        transfer: false,
      },
      txs: {},

      foodsData: [],
      liquidsData: [],
      gemstonesData: [],
      runesData: [],
      medalsData: [],
      gemstonePieceData: [],
      runePieceData: [],
      medalPieceData: [],

      mergeDialogVisible: false,
      propPiecesVisiable: false,
      mergeType: "",
      mergeNames: {
        gemstone: this.$t("prop.gemstone"),
        rune: this.$t("prop.rune"),
        medal: this.$t("prop.medal"),
      },
      mergeName: "Fragments",
      activeItemIndex: 0,
      mergePiecetData: {},
      mergeGemstoneData: [
        {
          img: "1.png",
        },
        {
          img: "1.png",
        },
        {
          img: "1.png",
        },
        {
          img: "1.png",
        },
        {
          img: "1.png",
        },
        {
          img: "1.png",
        },
      ],

      mergePropDatas: [],

      //USE
      propsData: {},
    };
  },
  methods: {
    init() {
      if (!this.web3 || !this.web3.web3Instance) {
        return;
      }
      var chainId = this.web3.networkId;
      var web3 = this.web3.web3Instance();

      this.dragonPropContract = new web3.eth.Contract(
        abiJsons["DragonProp"]["abi"],
        abiJsons["DragonProp"]["address"],
        {
          from: this.web3.coinbase,
        }
      );

      // this.propPieceMergeContract = new web3.eth.Contract(
      //   abiJsons["PropPieceMerge"]["abi"],
      //   abiJsons["PropPieceMerge"]["address"],
      //   {
      //     from: this.web3.coinbase,
      //   }
      // );
      this.getInfo();
    },

    getInfo() {
      this.getPropsInfo();
      // this.getPropPiecesInfo();
    },

    getPropsInfo() {
      let that = this;
      // that.foodsData = [];
      // that.gemstonesData = [];
      // that.runesData = [];
      // that.medalsData = [];

      // that.liquidsData = [];
      // dragonProp.getMyPieces(owner,0,0)
      this.dragonPropContract.methods
        .balanceAllOf(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          if (!Number(res.length)) {
            return;
          }
          res.map((num, index) => {
            let type = Config["backpack"][index];
            that.$set(that.propsData, type, num);
          });
        });
    },

    getPropPiecesInfo() {
      let that = this;
      that.gemstonePieceData = [];
      that.runePieceData = [];
      that.medalPieceData = [];
      this.dragonPropContract.methods
        .getMyPieces(this.web3.coinbase, 0, 0)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          let _pieces = [];
          let _allProps = res[0];
          _allProps.map((prop, i) => {
            if (Number(prop) > 0) {
              _pieces.push(i);
            }
          });
          _pieces.map((piece, index) => {
            let _propPiece = res[1][piece];
            _propPiece = Util.getPropPieceInfo(_propPiece);

            switch (_propPiece["propType"]) {
              case "101":
                that.gemstonePieceData.push(_propPiece);
                break;
              case "102":
                that.runePieceData.push(_propPiece);
                break;
              case "103":
                that.medalPieceData.push(_propPiece);
                break;
            }

            this.showMergeDialog(this.mergeType);
          });
        });
    },

    renderMergeInfo(data, type) {},

    addToMergePropData(index) {
      if (this.mergePropDatas.indexOf(this.mergePiecetData[index][0]) < 0) {
        if (this.mergePropDatas.length === 6) {
          return;
        }
        this.mergePropDatas.push(this.mergePiecetData[index][0]);
      } else {
        let _index = this.mergePropDatas.indexOf(
          this.mergePiecetData[index][0]
        );
        this.mergePropDatas.splice(_index, 1);
      }
    },

    getApproveInfo() {
      this.dragonContract.methods
        .isApprovedForAll(this.web3.coinbase, abiJsons["DragonLab"]["address"])
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          this.unApproveEggLab = res;
        });
    },

    closeDialog() {
      this.$emit("closeFunc", "backpack");
    },

    openTrasnfer(data) {
      this.transferData = data;
      this.transferImg = this.transferData.img;
      this.trasnferDialogVisible = true;
    },

    closeTransferFunc() {
      this.openTransferBox = false;
      this.trasnferDialogVisible = false;
    },

    toTransferNft() {
      let that = this;
      if (!this.transferAddress || this.transferAddress.length != 42) {
        this.$message({
          message: that.$t("common.transferAddressError"),
          type: "error",
          center: true,
        });
        return;
      }
      that.$set(that.loadings, "transfer", true);

      let _contract = this.dragonPropContract;

      _contract.methods
        .safeBatchTransferFrom(
          this.web3.coinbase,
          this.transferAddress,
          [this.currentTransfer.id],
          "0x"
        )
        .send({
          gasPrice: that.web3.gasPrice,
        })
        .on("transactionHash", function (hash) {
          
        })
        .on("receipt", function (receipt) {
          if (that.txs[receipt.transactionHash]) {
            return;
          }
          that.txs[receipt.transactionHash] = receipt.transactionHash;
          that.$set(that.loadings, "transfer", false);
          that.transferDialogVisable = false;
          that.getInfo();
          that.$message({
            message: that.$t("common.success"),
            type: "success",
            center: true,
          });
          that.transferAddress = "";
        })
        .on("confirmation", function (confirmationNumber, receipt) {
          
        })
        .on("error", function (err) {
          
          that.$message({
            message: that.$t("common.failed"),
            type: "error",
            center: true,
          });
          that.$set(that.loadings, "transfer", false);
        });
    },
    showMergeDialog(type) {
      this.mergeType = type;
      this.mergePropDatas = [];
      let _pieceData = null;
      if (type == "gemstone") {
        _pieceData = this.gemstonePieceData;
      } else if (type == "rune") {
        _pieceData = this.runePieceData;
      } else if (type == "medal") {
        _pieceData = this.medalPieceData;
      }
      this.mergePiecetData = {};
      _pieceData.map((piece) => {
        
        if (!this.mergePiecetData[piece["number"]]) {
          this.$set(this.mergePiecetData, piece["number"], []);
          this.mergePiecetData[piece["number"]].push(piece);
        } else {
          this.mergePiecetData[piece["number"]].push(piece);
        }
      });
      this.mergeName = this.mergeNames[type];
      this.propPiecesVisiable = false;
      this.mergeDialogVisible = true;
    },

    showUpgradeDialog(type) {
      this.upgradeDialogVisible = true;
    },
    framentShowFilter(item) {
      let _data = this.mergeGemstoneData.find((value, index) => {
        return (
          Number(value["typeId"]) > 0 && value["typeId"] === item["typeId"]
        );
      });
      return !_data;
    },

    toMergePiece() {
      let that = this;
      if (that.loadings["merge"]) {
        return;
      }
      if (this.mergePropDatas.length < 6) {
        this.$message({
          message: "Insufficient number of fragments",
          type: "warning",
          center: true,
        });
        return;
      }
      let _pieceTypes = [];
      let _propType = Number(this.mergePropDatas[0]["propType"]);
      this.mergePropDatas.map((item, index) => {
        _pieceTypes.push(Number(item["attr"]) + 1000);
      });
      that.$set(that.loadings, "merge", true);
      
      this.propPieceMergeContract.methods
        .merge(_propType, _pieceTypes)
        .estimateGas({ from: that.web3.coinbase }, function (err, gas) {
          
          if (err) {
            that.$set(that.loadings, "merge", false);
            that.$message({
              message: that.$t("common.merge") + that.$t("common.fragment"),
              type: "warning",
              center: true,
            });
            return;
          }
          that.propPieceMergeContract.methods
            .merge(_propType, _pieceTypes)
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {
              
            })
            .on("receipt", function (receipt) {
              
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;
              that.$set(that.loadings, "merge", false);

              that.$message({
                message:
                  that.$t("common.merge") +
                  that.$t("common.fragment") +
                  that.$t("common.success"),
                type: "success",
                center: true,
              });
              that.mergePropDatas = [];
              that.getInfo();
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              
            })
            .on("error", function (err) {
              that.$message({
                message:
                  that.$t("common.merge") +
                  that.$t("common.fragment") +
                  that.$t("common.failed"),
                type: "error",
                center: true,
              });
              that.$set(that.loadings, "merge", false);
            });
        });
    },

    addToMergePiece() {
      this.propPiecesVisiable = false;
      this.mergeGemstoneData.splice(
        this.currentAddIndex,
        1,
        this.mergePiecetData[this.activeItemIndex]
      );
    },
  },
  computed: {
    web3() {
      return this.$store.state.web3;
    },
  },

  watch: {
    web3(val1, val2) {},
  },

  mounted() {
    this.init();
    this.$store.dispatch("ASET_WALLETVISIABLE", false);
  },

  filters: {
    liquidTypeFilter(value) {
      switch (value) {
        case 0:
          return "Common potion";
        case 1:
          return "Precious potion";
      }
    },
  },
};
</script>

<style scoped>
#backpack-container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  background-image: url("../../asset/ui/backpack-bg.jpg");
  background-size: 100% 100%;
}

#backpack-container .backpack-content {
  width: 99%;
  height: 83vh;
  margin: 5vh auto 0;
  background-image: url("../../asset/ui/common_bg.png");
  background-size: 100% 100%;
  position: relative;
}

#backpack-container .return {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 60px;
}

#backpack-container .backpack-content .title {
  position: absolute;
  left: 0;
  top: 0;
  font-size: 38px;
  font-weight: 500;
  color: #ffffff;
  text-shadow: -0.0355em -0.0355em 0 #000, 0.0355em -0.0355em 0 #000,
    -0.0355em 0.0355em 0 #000, 0.0355em 0.0355em 0 #000;
  display: flex;
  align-items: center;
}

#backpack-container .backpack-content .title img {
  height: 80px;
}

#backpack-container .backpack-content .navs-list {
  width: 90%;
  position: absolute;
  bottom: 0;
  left: 2%;
  transform: translate(0%, 88%);
  display: flex;
}

#backpack-container .backpack-content .navs-list .nav-item {
  width: 90px;
  height: 70px;
  background-image: url("../../asset/ui/nav_off.png");
  background-size: 100% 100%;
  margin-right: 10px;
}

#backpack-container .backpack-content .navs-list .activeNav {
  background-image: url("../../asset/ui/nav_on.png");
}

#backpack-container .backpack-content .navs-list .nav-item .icon {
  width: 50%;
}

#backpack-container .backpack-content .navs-list .nav-item p {
  font-size: 16px;
  font-weight: 500;
  color: #443e39;
}

#backpack-container .backpack-content .main-content {
  width: 98%;
  height: 86%;
  top: 53%;
  position: absolute;
  background-image: url("../../asset/ui/bg12.png");
  background-size: 100% 100%;
  box-sizing: border-box;
  padding: 4%;
}

#backpack-container .backpack-content .main-content .prop-list {
  width: 100%;
  height: 99%;
  overflow-y: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

#backpack-container .backpack-content .main-content .prop-item {
  width: 103px;
  height: 103px;
  background-image: url("../../asset/ui/reward.png");
  background-size: 100% 100%;
  margin-bottom: 24px;
  position: relative;
}

#backpack-container .backpack-content .main-content .prop-item .icon {
  width: 55%;
}

#backpack-container .backpack-content .main-content .prop-item .num {
  position: absolute;
  right: 5px;
  bottom: 5px;
  font-size: 22px;
  font-weight: 500;
  color: #fde161;
  text-shadow: -0.0355em -0.0355em 0 #000, 0.0355em -0.0355em 0 #000,
    -0.0355em 0.0355em 0 #000, 0.0355em 0.0355em 0 #000;
}
</style>




