<template>
  <div class="hall-container">
    <img
      src="../../asset/ui/common_return.png"
      alt
      class="return"
      @click="$parent.hallViewVisiable = false"
    />
    <div class="hall-title">War Hall</div>
    <div class="filter-navs">
      <div
        class="filter-item"
        @click="showRingType = 'cyber'"
        :class="{ active: showRingType === 'cyber' }"
      >
        CyberDragon
      </div>
      <div
        class="filter-item"
        :class="{ active: showRingType === 'dragon' }"
        @click="showRingType = 'dragon'"
      >
        DragonKnight
      </div>
    </div>
    <div class="hall-content">
      <div
        class="game-item"
        v-for="(item, index) in ringsData"
        :key="index"
        v-show="filterRing(item, index)"
      >
        <div class="inner-box">
          <div class="hero-box">
            <img
              v-if="item.cyber"
              :src="
                'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/bnx/hero-' +
                item.cyber.type +
                '.png'
              "
              alt
              class="hero"
            />
            <!-- <p  v-if="item.cyber">{{item.cyber}}</p>
            <p  v-if="item.dragon">{{item.dragon}}</p>-->

            <img v-if="item.dragon" :src="item.dragon.img" alt class="hero" />
            <img src="../../asset/ui/common-base.png" alt class="hero-base" />
          </div>
          <div
            class="mine-rings"
            v-if="item.home.toLowerCase() == web3.coinbase"
          >
            My Ring
          </div>
          <div class="challenge-btn" v-else @click="showChallengeView(item)">
            Challenge
          </div>
        </div>
      </div>
    </div>
    <div class="refresh-btn" @click="getRingsInfo"></div>
    <div class="creat-btn" @click="creatRingDialogVisiable = true">
      Creat Ring
    </div>
    <div class="mine-btn side-bar" @click="mineRingDialogVisiable = true">
      <img src="../../asset/ui/mine.png" alt class="icon" />
      <p>Record</p>
    </div>

    <div class="mineRing-dialog" v-if="mineRingDialogVisiable">
      <img src="../../asset/ui/grass-top.png" alt class="mineRing-top" />
      <img
        src="../../asset/ui/common_return.png"
        alt
        class="mineRing-return"
        @click="mineRingDialogVisiable = false"
      />
      <div class="mineRing-content">
        <div class="mine-navs">
          <div
            class="nav-btn"
            @click="activeMineTab = 'record'"
            :class="{ activeNav: activeMineTab === 'record' }"
          >
            Record
          </div>
          <div
            class="nav-btn"
            @click="activeMineTab = 'ring'"
            :class="{ activeNav: activeMineTab === 'ring' }"
          >
            My Ring
          </div>
        </div>
        <div class="content-detail">
          <div class="ring-list" v-show="activeMineTab === 'ring'">
            <div
              class="ring-item"
              v-for="(item, index) in ringsData"
              :key="index"
              v-show="
                ringsData[index]['home'].toLowerCase() ===
                web3['coinbase'].toLowerCase()
              "
            >
              <div class="hero-box">
                <img
                  v-if="item.cyber"
                  :src="
                    'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/bnx/hero-' +
                    item.cyber.type +
                    '.png'
                  "
                  alt
                  class="hero"
                />
                <img
                  v-if="item.dragon"
                  :src="item.dragon.img"
                  alt
                  class="hero"
                />
                <img
                  src="../../asset/ui/common-base.png"
                  alt
                  class="hero-base"
                />
              </div>
            </div>
            <div
              class="ring-item"
              v-for="(item, index) in 2"
              :key="index + 10000"
              style="height: 0; padding: 0"
            ></div>
          </div>

          <div class="record-list" v-show="activeMineTab === 'record'">
            <div
              class="record-item"
              v-for="(item, index) in warsRecord"
              :key="index"
            >
              <div class="mine-info">
                <img
                  src="../../asset/ui/winner-icon.png"
                  v-if="item.meWin && !item.isGuest"
                  alt
                  class="result"
                />
                <img
                  src="../../asset/ui/failure-icon.png"
                  v-if="!item.meWin && !item.isGuest"
                  alt
                  class="result"
                />
                <div class="user-icon" v-if="!item.isGuest">
                  <img src="../../asset/ui/user_icon.png" alt />
                </div>
                <div
                  class="hero-box"
                  :style="`backgroundImage:url(${
                    item.dragonHome ? item.dragonImg : item.cyberImg
                  })`"
                ></div>
              </div>
              <div class="center-part">
                <img src="../../asset/ui/vs.png" alt />
                <p>
                  <span v-if="item.meWin"
                    >{{ item.dcReward | filterTokenAmount(18, 2) }}DG</span
                  >
                  <span v-else>0</span>
                </p>
              </div>
              <div class="rival-info">
                <img
                  src="../../asset/ui/winner-icon.png"
                  v-if="item.meWin && item.isGuest"
                  alt
                  class="result"
                />
                <img
                  src="../../asset/ui/failure-icon.png"
                  v-if="!item.meWin && item.isGuest"
                  alt
                  class="result"
                />
                <div class="user-icon" v-if="item.isGuest">
                  <img src="../../asset/ui/user_icon.png" alt />
                </div>
                <div
                  class="hero-box"
                  :style="`backgroundImage:url(${
                    item.dragonHome ? item.cyberImg : item.dragonImg
                  })`"
                ></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="creatRing-dialig" v-if="creatRingDialogVisiable">
      <div class="creatRing-content">
        <div class="cyber-part" v-show="activeCreatRingType === 'cyberDragon'">
          <img
            src="../../asset/ui/common_return.png"
            alt
            class="creatRing-close"
            @click="creatRingDialogVisiable = false"
          />
          <div class="creatRing-title">Cyber Ring</div>
          <div class="creat-box">
            <div
              class="attr-list"
              v-if="creatCyberRingActive && creatCyberRingActive['0']"
            >
              <div class="attr-item">
                <div>
                  <svg
                    t="1634636781869"
                    class="icon"
                    viewBox="0 0 1024 1024"
