                      p-id="3414"
                      fill="#4794E4"
                    />
                  </svg>
                  <span v-text="$t('dragon.strength')"></span>
                </div>
                <div class="value">{{ creatCyberRingActive["0"][0] }}</div>
              </div>

              <div class="attr-item">
                <div>
                  <svg
                    t="1634637225040"
                    class="icon"
                    viewBox="0 0 1024 1024"
