                  >
                    <path
                      d="M891.9 208.6C849.2 158.9 756.9 77.3 703.7 95c-20.2 6.7-17.8 5.9-14.2 74.6 2.3 44.4-3.6 63.9-67.5 99.4-45.2 25.1-88.8 7.1-105.3-22.4-18.7-33.4-75.8-62.8-97.1-52.1-21.3 10.7-21.3 159.8-42.6 255.7S306 610 254 664.9c-52 54.9-118.6 73.5-153.5 102.3-48.8 40.4-14.6 144.5 17.4 162.3 32 17.8 88.8 17.8 152.7 17.8 55.1 0 183.7-17.9 230.8-49.7 35.6-24 198.8-178.3 262.8-252.1S966.4 393.3 970 371.8c3.5-21.5-35.5-113.5-78.1-163.2zM643.1 618.2l-190 1h-0.1c-12.1 0-21.9-9.8-22-21.9-0.1-12.1 9.7-22.1 21.9-22.1l190-1h0.1c12.1 0 21.9 9.8 22 21.9 0.1 12.1-9.7 22-21.9 22.1z m92-114l-190 1h-0.1c-12.1 0-21.9-9.8-22-21.9-0.1-12.2 9.7-22.1 21.9-22.1l190-1h0.1c12.1 0 21.9 9.8 22 21.9 0.1 12.1-9.7 22-21.9 22.1zM950 894.2h-0.1l-220 1c-12.2 0.1-22 9.9-21.9 22.1 0.1 12.1 9.9 21.9 22 21.9h0.1l220-1c12.2-0.1 22-9.9 21.9-22.1-0.1-12.1-9.9-21.9-22-21.9zM830 793.2h0.2l120-1c12.2-0.1 21.9-10 21.8-22.2-0.1-12.1-9.9-21.8-22-21.8h-0.2l-120 1c-12.2 0.1-21.9 10-21.8 22.2 0.1 12 9.9 21.8 22 21.8z"
                      p-id="42851"
                      fill="#9B63DE"
                    />
                  </svg>
                  <span v-text="$t('dragon.agility')"></span>
                </div>
                <div class="value">{{ creatCyberRingActive["0"][1] }}</div>
              </div>
              <div class="attr-item">
                <div>
                  <svg
                    t="1634637399662"
                    class="icon"
                    viewBox="0 0 1024 1024"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    p-id="48099"
