                      fill="#FF4C24"
                      p-id="48101"
                    />
                  </svg>
                  <span v-text="$t('dragon.constitution')"></span>
                </div>
                <div class="value">{{ creatCyberRingActive["0"][2] }}</div>
              </div>

              <div class="attr-item">
                <div>
                  <svg
                    t="1634637485382"
                    class="icon"
                    viewBox="0 0 1024 1024"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    p-id="51199"
                    width="200"
                    height="200"
                  >
                    <path
                      d="M469.3 917.3h85.3V960h-85.3zM384 746.6h256v42.7H384zM384 832h256v42.7H384zM512 64c-164.9 0-298.6 133.7-298.6 298.6 0 119 69.9 221.5 170.7 269.5V704h256v-71.9c100.7-48 170.6-150.4 170.6-269.4C810.6 197.7 677 64 512 64z"
                      p-id="51200"
                      fill="#00F1A2"
                    />
                  </svg>
                  <span v-text="$t('dragon.intelligence')"></span>
                </div>
                <div class="value">{{ creatCyberRingActive["0"][3] }}</div>
              </div>

