                <div class="value">{{ creatCyberRingActive["0"][4] }}</div>
              </div>

              <div class="attr-item">
                <div>
                  <svg
                    t="1634637775264"
                    class="icon"
                    viewBox="0 0 1024 1024"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    p-id="5062"
                    width="200"
                    height="200"
                  >
