                  </svg>
                  <span v-text="$t('dragon.spirit')"></span>
                </div>

                <div class="value">{{ creatCyberRingActive["0"][5] }}</div>
              </div>
            </div>
            <div class="hero-box center">
              <img
                :src="
                  'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/bnx/hero-' +
                  creatCyberRingActive['type'] +
                  '.png'
                "
                alt
                class="hero"
                v-if="creatCyberRingActive && creatCyberRingActive['type']"
              />
              <img src="../../asset/ui/detail-base.png" alt class="hero-base" />
            </div>

            <div class="remaining-box">
              <p>Times</p>
              <p>{{ creatCyberRingActive["remainNumber"] }}</p>
            </div>
          </div>

          <div class="hero-content">
            <div class="heros-list">
              <div
                class="hero-item"
                @click="creatCyberRingActive = item"
                v-for="(item, index) in userCyberDragonsData"
                :key="index"
              >
                <div
                  class="active item-inner"
                  :class="{ 'active-item': creatCyberRingActive === item }"
                >
                  <div class="element">
                    {{ item.profession }}
                    <!-- <p>Ranger</p> -->
                    <!-- <img src="https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/element/element-1.png" alt=""> -->
                  </div>

                  <img
                    :src="
                      'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/bnx/hero-' +
                      item.type +
                      '.png'
                    "
                    alt
                    class="hero center"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="confirm-btn template-btn4" @click="creatCyberRing">
            Confirm
            <i class="el-icon-loading" v-show="loadings['creatCyberRing']"></i>
          </div>
        </div>

        <div class="dragon-part" v-show="activeCreatRingType === 'dragon'">
          <img
            src="../../asset/ui/common_return.png"
            alt
            class="creatRing-close"
            @click="creatRingDialogVisiable = false"
          />
          <div class="creatRing-title">Dragon Ring</div>
          <div class="creat-box">
            <div
              class="attr-list"
              v-if="creatDragonRingActive && creatDragonRingActive['rarity']"
            >
              <div class="attr-item">
                <div>
                  <svg
                    t="1634636781869"
                    class="icon"
                    viewBox="0 0 1024 1024"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    p-id="3413"
                    width="200"
                    height="200"
