            <div class="heros-list">
              <div
                class="hero-item"
                @click="creatDragonRingActive = item"
                v-for="(item, index) in userDragonsData"
                :key="index"
              >
                <div
                  class="active item-inner"
                  :class="{ 'active-item': creatDragonRingActive === item }"
                >
                  <div class="element">
                    <img
                      :src="
                        'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/element/element-' +
                        item.element +
                        '.png'
                      "
                      alt
                    />
                  </div>

                  <img :src="item.img" alt class="hero center" />
                </div>
              </div>
            </div>
          </div>
          <div
            class="confirm-btn template-btn4"
            v-if="ringsNumber < 900"
            @click="creatDragonRing"
          >
            Confirm
            <i class="el-icon-loading" v-show="loadings['creatDragonRing']"></i>
          </div>

          <div v-else class="confirm-tips">Now the ring is enough</div>
        </div>

        <div class="switch-navs">
          <div
            class="nav"
            @click="activeCreatRingType = 'dragon'"
            :class="{ active: activeCreatRingType === 'dragon' }"
          >
            DragonKnight
          </div>

          <div
            class="nav"
            @click="activeCreatRingType = 'cyberDragon'"
            :class="{ active: activeCreatRingType === 'cyberDragon' }"
          >
            CyberDragon
          </div>
        </div>
      </div>
    </div>

    <battleView v-if="battleViewVisiable" :battleInfo="battleInfo"></battleView>
    <challengeView
      :ringRival="ringRival"
      v-if="challengeViewVisiable"
    ></challengeView>
  </div>
</template>

<script>
import store from "@/store/index";
import ContractUtil from "../../utils/contractUtils";
const path = store.state.contractPath;
const abiJsons = require("../../contracts/deployments." + path + ".json");
const tokensJsons = require("../../contracts/tokens." + path + ".json");
const erc20AbiJosn = require("../../contracts/erc20.json");
import Util from "../../utils/util";
import battleView from "./BattleView.vue";
import challengeView from "./Challenge.vue";

export default {
  name: "hallView",
  components: {
    battleView,
    challengeView,
  },
  data() {
    return {
      tx: {},
      loadings: {},
      txs: {},
      activeTribe: "dragon",
      activeDragon: null,
      unApproveToken: {},
      dragonData: [],
      activeAttr: 0,
      tokens: ["DG", "DK"],
      tokenContracts: {},
      dragonsRemainNumbers: {},
      hallViewVisiable: false,
      activeMineTab: "record",
      mineRingDialogVisiable: false,
      creatRingDialogVisiable: false,
      selectDialogVisiable: false,
      selectedRingInfo: null,
      selectViewType: "",
      battleViewVisiable: false,
      challengeViewVisiable: false,
      showDragonRing: true,
      showBnxRing: true,
      activeCreatRingType: "dragon",
      bnxContracts: {
        warrior: null,
        rogue: null,
        mage: null,
        ranger: null,
      },
      userBnxCardsData: [],
      userDragonsData: [],
      userCyberDragonsData: [],
      creatCyberRingActive: {},
      creatDragonRingActive: {},
      ringsData: [],
      ringRival: null,
      warsRecord: [],
      battleInfo: null,
      ringsNumber: 0,
      dragonWarOldContract: null,
      showRingType: "cyber",
    };
  },
  watch: {
    web3(val1, val2) {
      if (this.web3.web3Instance) {
        this.init();
      }
    },
  },

  computed: {
    web3() {
      return this.$store.state.web3;
    },
  },
  mounted() {
    this.init();
    this.$store.dispatch("ASET_WALLETVISIABLE", false);
  },

  methods: {
    init() {
      if (!this.web3 || !this.web3.web3Instance) {
        return;
      }
      let web3 = this.web3.web3Instance();
      let that = this;

      this.dragonAddress = abiJsons["Dragon"]["address"];
      this.dragonContract = new web3.eth.Contract(
        abiJsons["Dragon"]["abi"],
        this.dragonAddress,
        {
          from: this.web3.coinbase,
        }
      );

      for (let i in this.bnxContracts) {
        let _address = abiJsons["CyberCard"][i];
        this.bnxContracts[i] = new web3.eth.Contract(
          abiJsons["CyberCard"]["abi"],
          _address,
          {
            from: this.web3.coinbase,
          }
        );
      }

      this.cyberDragonAddress = abiJsons["CyberDragon"]["address"];
      this.cyberDragonContract = new web3.eth.Contract(
        abiJsons["CyberDragon"]["abi"],
        this.cyberDragonAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.dragonWarAddress = abiJsons["DragonWar"]["address"];
      this.dragonWarContract = new web3.eth.Contract(
        abiJsons["DragonWar"]["abi"],
        this.dragonWarAddress,
        {
          from: this.web3.coinbase,
        }
      );
      this.dragonWarOldAddress = abiJsons["DragonWar"]["address1"];
      this.dragonWarOldContract = new web3.eth.Contract(
        abiJsons["DragonWar"]["abi"],
        this.dragonWarOldAddress,
        {
          from: this.web3.coinbase,
        }
      );
      // "warrior": "0xB384656Eb3eDaf61086b4f3a7Ec27B93d8be5691",
      // "rogue": "0xa053E04010df66cDB4b0fE097B0e999fE225B70a",
      // "mage": "0xa053E04010df66cDB4b0fE097B0e999fE225B70a",
      // "ranger": "0xa053E04010df66cDB4b0fE097B0e999fE225B70a",
      // "address": "0xB384656Eb3eDaf61086b4f3a7Ec27B93d8be5691",

      for (let i in this.tokens) {
        this.tokenContracts[this.tokens[i]] = new web3.eth.Contract(
          erc20AbiJosn,
          tokensJsons[this.tokens[i]]["address"],
          {
            from: this.web3.coinbase,
          }
        );
      }

      this.getInfo();
    },

    getInfo() {
      this.getDragonInfo();
      this.getCyberCardInfo();
      this.getRingsInfo();
      this.getUserRecords(this.dragonWarContract);
      this.getUserRecords(this.dragonWarOldContract);
    },

    getRingsInfo() {
      let that = this;
      that.ringsData = [];
      this.dragonWarContract.methods
        .getDragonRingLen()
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          that.ringsNumber = res;

          if (!Number(res)) {
            return;
          }
          this.dragonWarContract.methods
            .getDragonRings(0, res)
            .call({ from: this.$store.state.contractFrom })
            .then((res) => {
              // this.dragonWarContract.methods
              //   .getCyberIdLen()
              //   .call({ from: this.$store.state.contractFrom })
              //   .then(cyberLen => {
              // this.dragonWarContract.methods
              //   .getCyberIds(cyberLen > 50 ? cyberLen - 50 : 0, cyberLen)
              //   .call({ from: this.$store.state.contractFrom })
              //   .then(cyberIds => {
              var _res = [];
              for (var i in res) {
                // if (
                //   res[i]["home"].toLowerCase() !=
                //   "0x9fbd4F877ea2aD2543717A9C3ecEA7c22482d7c5".toLowerCase()
                // ) {
                _res.push(res[i]);
                // }
              }
              // var _cyberIds = {};
              // for (var i in cyberIds) {
              //   _cyberIds[cyberIds[i]] = cyberIds[i];
              // }

              res = _res.sort(function (a, b) {
                return Math.random() - 0.5;
              });
              // for (var i in _cyberIds) {
              //   res.push({
              //     home: "0x9fbd4F877ea2aD2543717A9C3ecEA7c22482d7c5",
              //     dragonId: 0,
              //     cyberId: _cyberIds[i],
              //     ringId: 1
              //   });
              // }
              let _indexArr = [];

              res.forEach((item) => {
                this.renderRingDetail(item);
              });
              // });
              // });
            });
        });
    },

    renderRingDetail(info) {
      let _info = info;
      let that = this;
      let _ring = {
        dragon: null,
        cyber: null,
        home: info["home"],
        ringId: info["ringId"],
      };
      if (Number(_info["dragonId"])) {
        let _dragon = Util.getDragonInfo(_info["dragonId"]);
        _ring["dragon"] = _dragon;
        _ring.id = _dragon.id;
        let result = this.ringsData.find((item) => {
          return item.id == _ring.id;
        });
        if (result) return;

        this.ringsData.push(_ring);
      }
      var tokenId = _info["cyberId"];
      if (Number(tokenId)) {
        that.cyberDragonContract.methods
          .getPlayerInfoBySet(tokenId)
          .call({ from: this.$store.state.contractFrom })
          .then((res) => {
            let _info = res;
            let type = Util.getCyberDragonType(res["1"]);

            let _type = 0;
            switch (type) {
              case "mage":
                _type = 2;
                break;

              case "ranger":
                _type = 3;
                break;

              case "rogue":
                _type = 4;
                break;

              case "warrior":
                _type = 5;
                break;
            }
            that.$set(_info, "type", _type);
            that.$set(_info, "profession", type);
            that.$set(_info, "id", tokenId);
            _ring["cyber"] = _info;
            _ring.id = _info.id;
            let result = this.ringsData.find((item) => {
              return item.id == _ring.id;
            });

            if (result) return;
            this.ringsData.push(_ring);
          });
      }
    },

    getCyberCardInfo() {
      let that = this;
      that.userCyberDragonsData = [];
      for (let i in this.bnxContracts) {
        this.getCyberDragonByType(i);
      }
    },

    getCyberDragonByType(type) {
      let that = this;
      this.bnxContracts[type].methods
        .balanceOf(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          if (!Number(res)) {
            return;
          }
          for (let index = 0; index < res; index++) {
            this.bnxContracts[type].methods
              .tokenOfOwnerByIndex(this.web3.coinbase, index)
              .call({ from: this.$store.state.contractFrom })
              .then((tokenId) => {
                // cyberDragonContract
                that.cyberDragonContract.methods
                  .getPlayerInfoBySet(tokenId)
                  .call({ from: this.$store.state.contractFrom })
                  .then((res) => {
                    let _info = res;
                    let _type = 0;

                    switch (type) {
                      case "mage":
                        _type = 2;
                        break;

                      case "ranger":
                        _type = 3;
                        break;

                      case "rogue":
                        _type = 4;
                        break;

                      case "warrior":
                        _type = 5;
                        break;
                    }

                    that.$set(_info, "type", _type);
                    that.$set(_info, "profession", type);
                    that.$set(_info, "tokenId", tokenId);
                    this.getRemainNumber(_info, _info["tokenId"]);
                    that.userCyberDragonsData.push(_info);
                  });
              });
          }
        });
    },

    getDragonInfo() {
      this.dragonContract.methods
        .balanceOf(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          // this.myNftBalance = res;
          if (!Number(res)) {
            return;
          }
          this.dragonContract.methods
            .tokensOf(this.web3.coinbase, 0, res)
            .call({ from: this.$store.state.contractFrom })
            .then((res) => {
              let dragonList = [];
              for (let i in res) {
                let _info = Util.getDragonInfo(res[i]);
                this.getRemainNumber(_info, _info["id"]);
                this.getDragonDetailInfo(_info, (info) => {
                  dragonList.push(info);
                  if (dragonList.length === res.length) {
                    dragonList.sort(function (a, b) {
                      return a.type - b.type >= 0 ? -1 : 1;
                    });
                    if (!this.activeDragon) {
                      this.activeDragon = dragonList[0];
                    }
                    this.userDragonsData = dragonList;
                  }
                });
              }
            });
        });
    },

    getDragonDetailInfo(info, cb) {
      let that = this;
      this.dragonContract.methods
        .getDragonInfo(info.id)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          let _power = 0;
          for (let key in res) {
            if (!Number(key)) {
              _power = _power + Number(res[key]);
            }
            that.$set(info, key == "spririt" ? "spirit" : key, res[key]);
          }

          cb(Util.getDragonDetailAttr(info));
        });
    },

    getUserRecords(contract) {
      ContractUtil.call(contract, "getUserRecordLen", [
        this.web3.coinbase,
      ]).then((res) => {
        ContractUtil.call(contract, "getUserRecords", [
          this.web3.coinbase,
          0,
          0,
        ]).then((res) => {
          res.forEach((item) => {
            let isGuest =
              item["guest"].toLowerCase() == this.web3.coinbase.toLowerCase();

            let recordInfo = {
              home: item.home,
              guest: item.guest,
              isGuest: isGuest,
              dragonImg: Util.getDragonInfo(item.dragonId).img,
              dragonHome: item.homeId == item.dragonId,
              dragonWin:
                (item.homeId == item.dragonId && item.status == 2) ||
                (item.dragonId != item.homeId && item.status == 1),
              meWin:
                (isGuest && item.status == 2) || (!isGuest && item.status == 1),
              dcReward: item.dcReward,
            };
            ContractUtil.call(this.cyberDragonContract, "getPlayerInfoBySet", [
              item.cyberId,
            ]).then((res) => {
              let type = Util.getCyberDragonType(res[1]);

              recordInfo = {
                ...recordInfo,
                cyberImg: `https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/bnx/hero-${type}.png`,
              };
              let result = this.warsRecord.find((item) => {
                return (
                  item.home == recordInfo.home &&
                  item.dcReward == recordInfo.dcReward &&
                  item.guest == recordInfo.guest
                );
              });
              if (result) return;
              this.warsRecord.push(recordInfo);
              this.warsRecord.sort((a, b) => b.dcReward - a.dcReward);
            });
          });
        });
      });
    },

    getRemainNumber(info, id) {
      this.dragonWarContract.methods
        .getRemainNumber([id])
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          this.$set(info, "remainNumber", res[0]);
        });
    },

    creatDragonRing() {
      let that = this;
      let tokenId = this.creatDragonRingActive["id"];
      if (!tokenId) {
        this.$message({
          message: "Please select hero first",
          type: "tip",
          center: true,
        });
        return;
      }
      let loading = "creatDragonRing";

      let _address = this.dragonAddress;
      if (this.loadings[loading]) {
        return;
      }

      that.$set(that.loadings, loading, true);

      that.dragonWarContract.methods
        .createRing(_address, tokenId)
        .estimateGas({ from: this.web3.coinbase }, function (err, gas) {
          if (err) {
            that.$set(that.loadings, loading, false);
            that.$message({
              message: that.$t("common.failed") + Util.errMsg(err),
              type: "warning",
              center: true,
            });
            return;
          }

          that.dragonWarContract.methods
            .createRing(_address, tokenId)
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {})
            .on("receipt", function (receipt) {
              if (that.txs[receipt.transactionHash]) {
                return;
              }

              that.txs[receipt.transactionHash] = receipt.transactionHash;
              that.$set(that.loadings, loading, false);
              that.creatRingDialogVisiable = false;
              that.getInfo();
              that.$message({
                message: that.$t("common.success"),
                type: "success",
                center: true,
              });
            })
            .on("confirmation", function (confirmationNumber, receipt) {})
            .on("error", function (err) {
              that.$set(that.loadings, loading, false);
            });
        });
    },

    creatCyberRing() {
      let that = this;
      let tokenId = this.creatCyberRingActive["tokenId"];
      let profession = this.creatCyberRingActive["profession"];
      if (!tokenId) {
        this.$message({
          message: "Please select hero first",
          type: "tip",
          center: true,
        });
        return;
      }
      let loading = "creatCyberRing";

      let _professionAddress = abiJsons["CyberCard"][profession];
      if (this.loadings[loading]) {
        return;
      }
      that.$set(that.loadings, loading, true);

      that.dragonWarContract.methods
        .createRing(_professionAddress, tokenId)
        .estimateGas({ from: this.web3.coinbase }, function (err, gas) {
          if (err) {
            that.$set(that.loadings, loading, false);
            that.$message({
              message: that.$t("common.failed") + Util.errMsg(err),
              type: "warning",
              center: true,
            });
            return;
          }

          that.dragonWarContract.methods
            .createRing(_professionAddress, tokenId)
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {})
            .on("receipt", function (receipt) {
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;
              that.$set(that.loadings, loading, false);

              that.getInfo();
            })
            .on("confirmation", function (confirmationNumber, receipt) {})
            .on("error", function (err) {
              that.$set(that.loadings, loading, false);
            });
        });
    },

    showChallengeView(item) {
      if (item["dragon"]) {
        this.ringRival = {
          type: "dragon",
          img: item["dragon"]["img"],
          id: item["dragon"]["id"],
        };
      } else {
        this.ringRival = {
          type: "cyber",
          img:
            "https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/bnx/hero-" +
            item["cyber"]["type"] +
            ".png",
          id: item["cyber"]["id"],
        };
      }

      this.challengeViewVisiable = true;
    },

    enterBattle(homeId, renturnValue, challenger) {
      try {
        let dragonId = renturnValue["dragonId"];
        let cyberId = renturnValue["cyberId"];
        let seedNum = renturnValue["seedNum"];
        let round = renturnValue["round"];
        let status = renturnValue["status"];
        this.getBattleInfo(
          homeId,
          dragonId,
          cyberId,
          seedNum,
          round,
          challenger,
          status
        );
      } catch (err) {}
    },

    getBattleInfo(
      homeId,
      dragonId,
      cyberId,
      seedNum,
      round,
      challenger,
      status
    ) {
      this.dragonWarContract.methods
        .getBattleInfo(homeId, dragonId, cyberId, seedNum, round)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          this.renderBattleInfo(res, challenger, status);
        });
    },

    renderBattleInfo(res, challenger, status) {
      let that = this;
      // let dragonId =
      //   "57896045346576657731129868703047550226655324535279451195552893658583726555176";
      // let cyberId = "11";
      // let remainHp = [
      //   "455",
      //   "290",
      //   "455",
      //   "290",
      //   "417",
      //   "218",
      //   "417",
      //   "141",
      //   "417",
      //   "103",
      //   "417",
      //   "103",
      //   "382",
      //   "50",
      //   "346",
      //   "50",
      //   "346",
      //   "19",
      //   "346",
      //   "0",
      //   "0",
      //   "0",
      // ];
      // let reward = Number("39000000000000000000") / Math.pow(10, 18);
      let dragonId = res["dragonId"];
      let cyberId = res["cyberId"];
      let remainHp = res["remainHp"];
      let reward = (Number(res["reward"]) / Math.pow(10, 18)).toFixed(1);

      let _dragon = Util.getDragonInfo(dragonId);

      // let challenger = "dragon";

      this.battleInfo = {
        dragon: null,
        remainHp: remainHp,
        reward: reward,
        cyber: null,
        challenger: challenger,
        status: status,
      };

      this.cyberDragonContract.methods
        .getPlayerInfoBySet(cyberId)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          let _info = res;
          let type = Util.getCyberDragonType(res["1"]);

          let _type = 0;
          switch (type) {
            case "mage":
              _type = 2;
              break;

            case "ranger":
              _type = 3;
              break;

            case "rogue":
              _type = 4;
              break;

            case "warrior":
              _type = 5;
              break;
          }
          that.$set(_info, "type", _type);
          that.$set(_info, "profession", type);
          that.$set(_info, "id", cyberId);

          _info = Util.getCyberDragonDetailAttr(_info);
          that.battleInfo["cyber"] = _info;

          that.getDragonDetailInfo(_dragon, (dragon) => {
            that.battleInfo["dragon"] = dragon;
            that.challengeViewVisiable = false;
            that.battleViewVisiable = true;
          });
        });
    },

    filterRing(item, index) {
      let isShow = false;

      if (item["dragon"] && this.showRingType === "dragon") {
        return true;
      }

      if (item["cyber"] && this.showRingType === "cyber") {
        return true;
      }

      return false;
    },
  },
  filters: {
    filterTokenAmount(value, decimals, number) {
      let result = (value * 1) / 10 ** decimals;
      // this.getDragonInfo
      return result.toFixed(number);
    },
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
};
</script>

