<script>
import store from "@/store/index";
import { Container } from "hilojs";
export default {
  name: "breakEgg",
  props: {
    summonResultDragons: Array,
  },
  data() {
    return {
      stage: null,
      width: document.documentElement.clientWidth * 2,
      height: document.documentElement.clientHeight * 2,
      scale: 0.5,
      asset: null,
      canvas: null,
      showDragon: false,
    };
  },

  mounted() {
    if (this.width > 480) {
      this.width = (document.documentElement.clientHeight / 1.78) * 2;
    }

    this.asset = this.$store.state.asset;
    this.initStage(this.$refs.resultContainer);
    this.$store.dispatch("ASET_WALLETVISIABLE", false);
  },

  methods: {
    initStage(container) {
      this.stage = new Hilo.Stage({
        renderType: "canvas",
        width: this.width,
        height: this.height,
        scaleX: 0.5,
        scaleY: 0.5,
        container: container,
      });
      this.stage.canvas.style.left = 0;
      this.stage.canvas.style.top = 0;
      this.stage.enableDOMEvent([
        Hilo.event.POINTER_START,
        Hilo.event.POINTER_MOVE,
        Hilo.event.POINTER_END,
      ]);
      this.ticker = new Hilo.Ticker(60);
      this.ticker.addTick(this.stage);
      this.ticker.addTick(Hilo.Tween);
      this.ticker.start(true);
      // this.renderExplode();

      this.renderDragon();
    },

    renderDragon() {
      for (let i = 0; i < this.summonResultDragons.length; i++) {
        this.runBreak(i);
      }
    },

    runBreak(index) {
      let that = this;
      let dragonNum = this.summonResultDragons.length;
      let breakContainer = new Hilo.Container({
        id: "breakContainer_" + index,
        width: this.width * 0.3,
        height: this.width * 0.3,
      });
      breakContainer.pivotX = breakContainer.width / 2;
      breakContainer.pivotY = breakContainer.height / 2;
      if (dragonNum > 1) {
        if (index < 3) {
          breakContainer.y = this.height * 0.4 - breakContainer.height / 2;
          if (index === 0) {
            breakContainer.x = this.width * 0.5 - breakContainer.width * 1;
          } else if (index === 1) {
            breakContainer.x = this.width * 0.5;
          } else {
            breakContainer.x = this.width * 0.5 + breakContainer.width * 1;
          }
        } else {
          breakContainer.y = this.height * 0.45 + breakContainer.height * 0.7;
          if (index === 3) {
            breakContainer.x = this.width * 0.5 - breakContainer.width * 0.76;
          } else {
            breakContainer.x = this.width * 0.5 + breakContainer.width * 0.8;
          }
        }
      } else {
        breakContainer.x = this.width * 0.5;
        breakContainer.y = this.height * 0.5;
      }

      this.stage.addChild(breakContainer);

      this.renderEgg(breakContainer, index);
    },
    renderEgg(container, index) {
      let element = this.summonResultDragons[index]["element"];
      let dragonType = this.summonResultDragons[index]["type"];
      let atlas = this.asset.getEggBreak(element);

      let eggSprite = new Hilo.Sprite({
        id: "breakSprite",
        atlas: atlas,
      });

      eggSprite.addFrame(atlas.getSprite("sprite"));
      let _scale = container.width / eggSprite.width;
      eggSprite.interval = 4;
      eggSprite.width = container.width * 2.5;
      eggSprite.height = _scale * eggSprite.height * 2.5;
      eggSprite.pivotX = eggSprite.width * 0.5;
      eggSprite.pivotY = eggSprite.height;
      eggSprite.x = container.width * 0.45;
      eggSprite.y = container.height * 1.65;
      container.addChild(eggSprite);

      setTimeout(() => {
        eggSprite.removeFromParent();
        this.renderDragonLight(container, index);
      }, 3000);
    },

    renderDragonLight(container, index) {
      let that = this;
      let lightAtlas = this.asset.getDragonLight(1);

      let lightSprite = new Hilo.Sprite({
        id: "lightSprite",
        atlas: lightAtlas,
      });

      lightSprite.addFrame(lightAtlas.getSprite("sprite"));
      let _scale = container.width / lightSprite.width;
      lightSprite.interval = 4;
      lightSprite.width = container.width * 2.6;
      lightSprite.height = _scale * lightSprite.height * 2.6;
      lightSprite.pivotX = lightSprite.width / 2;
      lightSprite.pivotY = lightSprite.height / 2;
      lightSprite.x = container.width * 0.5;
      lightSprite.y = container.height * 0.57;
      container.addChild(lightSprite);

      let lightBitmap = new Hilo.Bitmap({
        id: "light",
        image: this.asset.lastLight,
      });
      let _scaleBitmap = container.width / lightBitmap.width;
      lightBitmap.width = _scaleBitmap * lightBitmap.width * 2;
      lightBitmap.height = _scaleBitmap * lightBitmap.height * 2;
      lightBitmap.pivotX = lightBitmap.width / 2;
      lightBitmap.pivotY = lightBitmap.height / 2;
      lightBitmap.x = container.width * 0.52;
      lightBitmap.y = container.height * 0.57;

      container.addChildAt(lightBitmap, container.children.length - 2);
      this.toRoundLightBg(lightBitmap);

      setTimeout(() => {
        this.showDragon = true;
        // this.renderDragonImage(container, index);
      }, 550);

      setTimeout(() => {
        lightSprite.removeFromParent();
      }, 3000);
    },

    toRoundLightBg(lightBitmap) {
      Hilo.Tween.to(
        lightBitmap,
        {
          rotation: 360,
        },
        { duration: 5600, loop: true }
      );
    },

    renderDragonImage(container, index) {
      let dragonType = this.summonResultDragons[index]["type"];
      let dragonAtlas = this.asset.getDragonAtlas(dragonType);
      let dragonSprite = new Hilo.Sprite({
        id: "dragon",
        atlas: dragonAtlas,
      });
      dragonSprite.addFrame(dragonAtlas.getSprite("dragon"));
      dragonSprite.interval = 4;
      dragonSprite.goto("left");
      dragonSprite.width = dragonSprite.width * 1.4;
      dragonSprite.height = dragonSprite.height * 1.4;
      dragonSprite.pivotX = dragonSprite.width / 2;
      dragonSprite.pivotY = dragonSprite.height / 2;
      dragonSprite.x = container.width * 0.52;
      dragonSprite.y = container.height * 0.57;
      container.addChild(dragonSprite);
    },
  },
};
</script>

<style scoped>
.result-container {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  background-color: rgb(0, 0, 0, 0.85);
}

.confirm-btn {
