  width: 130px;
  height: 60px;
  background-image: url("../../asset/ui/yellow-btn.png");
  background-size: 100% 100%;
  line-height: 60px;
  left: 50%;
  top: 78%;
  transform: translate(-50%, 0);
  cursor: pointer;
  font-size: 17px;
  font-weight: normal;
  text-shadow: -0.0555em -0.0555em 0 #000, 0.0555em -0.0555em 0 #000,
    -0.0555em 0.0555em 0 #000, 0.0555em 0.0555em 0 #000;
  color: #fff;
}

.dragons-container {
  width: 90%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: space-around;
  z-index: 4;
  flex-wrap: wrap;
}

.dragons-container .dragon-item {
  width: 30%;
  margin-bottom: 10vh;
  padding-bottom: 30%;
  /* background: rgba(151, 82, 82, 0.404); */
  position: relative;
}

.dragons-container .dragon-item .dragon {
  width: 50%;
  z-index: 2;
  position: absolute;
  top: 36%;
  left: 50%;
  transform: translate(-50%, 0);
}

.dragons-container .dragon-item .base {
  width: 90%;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, 0);
}

.dragons-container .dragon-item .top-box {
  width: 70%;
  position: absolute;
  top: 0;
  left: 50%;
  display: flex;
  justify-content: space-around;
  transform: translate(-50%, 0);
}
.dragons-container .dragon-item .top-box img {
  height: 24px;
}

