const Config = require("../data/config.json");
export default Hilo.Class.create({
  Mixes: Hilo.EventMixin,
  queue: null,
  track: null,
  coin: null,
  light: null,
  imgHost: "",

  load() {
    this.imgHost = 'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/'
    this._url = 'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/gameimg/'
    // this.imgHost = 'http://localhost:8081/'
    // this._url = 'http://localhost:8081/static/gameimg/'

    
    let resources = [{
        id: 'track',
        src: this._url + 'track.png'
      },
      {
        id: 'coin',
        src: this._url + 'coin.png'
      },
      // {
      //   id: "landBase",
      //   src: this._url + 'landBase.png'
      // },
      // {
      //   id: "land",
      //   src: this._url + 'land.png'
      // },
      // {
      //   id: "land_0",
      //   src: this._url + '1.png'
      // },
      // {
      //   id: "land_1",
      //   src: this._url + '2.png'
      // },
      // {
      //   id: "land_2",
      //   src: this._url + '3.png'
      // },
      // {
      //   id: "land_3",
      //   src: this._url + '4.png'
      // },
      // {
      //   id: "land_4",
      //   src: this._url + '5.png'
      // },
      // {
      //   id: "land_5",
      //   src: this._url + '6.png'
      // },
      // {
      //   id: "land_6",
      //   src: this._url + '7.png'
      // },
      // {
      //   id: "land_7",
      //   src: this._url + '8.png'
      // },
      // {
      //   id: "land_8",
      //   src: this._url + '9.png'
      // },
      {
        id: "percentageBar",
        src: this._url + '01_down.png'
      },
      {
        id: "percentageInner",
        src: this._url + '02_up.png'
      },


      {
        id: "num",
        src: this.imgHost + "/static/gameimg/num.png"
      },
      {
        id: "life-num",
        src: this.imgHost + "/static/gameimg/life-num.png"
      },
      {
        id: "startBattle",
        src: this.imgHost + "/static/gameimg/start-battle.png"
      },
      {
        id: "victory",
        src: this.imgHost + "/static/gameimg/victory.png"
      },
      {
        id: "explode",
        src: this.imgHost + '/static/gameimg/animation/explode.png'
      },
      {
        id: "eggAni",
        src: this.imgHost + '/static/gameimg/animation/eggani.png'
      },
      {
        id: "miss",
        src: this.imgHost + '/static/gameimg/miss.png'
      },
      {
        id: "defeat",
        src: this.imgHost + '/static/gameimg/defeat.png'
      },
      {
        id: "dragonLight",
        src: this.imgHost + '/static/gameimg/dragonLight.png'
      },
      {
        id: "lastLight",
        src: this.imgHost + '/static/gameimg/light.png'
      },
      {
        id: "bossComing",
        src: this.imgHost + '/static/gameimg/bosscoming.png'
      }
    ];

    var dragonIds = [
      1, 2, 3, 4,
      11, 12, 13, 14,
      16, 17, 18, 19,
      21, 22, 23, 24,
      26, 27, 28, 29
    ];
    for (let i in dragonIds) {
      var dragonId = dragonIds[i];

      if (dragonId == 2 || dragonId == 21) {
        resources.push({
          id: 'tire_' + dragonId,
          src: this.imgHost + '/static/images/dragons/tire_' + dragonId + '@0.5x.png?v=0.1',
          type: "png"
        });


      } else {
        resources.push({
          id: 'tire_' + dragonId,
          src: this.imgHost + '/static/images/dragons/tire_' + dragonId + '@0.5x.png',
          type: "png"
        });
      }

      resources.push({
        id: 'dragon_' + dragonId,
        src: this.imgHost + '/static/images/dragons/dragon' + dragonId + '.png',
        type: "png"
      });
    } 

    for (let i = 1; i <= 5; i++) {
      resources.push({
        id: 'attack_' + i,
        src: this.imgHost + '/static/gameimg/attack/attack_' + i + '.png',
        type: "png"
      });

      resources.push({
        id: 'breakEgg_' + i,
        src: this.imgHost + '/static/gameimg/break-' + 1 + '.png', //TODO
        type: "png"
      });
    }

    for (let _index = 1; _index <= 5; _index++) {
      resources.push({
        id: 'bnx-hero-' + _index,
        src: this.imgHost + '/static/images/bnx/hero-' + _index + '.png',
        type: "png"
      });
    }


    for (let i = 1; i < 4; i++) {
      let monster_id = i;
      resources.push({
        id: 'monster_' + monster_id,
        src: this.imgHost + 'static/images/monster/' + monster_id + '.png',
        type: "png"
      });
    }
    this.queue = new Hilo.LoadQueue();
    this.queue.add(resources);
    this.queue.on('complete', this.onComplete.bind(this));
    this.queue.start();
  },


  onComplete() {
    this.track = this.queue.get('track').content;
    this.coin = this.queue.get('coin').content;
    // this.landBase = this.queue.get('landBase').content;
    // this.land = this.queue.get('land').content;
    this.percentageBar = this.queue.get('percentageBar').content;
    this.percentageInner = this.queue.get('percentageInner').content;

    this.num = this.queue.get("num").content;
    this.lifeNum = this.queue.get("life-num").content;
    this.startBattle = this.queue.get("startBattle").content;
    this.victory = this.queue.get("victory").content;
    this.miss = this.queue.get("miss").content;
    this.defeat = this.queue.get("defeat").content;
    this.lastLight = this.queue.get("lastLight").content;
    this.bnxHero = [];
    this.cyber_mage = this.queue.get('bnx-hero-' + 2).content;
    this.cyber_ranger = this.queue.get('bnx-hero-' + 3).content;
    this.cyber_rogue = this.queue.get('bnx-hero-' + 4).content;
    this.cyber_warrior = this.queue.get('bnx-hero-' + 5).content;
    for (let i = 1; i <= 5; i++) {
      this.bnxHero.push(this.queue.get('bnx-hero-' + i).content)
    }

    // this.landAssset = [
    //   this.queue.get('land_0').content,
    //   this.queue.get('land_1').content,
    //   this.queue.get('land_2').content,
    //   this.queue.get('land_3').content,
    //   this.queue.get('land_4').content,
    //   this.queue.get('land_5').content,
    //   this.queue.get('land_6').content,
    //   this.queue.get('land_7').content,
    //   this.queue.get('land_8').content,
    // ]
    this.queue.off('complete');
    this.fire('complete');
  },

  getDragon(dragonId) {
    return {
      'dragon': this.queue.get('tire_' + dragonId).content
    };
  },
  getDragonOne(dragonId) {
    return this.queue.get('dragon_' + dragonId).content;
  },

  getDragonAtlas(dragonId) {
    var dragonAtlas = new Hilo.TextureAtlas({
      image: this.queue.get('tire_' + dragonId).content,
      width: 128,
      height: 128 * 20,
      frames: {
        frameWidth: 128,
        frameHeight: 128,
        numFrames: 20
      },
      sprites: {
        dragon: [{
          rect: 0,
          name: "side"
        }, {
          rect: 1
        }, {
          rect: 2
        }, {
          rect: 3,
          next: "side"
        }, {
          rect: 4,
          name: "back"
        }, {
          rect: 5
        }, {
          rect: 6
        }, {
          rect: 7,
          next: "back"
        }, {
          rect: 8,
          name: "front"
        }, {
          rect: 9
        }, {
          rect: 10
        }, {
          rect: 11,
          next: "front"
        }, {
          rect: 12,
          name: "sideleft"
        }, {
          rect: 13
        }, {
          rect: 14
        }, {
          rect: 15,
          next: "sideleft"
        }, {
          rect: 16,
          name: "sideright"
        }, {
          rect: 17
        }, {
          rect: 18
        }, {
          rect: 19,
          next: "sideright"
        }]
      }
    });

    return dragonAtlas;
  },

  getBossComingAtlas() {
    let _numFrames = 33;
    let _sprites = [];
    for (let i = 0; i < _numFrames; i++) {
      _sprites.push({
        rect: i,
      })
    }
    var _Atlas = new Hilo.TextureAtlas({
      image: this.queue.get('bossComing').content,
      width: 318,
      height: 6482,
      frames: {
        frameWidth: 318,
        frameHeight: 6482 / 33,
        numFrames: 33
      },
      sprites: {
        sprite: _sprites
      }
    });

    return _Atlas;
  },

  getMonster(monsterId) {
    return {
      'monster': this.queue.get('monster_' + monsterId).content
    };
  },


  getMonsterAtlas(monsterId) {
    let _numFrames = Number(Config['monster'][monsterId]["numFrames"])
    let _sprites = [];
    for (let i = 0; i < _numFrames; i++) {
      _sprites.push({
        rect: i,
        name: "monster" + monsterId + "_" + i
      })
    }
    var monsterAtlas = new Hilo.TextureAtlas({
      image: this.queue.get('monster_' + monsterId).content,
      width: Number(Config['monster'][monsterId]["frameWidth"]),
      height: Number(Config['monster'][monsterId]["frameHeight"]),
      frames: {
        frameWidth: Number(Config['monster'][monsterId]["frameWidth"]),
        frameHeight: Number(Config['monster'][monsterId]["frameHeight"]) / Number(Config['monster'][monsterId]["numFrames"]),
        numFrames: Number(Config['monster'][monsterId]["numFrames"])
      },
      sprites: {
        monster: _sprites
      }
    });

    return monsterAtlas;
  },

  getAttack(type) {
    return {
      'monster': this.queue.get('attack_' + type).content
    };
  },

  getAttackAtlas(type) {
    var attackAtlas = new Hilo.TextureAtlas({
      image: this.queue.get('attack_' + type).content,
      width: 380,
      height: 3200,
      frames: {
        frameWidth: 380,
        frameHeight: 400,
        numFrames: 8
      },
      sprites: {
        attack: [{
          rect: 0,
        }, {
          rect: 1
        }, {
          rect: 2
        }, {
          rect: 3,
        }, {
          rect: 4,
        }, {
          rect: 5
        }, {
          rect: 6
        }, {
          rect: 7,
        }]
      }
    });

    return attackAtlas;
  },

  getStartBattleAtlas() {
    let _sprites = [];
    for (let i = 0; i < 23; i++) {
      _sprites.push({
        rect: i,
      })
    }
    var startBattleAtlas = new Hilo.TextureAtlas({
      image: this.queue.get("startBattle").content,
      width: 318,
      height: 13364,
      frames: {
        frameWidth: 318,
        frameHeight: 13364 / 23,
        numFrames: 23
      },
      sprites: {
        pk: _sprites
      }
    });

    return startBattleAtlas;
  },

  getVictoryAtlas() {
    let _sprites = [];
    for (let i = 0; i < 27; i++) {
      _sprites.push({
        rect: i,
      })
    }
    var victoryAtlas = new Hilo.TextureAtlas({
      image: this.queue.get("victory").content,
      width: 526,
      height: 13962,
      frames: {
        frameWidth: 526,
        frameHeight: 498.64,
        numFrames: 28
      },
      sprites: {
        victory: _sprites
      }
    });

    return victoryAtlas;
  },


  getExplodeAtlas() {
    let _sprites = [];
    for (let i = 0; i < 24; i++) {
      _sprites.push({
        rect: i,
      })
    }
    var attackAtlas = new Hilo.TextureAtlas({
      image: this.queue.get('explode').content,
      width: 4014,
      height: 298,
      frames: {
        frameWidth: 167.25,
        frameHeight: 298,
        numFrames: 24
      },
      sprites: {
        sprite: _sprites
      }
    });

    return attackAtlas;
  },

  getEggStatic() {
    let _sprites = [];
    for (let i = 0; i < 24; i++) {
      _sprites.push({
        rect: i,
      })
    }
    var atlas = new Hilo.TextureAtlas({
      image: this.queue.get('eggAni').content,
      width: 596,
      height: 13601,
      frames: {
        frameWidth: 596,
        frameHeight: 13601 / 24,
        numFrames: 24
      },
      sprites: {
        sprite: _sprites
      }
    });

    return atlas;
  },

  getEggBreak(attr) {
    let _sprites = [];
    for (let i = 0; i < 51; i++) {
      _sprites.push({
        rect: i,
      })
    }
    var atlas = new Hilo.TextureAtlas({
      image: this.queue.get('breakEgg_' + attr).content,
      width: 250,
      height: 14179,
      frames: {
        frameWidth: 250,
        frameHeight: 14179 / 51,
        numFrames: 51
      },
      sprites: {
        sprite: _sprites
      }
    });

    return atlas;
  },


  getDragonLight() {
    let _sprites = [];
    for (let i = 0; i < 51; i++) {
      _sprites.push({
        rect: i,
      })
    }
    var atlas = new Hilo.TextureAtlas({
      image: this.queue.get('dragonLight').content,
      width: 526,
      height: 12647,
      frames: {
        frameWidth: 526,
        frameHeight: 12647 / 24,
        numFrames: 51
      },
      sprites: {
        sprite: _sprites
      }
    });

    return atlas;
  }


})



