import Storage from '../utils/storage'


var getContractPath = function () {
  var debug = Storage.get("dk_debug")
  if (debug == 1) {
    // return "testnet"
  }
  return process.env.CONTRACT_PATH;
}
var getContractFrom = function () {
  var debug = Storage.get("dk_debug")
  if (debug == 1) {
    // return "0xe2fc31F816A9b94326492132018C3aEcC4a93aE1"
  }
  return process.env.CALL_FROM;
}
var getEnv = function () {
  var debug = Storage.get("dk_debug")
  if (debug == 1) {
    // return "testnet"
  }
  return process.env.NODE_ENV;
}

var getDebug = function () {
  var debug = Storage.get("dk_debug")
  return debug ? debug : 0;
}

let getAsset = function () {
  var asset = Storage.get("game_asset")
  if (asset) {
    return asset
  }
  return null;
}

let state = {
  contractPath: getContractPath(),
  contractFrom: getContractFrom(),
  env: getEnv(),
  debug: getDebug(),
  web3Modal: null,
  providerOptions: null,
  web3: {
    isInjected: false,
    web3Instance: null,
    networkId: null,
    coinbase: null,
    balance: null,
    error: null,
    from: null,
    gasPrice: 0
  },

  asset: getAsset(),
  wallet: {
    DK: 0,
    DG: 0
  },
  walletVisiable: Storage.get("user_walletVisiable"),
  contractInstance: null,

  isTraining: 0,
  trainingTime: 0
}

export default state;



