<template>
  <div class="container center">
    <div class="title">Treasure Chest</div>
    <img
      src="../../asset/ui/common_return.png"
      alt
      class="container-return"
      @click="
        $parent.closeDialog('chest'),
          $store.dispatch('ASET_WALLETVISIABLE', true)
      "
    />
    <div class="chest-container center">
      <div class="chest-box" v-for="(item, index) in 4" :key="index">
        <p class="woodChestRemainNumber" v-if="item === 5">
          RemainNumber: {{ woodChestRemainNumber }}
        </p>
        <div class="chest-icon">
          <img
            :src="
              'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/chest/Chest0' +
              item +
              '_Close.png'
            "
            alt
            class="chest-close"
          />
        </div>

        <!-- <div
          class="template-btn4"
          v-if="item === 1"
          @click="selectContainerVisisbale = true"
        >
          Select Dragon
        </div> -->
        <div class="template-btn3">Coming Soon</div>
      </div>
    </div>

    <div
      class="claim-icon"
      @click="
        claimDialogVisiable = true;
        $store.dispatch('ASET_WALLETVISIABLE', true);
      "
      v-if="false"
    ></div>

    <div class="bottom-part">
      <div class="top-light"></div>
    </div>
    <div class="dragons-container" v-if="selectContainerVisisbale">
      <img
        src="../../asset/ui/common_return.png"
        alt
        class="return"
        @click="selectContainerVisisbale = false"
      />
      <div class="dragons-list">
        <div
          class="dragon-item"
          v-for="(item, index) in dragonData"
          :key="index"
          :style="{
            backgroundImage:
              'url(' +
              require('../../asset/ui/rarity/' + item.rarity + '.png') +
              ')',
          }"
          @click="activeDragon = item"
        >
          <!-- {{item}} -->
          <div
            :class="{ active: activeDragon['id'] === item.id }"
            class="active-box"
            @click="activeDragon['id'] === item.id"
          >
            <img
              class="attribute"
              :src="'../../asset/ui/element/element-' + item.element + '.png'"
              alt
            />
            <p class="level">Lv.{{ item.level }}</p>

            <img :src="item.img" alt class="dragon-icon" />

            <img
              class="quality"
              :src="
                'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/rarity/rarity_' +
                item.rarity +
                '.png'
              "
              alt
            />

            <p class="index">{{ $t("elements")[item.element] }}</p>
            <p class="remain">{{ item.remainNumber }}</p>
          </div>
        </div>
      </div>

      <div class="template-btn2" @click="openWoodChest">
        <img
          src="https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/key/key-1.png"
          alt
        />
        Open
        <i class="el-icon-loading" v-show="loadings['openWoodChest']"></i>
      </div>
    </div>
    <div class="result-dialog center" v-if="resultDialogVisiable">
      <div class="chest-icon center">
        <img
          :src="
            'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/chest/Chest0' +
            activeChestType +
            '_Cap.png'
          "
          alt
          class="chest-cap"
        />
        <img
          :src="
            'https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/chest/Chest0' +
            activeChestType +
            '_Box.png'
          "
          alt
          class="chest-bottom"
        />
      </div>

      <div class="result-box">
        <div class="result-list">
          <div class="result-item">
            <img class="center" src="../../asset/ui/dg.png" alt />
            <p>{{ chestReward["dgAmount"] }}</p>
          </div>
          <div class="result-item">
            <img class="center" src="../../asset/ui/dk.png" alt />
            <p>{{ chestReward["dkAmount"] }}</p>
          </div>
          <div class="result-item">
            <img
              class="center"
              src="https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/key/key-1.png"
              alt
            />
            <p>{{ chestReward["prop"] }}</p>
          </div>
        </div>

        <div
          class="confirm-btn template-btn4"
          @click="resultDialogVisiable = false"
        >
          Confirm
        </div>
      </div>
    </div>

    <div class="claim-dialog" v-if="claimDialogVisiable">
      <div class="claim-content">
        <div class="claim-items">
          <div class="claim-item">
            <img src="../../asset/ui/dg.png" alt />
            <p>
              {{ userReward["dcAmount"] }}
            </p>
          </div>
          <div class="claim-item">
            <img src="../../asset/ui/dk.png" alt />
            <p>
              {{ userReward["dkAmount"] }}
            </p>
          </div>
          <div class="claim-item">
            <img
              src="https://cdn.jsdelivr.net/gh/DragonKnightIO/BscDragonKnight/static/images/key/key-1.png"
              alt
            />
            <p>{{ userReward["prop"] }}</p>
          </div>
        </div>

        <div class="claim-btns" @click="claimChestPrize">
          Claim
          <i class="el-icon-loading" v-show="loadings['claimChestPrize']"></i>
        </div>
      </div>
      <div
        class="claim-dialog-modal"
        @click="
          claimDialogVisiable = false;
          $store.dispatch('ASET_WALLETVISIABLE', false);
        "
      ></div>
    </div>
  </div>
</template>

<script>
import store from "@/store/index";
const path = store.state.contractPath;
const abiJsons = require("../../contracts/deployments." + path + ".json");
const tokensJsons = require("../../contracts/tokens." + path + ".json");
const erc20AbiJosn = require("../../contracts/erc20.json");

import Util from "../../utils/util";
export default {
  name: "chest",
  data() {
    return {
      dragonData: {},
      activeAttr: 0,
      selectContainerVisisbale: false,
      resultDialogVisiable: false,
      activeChestType: 1,
      claimDialogVisiable: false,
      chestReward: {
        dgAmount: 0,
        dkAmount: 0,
        prop: 0,
      },

      userReward: {
        dgAmount: 0,
        dkAmount: 0,
        props: 0,
      },

      woodChestRemainNumber: 0,
      activeDragon: null,

      loadings: {},
      txs: {},
    };
  },
  computed: {
    web3() {
      return this.$store.state.web3;
    },
  },
  watch: {
    web3(val1, val2) {},
  },

  mounted() {
    this.init();
    this.$store.dispatch("ASET_WALLETVISIABLE", false);
  },

  methods: {
    init() {
      return false;
      if (!this.web3 || !this.web3.web3Instance) {
        return;
      }
      let web3 = this.web3.web3Instance();
      let that = this;

      this.dragonAddress = abiJsons["Dragon"]["address"];
      this.dragonContract = new web3.eth.Contract(
        abiJsons["Dragon"]["abi"],
        this.dragonAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.woodChestAddress = abiJsons["WoodChest"]["address"];
      this.woodChestContract = new web3.eth.Contract(
        abiJsons["WoodChest"]["abi"],
        this.woodChestAddress,
        {
          from: this.web3.coinbase,
        }
      );

      this.chestPrizeAddress = abiJsons["ChestPrize"]["address"];
      this.chestPrizeContract = new web3.eth.Contract(
        abiJsons["ChestPrize"]["abi"],
        this.chestPrizeAddress,
        {
          from: this.web3.coinbase,
        }
      );
      // this.dragonUpgradeAddress = abiJsons["DragonUpgrade"]["address"];

      //   for (var i in this.tokens) {
      //     that.tokenContracts[this.tokens[i]] = new web3.eth.Contract(
      //       erc20AbiJosn,
      //       tokensJsons[this.tokens[i]]["address"],
      //       {
      //         from: that.web3.coinbase
      //       }
      //     );
      //   }

      this.getInfo();
    },

    getInfo() {
      this.getDragonInfo();
      this.getRemainNumber();
      this.getUserRewards();
    },

    getRemainNumber() {
      this.woodChestContract.methods
        .getRemainNumber()
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
  
          this.woodChestRemainNumber = res;
        });
    },

    getUserRewards() {
      this.chestPrizeContract.methods
        .userRewards(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          
          this.userReward["dkAmount"] = (
            res["dkAmount"] / Math.pow(10, 18)
          ).toFixed(0);
          this.userReward["dcAmount"] = (
            res["dcAmount"] / Math.pow(10, 18)
          ).toFixed(0);
          this.userReward["props"] = res["props"];
        });
    },

    getDragonInfo() {
      let that = this;
      this.dragonContract.methods
        .balanceOf(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          
          if (!Number(res)) {
            return;
          }
          this.dragonContract.methods
            .tokensOf(this.web3.coinbase, 0, res)
            .call({ from: this.$store.state.contractFrom })
            .then((dragonIds) => {
              let dragonList = [];

              this.getDragonAttrs(dragonIds, (dragonAttrs) => {
                for (var i in dragonIds) {
                  let dragonInfo = Util.getDragonInfoWithAttr(
                    dragonIds[i],
                    dragonAttrs[i]
                  );

                  that.getDragonNumbers(dragonInfo);
                  if (i == 0 && !this.activeDragon) {
                    that.activeDragon = dragonInfo;
                  }

                  dragonList.push(dragonInfo);
                }
                dragonList.sort(function (a, b) {
                  return a.type - b.type >= 0 ? -1 : 1;
                });

                dragonList.map((dragon, index) => {
                  that.$set(that.dragonData, dragon.id, dragon);
                });
              });
            });
        });
    },

    getDragonAttrs(dragonIds, cb) {
      let that = this;

      this.dragonContract.methods
        .getDragonAttrExts(dragonIds)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          // that.getDragonNumbers(res[0],cb)
          cb(res[0]);
        });
    },

    getDragonNumbers(dragonInfo, cb) {
      let that = this;
      this.woodChestContract.methods
        .getDragonNumbers([dragonInfo.id])
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          that.$set(dragonInfo, "remainNumber", res[0]);
          // cb(dragonInfo);
        });
    },

    openWoodChest() {
      let that = this;
      let _id = this.activeDragon["id"];
      let loading = "openWoodChest";
      if (that.loadings[loading]) {
        return;
      }
      that.$set(that.loadings, loading, true);
      this.woodChestContract.methods
        .open(_id)
        .estimateGas({ from: that.web3.coinbase }, function (err, gas) {
          if (err) {
            that.$set(that.loadings, loading, false);
            that.$message({
              message: that.$t("common.failed") + Util.errMsg(err),
              type: "warning",
              center: true,
            });
            return;
          }
          that.woodChestContract.methods
            .open(_id)
            .send({
              gasPrice: that.web3.gasPrice,
            })
            .on("transactionHash", function (hash) {
              
            })
            .on("receipt", function (receipt) {
              
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;

              that.$set(that.loadings, loading, false);

              that.getInfo();

              that.renderChestOpenReward(
                receipt["events"]["RewardEvent"]["returnValues"]
              );
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              
            })
            .on("error", function (err) {
              that.$message({
                message: that.$t("common.failed") + Util.errMsg(err),
                type: "warning",
                center: true,
              });
              that.$set(that.loadings, loading, false);
            });
        });
    },

    renderChestOpenReward(event) {
      this.selectContainerVisisbale = false;
      this.chestReward["dgAmount"] = (
        event["dgAmount"] / Math.pow(10, 18)
      ).toFixed(0);
      this.chestReward["dkAmount"] = (
        event["dkAmount"] / Math.pow(10, 18)
      ).toFixed(0);
      this.chestReward["prop"] = event["prop"];
      this.resultDialogVisiable = true;
    },

    claimChestPrize() {
      let that = this;
      let loading = "claimChestPrize";
      if (that.loadings[loading]) {
        return;
      }
      that.$set(that.loadings, loading, true);
      this.chestPrizeContract.methods.claim().estimateGas(
        {
          from: that.web3.coinbase,
          value: this.web3.web3Instance().utils.toWei("0.002"),
        },
        function (err, gas) {
          if (err) {
            that.$set(that.loadings, loading, false);
            that.$message({
              message: that.$t("common.failed") + Util.errMsg(err),
              type: "warning",
              center: true,
            });
            return;
          }
          that.woodChestContract.methods
            .claim()
            .send({
              gasPrice: that.web3.gasPrice,
              value: this.web3.web3Instance().utils.toWei("0.002"),
            })
            .on("transactionHash", function (hash) {
              
            })
            .on("receipt", function (receipt) {
              
              if (that.txs[receipt.transactionHash]) {
                return;
              }
              that.txs[receipt.transactionHash] = receipt.transactionHash;

              that.$set(that.loadings, loading, false);

              that.$EventBus.$emit("refreshBalance");
              that.getInfo();
            })
            .on("confirmation", function (confirmationNumber, receipt) {
              
            })
            .on("error", function (err) {
              that.$message({
                message: that.$t("common.failed") + Util.errMsg(err),
                type: "warning",
                center: true,
              });
              that.$set(that.loadings, loading, false);
            });
        }
      );
    },
  },
};
</script>

