  color: #fffcf9;
  position: absolute;
  line-height: 18px;
  height: 18px;

  width: 78%;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, -90%);
  background-color: rgba(0, 0, 0, 0.493);
  border-radius: 0 0 10px 10px;
}

.dragon-item {
  width: 88%;
  margin: 0 auto;
  padding-bottom: 88%;
  background-size: 100% 100%;
  position: relative;
  margin-bottom: 20px;
}

.dragon-item .attribute {
  width: 14px;
  position: absolute;
  left: 0;
}

.dragon-item .templete-item {
  position: relative;
}

.dragon-item .level {
  height: 16px;
  line-height: 16px;
  font-size: 12px;
  background: rgba(0, 0, 0, 0.37);
  border-radius: 0 0 10px 10px;
  white-space: nowrap;
  text-shadow: 0.2px 0.4px 0 rgb(7 7 7), 0.2px 0.4px 0 rgb(7 7 7);
  position: absolute;
  color: #fff;
  bottom: 0;
}

.result-dialog {
  width: 100%;
  height: 100%;
  background-image: url("../../asset/ui/transparent-bg.gif");
  background-size: 100% 100%;
  background-color: rgba(3, 3, 3, 0.7);
}

.result-dialog .chest-icon {
  width: 50%;
  height: 22%;
  top: 30%;
}
.result-dialog .chest-icon .chest-cap {
  width: 96%;
  position: absolute;
  bottom: 51%;
  left: 58%;
  transform: translate(-50%, 0%);
}

.result-dialog .chest-icon .chest-bottom {
  z-index: 1;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
}

.result-dialog .result-box {
  width: 80%;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, 0);
}

.result-dialog .result-box .result-list {
  margin-top: 10vh;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.result-dialog .result-box .result-list .result-item {
  width: 23%;
  padding-bottom: 23%;
  background-image: url("../../asset/ui/reward.png");
  background-size: 100% 100%;
  position: relative;
}

.result-dialog .result-box .result-list .result-item img {
  width: 60%;
}

.result-dialog .result-box .result-list .result-item p {
  font-size: 20px;
  font-weight: 800;
  text-shadow: -0.0555em -0.0555em 0 #000, 0.0555em -0.0555em 0 #000,
    -0.0555em 0.0555em 0 #000, 0.0555em 0.0555em 0 #000;
  color: rgb(246, 223, 121);
  position: absolute;
  right: 4%;
  bottom: 4%;
}

.result-dialog .result-box .confirm-btn {
  margin: 10% auto;
}

.claim-icon {
  position: absolute;
  right: 2%;
  top: 5%;
  width: 90px;
  height: 70px;
  background-image: url("../../asset/ui/reward-icon.gif");
  background-size: 100% 100%;
}
</style>


