<script>
import store from "@/store/index";
import Util from "../../utils/util";
import ContractUtil from "../../utils/contractUtils";
import Pagination from "../pagination/pagination.vue";

const Config = require("../../data/config.json");
const path = store.state.contractPath;
const abiJsons = require("../../contracts/deployments." + path + ".json");
const tokensJsons = require("../../contracts/tokens." + path + ".json");
const erc20AbiJosn = require("../../contracts/erc20.json");
export default {
  name: "marketView",

  components: { Pagination },
  data() {
    return {
      ordersType: "market",
      marketContract: null,
      dragonContract: null,
      dkContract: null,
      dgContract: null,
      loadings: {},
      ordersOnSell: [],

      myTokenAllowances: {},
      myTokenBalances: {},
      myDragons: [],
      myOrdersOnSell: [],
      isDragonApproval: false,
      myDragonsVisible: false,
      dragonDetailVisible: false,
      sortOptionVisible: false,
      attributeFilterValueVisible: false,
      dragonDetail: null,
      myDragonType: "onsell",
      currentMarketToken: "DK",
      sellTokenType: "DK",
      sellSelected: {},
      cancelSelected: {},
      elementOption: "",
      elementOptions: [
        {
          id: 1,
          label: require("../../asset/ui/element/element-1.png"),
          name: "Wood",
        },
        {
          id: 2,
          label: require("../../asset/ui/element/element-2.png"),
          name: "Wind",
        },
        {
          id: 3,
          label: require("../../asset/ui/element/element-3.png"),
          name: "Water",
        },
        {
          id: 4,
          label: require("../../asset/ui/element/element-4.png"),
          name: "Fire",
        },
      ],
      attributeOptions: {
        strength: [0, 100],
        agility: [0, 100],
        constitution: [0, 100],
        intelligence: [0, 100],
        willpower: [0, 100],
        spirit: [0, 100],
      },
      rarityOption: "",
      rarityOptions: [
        { id: 0, label: "All" },
        { id: 5, label: "SS" },
        { id: 4, label: "S" },
        { id: 3, label: "A" },
        { id: 2, label: "B" },
        { id: 1, label: "C" },
      ],
      dragonsSort: [
        { id: 0, label: "Time", method: "mintTime" },
        { id: 1, label: "Price", method: "price" },
        { id: 2, label: "Total Power", method: "powerValue" },
        { id: 3, label: "Rarity", method: "rarity" },
        { id: 4, label: "Strength", method: "strength" },
        { id: 5, label: "Agility", method: "agility" },
        { id: 6, label: "Constitution", method: "constitution" },
        { id: 7, label: "Intelligence", method: "intelligence" },
        { id: 8, label: "Willpower", method: "willpower" },
        { id: 9, label: "Spirit", method: "spirit" },
      ],
      ordersShow: [],
      dragonSortOption: 0,
      sortMethod: false,
    };
  },
  computed: {
    web3() {
      return this.$store.state.web3;
    },
    filterOption() {
      return {
        element: this.elementOption,
        rarity: this.rarityOption,
        token: this.currentMarketToken,
      };
    },
  },
  watch: {
    web3(newVal, oldVal) {
      if (newVal) {
        this.init();
      }
    },
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (!this.web3 || !this.web3.web3Instance || this.marketContract) {
        return;
      }
      let web3 = this.web3.web3Instance();
      let tokenAddresses = {
        DK: tokensJsons["DK"]["address"],
        DG: tokensJsons["DG"]["address"],
      };
      this.dkContract = new web3.eth.Contract(
        erc20AbiJosn,
        tokenAddresses["DK"],
        {
          from: this.web3.coinbase,
        }
      );
      this.dgContract = new web3.eth.Contract(
        erc20AbiJosn,
        tokenAddresses["DG"],
        {
          from: this.web3.coinbase,
        }
      );
      this.dragonContract = new web3.eth.Contract(
        abiJsons["Dragon"]["abi"],
        abiJsons["Dragon"]["address"],
        {
          from: this.web3.coinbase,
        }
      );
      this.marketContract = new web3.eth.Contract(
        abiJsons["DragonMarket"]["abi"],
        abiJsons["DragonMarket"]["address"],
        {
          from: this.web3.coinbase,
        }
      );

      this.getMyPageInfo();
    },
    getMyPageInfo() {
      this.getMyDragons();
      this.getMarketOrdersAll();
      this.getDragonApproval();
      this.getMyTokenAllowance();
      // this.getMyTokenBalance();
    },
    getDragonApproval() {
      let marketAddress = abiJsons["DragonMarket"]["address"];
      ContractUtil.call(this.dragonContract, "isApprovedForAll", [
        this.web3.coinbase,
        marketAddress,
      ]).then((res) => {
        this.isDragonApproval = res;
      });
    },
    approveDragons() {
      let that = this,
        marketAddress = abiJsons["DragonMarket"]["address"];
      let loading = "approveDragons";
      if (this.loadings[loading]) {
        return;
      }
      that.$set(that.loadings, loading, true);

      let group = {
        contract: that.dragonContract,
        key: "setApprovalForAll",
        args: [marketAddress, true],
        onReceipt: (rec) => {
          that.isDragonApproval = true;
          // nextFunc && nextFunc();
          that.$message({
            message: "Approve Success",
            type: "success",
          });
          that.$set(that.loadings, loading, false);
        },
        onError: (msg) => {
          that.$message({
            message: "Approve Fail" + msg,
            type: "error",
          });
          that.$set(that.loadings, loading, false);
        },
      };
      ContractUtil.send(group);
    },
    approvalDragon() {},
    getMyDragons() {
      this.dragonContract.methods
        .balanceOf(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          if (!Number(res)) {
            return;
          }
          this.dragonContract.methods
            .tokensOf(this.web3.coinbase, 0, res)
            .call({ from: this.$store.state.contractFrom })
            .then((res) => {
              let dragonList = [],
                dragonIds = [];
              this.myDragons = [];
              for (let i in res) {
                let _dragonInfo = Util.getDragonInfo(res[i]);
                dragonList.push(_dragonInfo);
                dragonIds.push(_dragonInfo.id);
              }

              this.getDragonDetail(dragonIds, (dragons) => {
                let attrs = [
                  "strength",
                  "agility",
                  "constitution",
                  "intelligence",
                  "willpower",
                  "spririt",
                  "level",
                ];
                dragons.forEach((dragon, index) => {
                  let obj = { ...dragonList[index] },
                    power = 0;
                  for (var i in dragon) {
                    if (attrs.indexOf(i) != -1) {
                      obj[i == "spririt" ? "spirit" : i] = dragon[i];

                      if (i != "level") {
                        power += parseInt(dragon[i]);
                      }
                    }
                  }
                  obj.powerValue = power;
                  this.myDragons.push(obj);
                });
              });
            });
        });
    },
    getMarketOrdersAll() {
      let dragonAddress = abiJsons["Dragon"]["address"],
        dkAddress = tokensJsons["DK"]["address"],
        dgAddress = tokensJsons["DG"]["address"];
      ContractUtil.call(this.marketContract, "ordersLength", [
        dragonAddress,
      ]).then(async (res) => {
        if (res == 0) return;
        let orderAll = [],
          orderIds = [],
          orderList = [],
          myOrder = [];
        await ContractUtil.call(this.marketContract, "getOrders", [
          0,
          0,
          0,
          Util.none,
          dragonAddress,
          dkAddress,
        ]).then((res) => {
          orderList.forEach.call(res, (result) => {
            if (result.nftId == 0) return;
            let order = {};
            for (var i in result) {
              if (!Number(i) && i != "0") {
                i == "id"
                  ? (order["index"] = result[i])
                  : (order[i] = result[i]);
              }
            }
            order.token = "DK";
            let info = Util.getDragonInfo(order.nftId);
            order = { ...order, ...info };
            orderAll.push(order);
            orderIds.push(order.nftId);
          });
        });
        await ContractUtil.call(this.marketContract, "getOrders", [
          0,
          0,
          0,
          Util.none,
          dragonAddress,
          dgAddress,
        ]).then((res) => {
          orderList.forEach.call(res, (result) => {
            if (result.nftId == 0) return;
            let order = {};
            for (var i in result) {
              if (!Number(i) && i != "0") {
                i == "id"
                  ? (order["index"] = result[i])
                  : (order[i] = result[i]);
              }
            }
            order.token = "DG";
            let info = Util.getDragonInfo(order.nftId);
            order = { ...order, ...info };
            orderAll.push(order);
            orderIds.push(order.nftId);
          });
        });

        this.getDragonDetail(orderIds, (orders) => {
          let attrs = [
            "strength",
            "agility",
            "constitution",
            "intelligence",
            "willpower",
            "spririt",
            "level",
          ];
          orders.forEach((order, index) => {
            let obj = { ...orderAll[index] },
              power = 0;
            for (var i in order) {
              if (attrs.indexOf(i) != -1) {
                obj[i == "spririt" ? "spirit" : i] = order[i];
                if (i != "level") {
                  power += parseInt(order[i]);
                }
              }
            }
            obj.powerValue = power;
            if (obj.owner.toLowerCase() == this.web3.coinbase.toLowerCase()) {
              myOrder.push(obj);
            } else {
              orderList.push(obj);
            }
          });
        });

        this.ordersOnSell = orderList;
        this.myOrdersOnSell = myOrder;
        this.ordersOnSell.sort((a, b) => {
          return a.mintTime - b.mintTime;
        });
      });
    },
    getMyTokenBalance() {},
    getMyTokenAllowance() {
      let marketAddress = abiJsons["DragonMarket"]["address"];
      ContractUtil.call(this.dkContract, "allowance", [
        this.web3.coinbase,
        marketAddress,
      ]).then((res) => {
        this.$set(this.myTokenAllowances, "DK", res);
      });
      ContractUtil.call(this.dgContract, "allowance", [
        this.web3.coinbase,
        marketAddress,
      ]).then((res) => {
        this.$set(this.myTokenAllowances, "DG", res);
      });
    },
    approveToken(token) {
      let that = this,
        marketAddress = abiJsons["DragonMarket"]["address"];
      let group = {
        contract: that[`${token.toLowerCase()}Contract`],
        key: "approve",
        args: [marketAddress, Util.infinity],
        onReceipt: (rec) => {
          that.$set(that.loadings, "approve" + token, false);
          that.getMyTokenAllowance();
          that.$message({
            message: "Approve Success",
            type: "success",
          });
        },
        onError: (msg) => {
          that.$set(that.loadings, "approve" + token, false);
          that.$message({
            message: "Approve Fail" + msg,
            type: "error",
          });
        },
      };
      that.$set(that.loadings, "approve" + token, true);
      ContractUtil.send(group);
    },
    getDragonDetail(dragonIds, cb) {
      this.dragonContract.methods
        .getDragonInfos(dragonIds)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          cb(res);
        });
    },

    putOnSale(dragonId) {
      let loading = "putOnSale" + dragonId;
      let that = this,
        dragonIds = [dragonId],
        dragonPrices = [this.sellSelected[dragonId].price],
        nftAddress = abiJsons["Dragon"]["address"],
        tokenAddress =
          tokensJsons[this.sellSelected[dragonId]["token"]]["address"];
      if (this.loadings[loading]) return;
      that.$set(that.loadings, loading, true);

      let prices = dragonPrices.map((price) => {
        let num = that.web3.web3Instance().utils.toWei(price + "");
        return num;
      });
      let group = {
        contract: that.marketContract,
        key: "sells",
        args: [nftAddress, dragonIds, tokenAddress, prices],
        onReceipt: (rec) => {
          that.$set(that.loadings, loading, false);
          dragonIds.forEach((id) => {
            let index = that.myDragons.indexOf(
              that.myDragons.find((item) => item.id == id)
            );
            that.myDragons.splice(index, 1);
          });
          that.getMarketOrdersAll();
          that.$message({
            message: "Success listed",
            type: "success",
          });
        },
        onError: (msg) => {
          that.$set(that.loadings, loading, false);
          that.$message({
            message: "Failed to put on the shelves" + msg,
            type: "error",
          });
        },
      };
      ContractUtil.send(group);
    },

    cancelOrder(dragonId) {
      var loading = "cancelOrder" + dragonId;
      if (this.loadings[loading]) return;
      let that = this,
        dragonIds = [dragonId],
        dragonAddress = abiJsons["Dragon"]["address"];

      let group = {
        contract: this.marketContract,
        key: "cancelOrder",
        args: [dragonAddress, dragonIds],
        onReceipt: (rec) => {
          that.$set(that.loadings, loading, false);
          that.getMarketOrdersAll();
          that.getMyDragons();
          that.$message({
            message: "Success Cancel",
            type: "success",
          });
        },
        onError: (err) => {
          that.$set(that.loadings, loading, false);
          that.$message({
            message: "Failed to remove" + err,
            type: "err",
          });
        },
      };
      that.$set(that.loadings, loading, true);
      ContractUtil.send(group);
    },

    buyDragon() {
      if (this.loadings["buy" + this.dragonDetail.id]) {
        return;
      }
      let that = this,
        id = that.dragonDetail.id,
        nftAddress = abiJsons["Dragon"]["address"];
      let group = {
        contract: that.marketContract,
        key: "buy",
        args: [nftAddress, id],
        onReceipt: (rec) => {
          that.getMarketOrdersAll();
          that.getMyDragons();
          that.$set(that.loadings, "buy" + id, false);
          that.dragonDetailVisible = false;
          that.$store.dispatch("ASET_WALLETVISIABLE", false);
          that.$message({
            message: "Successful purchase",
            type: "success",
          });

          that.$EventBus.$emit("refreshBalance");
        },
        onError: (msg) => {
          that.$set(that.loadings, "buy" + id, false);
          that.$message({
            message: "Failed purchase" + msg,
            type: "error",
          });
        },
      };
      that.$set(that.loadings, "buy" + id, true);
      ContractUtil.send(group);
    },
    closeDragonDetail() {
      this.dragonDetailVisible = false;
      this.$store.dispatch("ASET_WALLETVISIABLE", false);
    },

    chooseDragonSell(id) {
      if (this.sellSelected[id]) {
        this.$set(this.sellSelected, id, null);
        return;
      }
      let obj = {
        price: 0,
        token: "DK",
      };
      this.$set(this.sellSelected, id, obj);
    },

    attributeNumberFilter(order) {
      for (let key in this.attributeOptions) {
        if (
          order[key] < this.attributeOptions[key][0] ||
          order[key] > this.attributeOptions[key][1]
        ) {
          return false;
        }
      }
      return true;
    },

    showSortDropMenu() {
      this.sortOptionVisible = !this.sortOptionVisible;
    },
    showAttributeValueDropMenu() {
      this.attributeFilterValueVisible = !this.attributeFilterValueVisible;
    },
    attributeNumberChange(e) {},
    sortDragonPlace(item) {
      if (this.dragonSortOption == item.id) {
        this.sortMethod = !this.sortMethod;
      } else {
        this.sortMethod = false;
      }
      this.dragonList = [];
      let value = item.method;
      this.dragonSortOption = item.id;
      this.sortOptionVisible = false;

      this.ordersOnSell.sort((a, b) => {
        return !this.sortMethod ? b[value] - a[value] : a[value] - b[value];
      });
    },
    changeSellToken(type) {
      this.sellTokenType = type;
      for (var i in this.sellSelected) {
        this.$set(this.sellSelected[i], "token", type);
      }
    },
    showDragonDetail(item) {
      this.dragonDetail = item;
      this.dragonDetailVisible = true;
      this.$store.dispatch("ASET_WALLETVISIABLE", true);
    },
    changeOrderType(type) {
      this.ordersType = type;
    },
    changeElementOption(opt) {
      this.elementOption = opt;
    },
    changeRarityOption(opt) {
      this.rarityOption = opt;
    },
    showMyDragonsDialog() {
      this.myDragonsVisible = !this.myDragonsVisible;
    },
    changeMyDragonType(type) {
      this.myDragonType = type;
    },
    closeDialog() {
      this.$parent.closeDialog("market");
    },
  },
  filters: {
    filterTokenAmount(value, decimals, number) {
      let result = (value * 1) / 10 ** decimals;
      return result.toFixed(number);
    },
    filterUpCaseFirstLetter(value) {
      return value.charAt(0).toUpperCase() + value.slice(1);
    },
  },
};
</script>

