import store from "../store/index";
import Util from "../utils/util";
export default {
  // call(group) {
  //   let { contract, key, args, callback } = group;
  //   contract.methods[key](...args)
  //     .call()
  //     .then(callback);
  // },
  call(contract, method, args = []) {
    return new Promise((resolve, reject) => {
      contract.methods[method](...args)
        .call()
        .then(res => {
          resolve(res);
        });
    });
  },
  send(group) {
    let {
      contract,
      key,
      args,
      onTxHash,
      onReceipt,
      onConfirm,
      onError
    } = group;

    let txs = {};
    contract.methods[key](...args).estimateGas({
        from: store.state.web3.coinbase
      },
      function (err, gas) {
        if (err) {
          let msg = Util.errMsg(err);
          onError && onError(msg);
          return;
        }
        contract.methods[key](...args)
          .send({
            gasPrice: store.state.web3.gasPrice
          })
          .on("transactionHash", hash => {
            onTxHash && onTxHash();
          })
          .on("receipt", receipt => {
            if (txs[receipt.transactionHash]) {
              return;
            }
            txs[receipt.transactionHash] = receipt.transactionHash;
            onReceipt && onReceipt(receipt);
          })
          .on("confirmation", (confirmationNumber, receipt) => {
            onConfirm && onConfirm(confirmationNumber);
          })
          .on("error", err => {
            let msg = Util.errMsg(err);
            onError && onError(msg);
          });
      }
    );
  }
};



