<script>
import i18n from "../locales/index";
import TrainingView from "./training/Training.vue";
import DungeonView from "./dungeon/Dungeon.vue";
import SummonView from "./shop/Summon.vue";
import backpackView from "./backpack/Backpack";
import DragonView from "./dragon/Dragon";
import MarketView from "./market/Market";
import ChestView from "./chest/chest";
import WalletView from "./wallet/Wallet";
import WarView from "./war/War";
import WarPVPView from "./warpvp/Hall.vue";
import store from "../store/index";
var path = store.state.contractPath;
const erc20AbiJosn = require("../contracts/erc20.json");
const tokensJsons = require("../contracts/tokens." + path + ".json");

export default {
  name: "Home",
  components: {
    TrainingView,
    DungeonView,
    SummonView,
    ChestView,
    WarView,
    WarPVPView,
    WalletView,
    backpackView,
    DragonView,
    MarketView,
  },

  data() {
    return {
      lang: "en",
      loadings: {},
      activeBtn: "train",
      durationTrainTime: null,
      patrolStatus: "",
      tokenContracts: {},
      tokens: ["DG", "DK"],
      tokenBalance: {},
      windowWidth: document.documentElement.clientWidth,
      windowHeight: document.documentElement.clientHeight,
      generalBarType: null,
      generalChoiced: null,
      generalBarChoicedData: {},

      //VIEW
      trainingViewVisiable: false,
      dungeonViewVisiable: false,
      summonViewVisiable: false,

      maskVisible: false,
      DialogVisiable: false,
      backpackDialogVisiable: false,

      shopDialogVisiable: false,
      itemBoxDialogVisiable: false,
      chestViewVisiable: false,

      transactionDialogVisiable: false,
      dragonDialogVisiable: false,
      contactDialogVisiable: false,
      marketDialogVisiable: false,

      warViewVisiable: false,

      warPVPVisiable: false,

      noticeDatas: [
        {
          title: "1",
          desc: "22222222222222222222222222222222222222",
          isHidden: false,
        },
      ],
      activeName: "backpack-dragon",
      eggHatchChoiceIndex: null,
    };
  },
  watch: {
    web3(newVal) {
      if (!newVal.coinbase) return;
      this.init();
    },
  },
  computed: {
    web3() {
      return this.$store.state.web3;
    },
  },

  mounted() {
    this.lang = i18n.locale;
    this.$store.dispatch("ASET_WALLETVISIABLE", true);
  },

  created() {
    this.$EventBus.$on("refreshBalance", () => {
      this.getBalance();
    });
  },

  methods: {
    closeDialog(type, open) {
      if (type !== "generalBar") {
        this.maskVisible = false;
      }
      switch (type) {
        case "shop":
          this.shopDialogVisiable = false;
          break;
        case "backpack":
          this.backpackDialogVisiable = false;
          this.$store.dispatch("ASET_WALLETVISIABLE", true);
          break;

        case "dragon":
          this.dragonDialogVisiable = false;
          break;
        case "market":
          this.marketDialogVisiable = false;
          this.$store.dispatch("ASET_WALLETVISIABLE", true);
          break;
        case "chest":
          this.chestViewVisiable = false;
          this.$store.dispatch("ASET_WALLETVISIABLE", true);
          break;
      }

      switch (open) {
        case "shop":
          this.shopDialogVisiable = true;
          break;
        case "backpack":
          this.backpackDialogVisiable = true;
          break;
        case "dragon":
          this.dragonDialogVisiable = true;
          break;
        case "market":
          this.marketDialogVisiable = true;
          break;
      }
      this.maskVisible = !!open;
    },

    checkLogin(to, isBottomNav) {
      if (this.$store.state.web3.coinbase) {
        if (isBottomNav) {
          this.trainingViewVisiable = false;
          this.dungeonViewVisiable = false;
          this.summonViewVisiable = false;
          this.dragonDialogVisiable = false;
        }
        this[to] = true;
      } else {
        this.$EventBus.$emit("userUnLogin");
      }
    },
    showLoading(text) {
      if (this.loading) {
        this.loading.text = text;
        return;
      }

      this.loading = this.$loading({
        lock: true,
        text: text,
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
      });
    },

    openGenaralBar(type, index) {
      this.generalBarType = type;
      this.generalChoiced = index + "";
      this.itemBoxDialogVisiable = true;
    },

    openDialog(type) {
      setTimeout(() => {
        this.maskVisible = true;
        switch (type) {
          case "dragon":
            this.dragonDialogVisiable = true;
            break;

          case "backpack":
            this.backpackDialogVisiable = true;
            break;
        }
      }, 300);
    },

    init() {
      var web3 = this.web3.web3Instance();
      this.tokens.map((token, i) => {
        this.tokenContracts[token] = new web3.eth.Contract(
          erc20AbiJosn,
          tokensJsons[token]["address"],
          {
            from: this.web3.coinbase,
          }
        );
      });

      this.getInfo();
    },

    getInfo() {
      this.getBalance();
    },

    getBalance() {
      this.tokens.map((token, i) => {
        this.getTokenBalance(token);
      });
    },

    getTokenBalance(token) {
      if (!this.tokenContracts[token]) {
        return;
      }
      this.tokenContracts[token].methods
        .balanceOf(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then((res) => {
          let _num = res / Math.pow(10, 18);
          if (token === "DG") {
            _num = _num.toFixed(0);
          } else {
            _num = _num.toFixed(2);
          }
          this.$set(this.tokenBalance, token, _num);

          this.setWallet();
        });
    },

    setWallet() {
      let wallet = this.tokenBalance;
      this.$store.dispatch("ASET_WALLET", wallet);
    },

    generalBarChoiced(type, choicedData) {},

    toMarket() {
      this.$message({
        message: this.$t("common.tradingMarketTip"),
        type: "warning",
        center: true,
      });
    },
  },

  filters: {
    addressFilter(address) {
      let _address = address.slice(0, 4);
      _address += "****";
      _address += address.slice(address.length - 4, address.length);
      return _address;
    },
    trainTimeFilter(seconds) {
      let hour =
        Math.floor(seconds / 3600) >= 10
          ? Math.floor(seconds / 3600)
          : "0" + Math.floor(seconds / 3600);
      seconds -= 3600 * hour;
      let min =
        Math.floor(seconds / 60) >= 10
          ? Math.floor(seconds / 60)
          : "0" + Math.floor(seconds / 60);
      seconds -= 60 * min;
      let sec = seconds >= 10 ? seconds : "0" + seconds;
      return hour + ":" + min + ":" + sec;
    },
  },
};
</script>

