<template>
  <div class="pagination-container">
    <div class="pagination-box">
      <div class="first-btn" @click="goToFirstPage">First</div>
      <div class="prev-btn" @click="prevPage">
        <i class="el-icon-caret-left"></i>
      </div>
      <div class="current-page">{{ current }} of {{ total }}</div>
      <div class="next-btn" @click="nextPage">
        <i class="el-icon-caret-right"></i>
      </div>
      <div class="last-btn" @click="goToLastPage">Last</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Pagination",
  data() {
    return {
      current: 1,
      orderList:[],
    };
  },
  props: {
    limit: {
      Number,
    },
    list: {
      Array,
    },
    value: {
      Array,
    },
    filter: {
      Object,
    },
  },
  computed: {
    total() {
      return parseInt(this.orderList.length / this.limit) + 1;
    },
  },

  watch: {
    list(newVal, oldVal) {
   this.listUpdated()
    },
    filter(newVal) {
        this.listUpdated()
    },
  },
  methods: {
    prevPage() {
      if (this.current == 1) return;
      this.current -= 1;
      this.switchListPage();
    },
    nextPage() {
      if (this.current == this.total) return;
      this.current += 1;
      this.switchListPage();
    },
    switchListPage() {
      let displayItems;
      if (this.current == this.total) {
        displayItems = this.orderList.slice((this.current - 1) * this.limit);
      } else {
        displayItems = this.orderList.slice(
          (this.current - 1) * this.limit,
          this.current * this.limit
        );
      }
      this.$emit("input", displayItems);
    },
    goToFirstPage() {
      this.current = 1;
      this.switchListPage();
    },
    goToLastPage() {
      this.current = this.total;
      this.switchListPage();
    },
    listUpdated(){
     let {element,rarity,token} = this.filter;
        let parsedList = [];
        this.list.forEach(item=>{
            if((element==""||item.element==element)&&(rarity==""||item.rarity==rarity)&&(item.token==token)){
                parsedList.push(item)
            }
        })
        if(parseInt(this.orderList.length / this.limit)!=parseInt(parsedList.length / this.limit)){
            this.current = 1;
        }
       this.orderList = parsedList
       this.switchListPage()
    }
  },
};
</script>

<style scoped>
.pagination-container {
  width: 100%;
}
.pagination-box {
  width: 100%;
  font-size: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pagination-box .first-btn,
.pagination-box .last-btn {
  padding: 5px 7px;
}
</style>


