import Storage from '../utils/storage'

const SET_ASSET = 'SET_ASSET'
const SET_WALLET = 'SET_WALLET'
const SET_WALLETVISIABLE = 'SET_WALLETVISIABLE'

const mutation = {

  [SET_ASSET](state, asset) {
    state.asset = asset;
    Storage.set("game_asset", asset)
  },

  [SET_WALLET](state, wallet) {
    state.wallet = wallet;
    Storage.set("user_wallet", wallet)
  },
  clearWalletCache(state) {
    let web3 = {
      isInjected: false,
      web3Instance: null,
      networkId: null,
      coinbase: null,
      balance: null,
      error: null,
      from: null,
      gasPrice: 0
    }
    state.providerOptions = null;
    state.web3Modal = null;
    state.web3 = web3
  },
  setProvider(state, web3Modal) {
    state.web3Modal = web3Modal.web3Modal;
    state.providerOptions = web3Modal.providerOptions
  },
  [SET_WALLETVISIABLE](state, walletVisiable) {
    state.walletVisiable = walletVisiable;
    Storage.set("user_walletVisiable", walletVisiable)
  },

  registerWeb3Instance(state, payload) {
    let result = payload
    let web3Copy = {}
    web3Copy.coinbase = result.coinbase;
    web3Copy.networkId = result.networkId
    web3Copy.balance = parseInt(result.balance, 10)
    web3Copy.isInjected = result.injectedWeb3
    web3Copy.web3Instance = result.web3;
    web3Copy.gasPrice = result.gasPrice ? result.gasPrice : 3e9
    state.web3 = web3Copy

  },
}

export default mutation;



