var DragonSprite = Hilo.Class.create({
  Extends: Hilo.Sprite,

  Mixes: Hilo.EventMixin,

  constructor: function constructor(properties) {
    DragonSprite.superclass.constructor.call(this, properties);

    this.addFrame(properties.atlas.getSprite("dragon"));

    // this.width = this.width / 2;
    // this.height = this.height / 2;
    var height = this.height;
    if (this.level < 11) {
      this.height = this.height * 0.8;
      this.width = this.width * 0.8;
    }
    if (this.level >= 11 && this.level < 17) {
      this.height = this.height * 0.9;
      this.width = this.width * 0.9;
    }
    if (this.level >= 17 && this.level < 22) {
      this.height = this.height * 1;
      this.width = this.width * 1;
    }
    if (this.level >= 22 && this.level < 27) {
      this.height = this.height * 1.1;
      this.width = this.width * 1.1;
    }
    if (this.level >= 27 && this.level < 30) {
      this.height = this.height * 1.2;
      this.width = this.width * 1.2;
    }
    if (this.level >= 30 && this.level < 32) {
      this.height = this.height * 1.4;
      this.width = this.width * 1.4;
    }
    if (this.level == 32) {
      this.height = this.height * 1.5;
      this.width = this.width * 1.5;
    }

    this.radiusTop = this.trackWidth / 2 - 20;
    this.radiusBottom = this.trackWidth / 2;

    this.interval = 10;
    this.pivotX = this.width / 2;
    this.pivotY = this.height / 2;

    this.centerTopX = this.startX + this.trackWidth / 2;
    this.centerTopY = this.radiusTop + height / 8;
    this.centerTopY = this.track.y + this.track.height * 0.425;

    this.centerBottomX = this.startX + this.trackWidth / 2;

    this.centerBottomY = this.startY + this.trackHeight + this.track.y * 0.85 - this.radiusBottom - this.height / 2;
    var angleTop = 1.15;
    this.leftTopX = this.centerTopX + Math.cos(angleTop * Math.PI) * this.radiusTop;
    this.leftTopY = this.centerTopY + Math.sin(angleTop * Math.PI) * this.radiusTop;

    angleTop = 1.8;
    this.rightTopX = this.centerTopX + Math.cos(angleTop * Math.PI) * this.radiusTop;
    this.rightTopY = this.centerTopY + Math.sin(angleTop * Math.PI) * this.radiusTop;

    var angleBottom = 0.2;
    this.rightBottomX = this.centerBottomX + Math.cos(angleBottom * Math.PI) * this.radiusBottom;
    this.rightBottomY = this.centerBottomY + Math.sin(angleBottom * Math.PI) * this.radiusBottom;

    angleBottom = 0.8;
    this.leftBottomX = this.centerBottomX + Math.cos(angleBottom * Math.PI) * this.radiusBottom;
    this.leftBottomY = this.centerBottomY + Math.sin(angleBottom * Math.PI) * this.radiusBottom;

    // this.leftSlope = (this.leftBottomX - this.leftTopX) / (this.leftBottomY - this.leftTopY);
    // this.rightSlope = (this.rightTopX - this.rightBottomX) / (this.rightTopY - this.rightBottomY);

    this.leftSlope = (this.leftTopX - this.leftBottomX) / (this.leftTopY - this.leftBottomY);
    this.rightSlope = (this.rightBottomX - this.rightTopX) / (this.rightBottomY - this.rightTopY);

    this.speed = 1 + this.level / 8 + this.level * 0.05;
  },

  state: "sideleft",
  startX: 0,
  startY: 0,

  centerTopX: 0,
  centerTopY: 0,
  centerBottomX: 0,
  centerBottomY: 0,
  angleTop: 1,
  angleBottom: 0.5,
  rotation: 0,

  leftTopX: 0,
  leftTopY: 0,
  leftBottomX: 0,
  leftBottomY: 0,
  rightTopX: 0,
  rightTopY: 0,
  rightBottomX: 0,
  rightBottomY: 0,

  trackHeight: 0,
  trackWidth: 0,
  track: null,
  status: "sideleft",
  curframe: "sideleft",
  speed: 14,
  speedFactor: 1,
  // duration:10, 
  level: 0,
  leftSlope: 0,
  rightSlope: 0,
  radiusTop: 0,
  radiusBottom: 0,
  gold: true,
  getReady: function getReady() {},



  start: function start(params) {},

  stop: function stop(params) {},

  updateSpeedFactor: function (speedFactor) {
    this.speedFactor = speedFactor
  },

  onUpdate: function onUpdate() {
    // if (this.status == "sideleft") {
    //   this.rotation = Math.PI + this.angleBottom * Math.PI;
    //   this.angleBottom += this.speedFactor * this.speed / 800;

    //   this.x = this.centerBottomX + Math.cos(this.angleBottom * Math.PI) * this.radiusBottom ;
    //   this.y = this.centerBottomY + Math.sin(this.angleBottom * Math.PI) * this.radiusBottom ;
    // }
    // return
    if (this.status == "sideleft") {
      if (this.angleBottom > 0.25 && this.curframe == "front") {
        this.goto("sideleft");
        this.curframe = "sideleft";
      }

      if (this.angleBottom > 0.75 && this.curframe == "sideleft") {
        this.goto("back");
        this.curframe = "back";
      }

      if (this.angleBottom >= 0.8) {
        this.status = "back";
      }

      this.rotation = Math.PI + this.angleBottom * Math.PI;
      this.angleBottom += this.speedFactor * this.speed / 800;


      this.x = this.centerBottomX + Math.cos(this.angleBottom * Math.PI) * this.radiusBottom;
      this.y = this.centerBottomY + Math.sin(this.angleBottom * Math.PI) * this.radiusBottom;
      // this.x = this.centerBottomX + x + Math.cos(this.angleBottom * Math.PI) * this.radiusBottom;
      // this.y = this.centerBottomY + Math.sin(this.angleBottom * Math.PI) * this.radiusBottom ;
    } else if (this.status == "back") {
      this.x += this.speedFactor * this.speed * this.leftSlope;
      this.y -= this.speedFactor * this.speed;

      if (this.y < this.leftTopY) {
        this.status = "sideright";
        this.angleTop = 1.15;
        
      }
    } else if (this.status == "sideright") {
      if (this.angleTop > 1.25 && this.curframe == "back") {
        this.goto("sideright");
        this.curframe = "sideright";
      }

      if (this.angleTop > 1.75 && this.curframe == "sideright") {
        this.goto("front");
        this.curframe = "front";
        
      }

      if (this.angleTop >= 1.8) {
        this.status = "front";
      }


      this.x = this.centerTopX + Math.cos(this.angleTop * Math.PI) * this.radiusTop;
      this.y = this.centerTopY + Math.sin(this.angleTop * Math.PI) * this.radiusTop;

      this.rotation = this.angleTop;
      this.angleTop += this.speedFactor * this.speed / 800;
    } else if (this.status == "front") {
      this.x += this.speedFactor * this.speed * this.rightSlope;
      this.y += this.speedFactor * this.speed;

      if (this.gold && this.y > this.rightTopY + 50) {
        this.gold = false;
        this.fire("gold", this.level);
      }
      if (this.y > this.rightBottomY) {
        this.status = "sideleft";
        this.angleBottom = 0.2;
        this.gold = true;
      }
    }


    
  }
});
export default DragonSprite



